<template>
  <keep-alive>
    <router-view/>
  </keep-alive>
</template>
<script>
export default { 
	name: 'App',
	watch: {
		$route() {
			this.setDocumentTitle()
		}
	},
	activated() {
		this.setDocumentTitle()
	},
	methods: {
		setDocumentTitle () {
			switch(this.$route.name) {
				case 'Front_Index': document.title = "首页"; break; 
				case 'Front_Article': document.title = "文章"; break; 
				case 'Front_Picture': document.title = "图片"; break; 
				case 'Front_Contact': document.title = "关于"; break; 
				case 'Admin': document.title = "拉扯网后台管理系统"; break; 
				case 'Admin_Column': document.title = "栏目管理"; break; 
				case 'Admin_Article': document.title = "文章管理"; break; 
				case 'Admin_Reviews': document.title = "评论管理"; break; 
				case 'Admin_Recycle': document.title = "回收站"; break; 
				case 'Admin_System': document.title = "系统配置"; break; 
				case 'Admin_User': document.title = "用户管理"; break; 
				case 'Admin_User_Edit': document.title = "用户编辑"; break; 
				case 'Admin_User_Groups': document.title = "角色管理"; break;
				case 'Admin_User_Role': document.title = "角色编辑"; break;  
				case 'Admin_About': document.title = "关于本系统"; break; 
				case 'Admin_Works': document.title = "作品管理"; break; 
				case 'Admin_Article_Add': document.title = "文章编辑"; break; 
				default: document.title = "拉扯网"; break; 
			}
		}
	}
}
</script>
<style>
@import url("http://at.alicdn.com/t/font_959376_huv4id0hjrh.css");
html{width:100%;height: 100%;}
body{width:100%;height: 100%;padding: 0;font-size:1vw;}
html{font-family: "Source Sans Pro", "Helvetica Neue","-apple-system","BlinkMacSystemFont","Helvetica Neue","PingFang SC","Microsoft YaHei","Source Han Sans SC","Noto Sans CJK SC","WenQuanYi Micro Hei","sans-serif"!important;}
ul,li,dl,dt,dd,p{margin:0;padding: 0;list-style: none;}
a{color:#000;}
a:hover,.router-link-exact-active{color:#4fc08d;text-decoration: none;}
input:focus{border-color:#4fc08d;}
.mint-indicator {position: relative;z-index: 2;}
.webuploader-container{position: absolute;left:0;top:0;z-index: 2; width:100%;height: 100%;display:flex;justify-content:center;align-items:center;}
.webuploader-container div{width:100%!important;height:100%!important;background-color:transparent!important;}
.webuploader-container div:last-child{left:0;top:0;z-index: 5;width:100%!important;height: 100%!important;}
.webuploader-container div:last-child input{width:100%;height: 100%;}
.mint-tab-item-label{font-size:1rem;line-height:.5;}
</style>


