<template>
<div class="articleIndex">
	<ul class="list">
		<li v-for="(l,index) in list" :key="l.id">
			<h4><router-link :to="'/article/detail/'+l.id">{{l.title}}</router-link></h4>
			<p v-if="l.desc">{{l.desc}}</p>
			<div class="tags" v-if="l.tags && l.tags.length>0" >
				<router-link v-for="(t,index) in l.tags" :key="index" :to="'/search/'+t">{{t}}</router-link>
			</div>
			<div class="source">
				<router-link :to="'/article/'+l.catalogId">{{getCatalogName(l.catalogId)}}</router-link>
				<time>{{getFullTime(l.createdAt)}}</time>
			</div>
		</li>
	</ul>
</div>
</template>

<script>
import { Toast, Indicator } from 'mint-ui'
import { mapState } from 'vuex'
export default {
	name: 'articleIndex',
	components: {},
	data () {
		return {
			lachewang: {},
			list: [],
			page: {
				current: 1,
				size: 15,
				total: 0,
				count: 0
			}
		}
	},
	activated () {
	    if(localStorage.lachewang) {
			this.lachewang = JSON.parse(localStorage.lachewang)
		}
		this.getArticleCount()
		this.getArticleList()
	},
	mounted() {
		let that = this
		window.addEventListener('scroll', ()=>{
			if((window.scrollY + document.body.offsetHeight) >= document.body.scrollHeight) {
				if(this.$route.name == 'Front_Article') {
					that.getNextPageArticleName()
				}
			}
		})
	},
	created(){},
	methods: {
		getArticleCount() {
			let that = this
			this.$axios({
	      		method: 'GET',
	      		url: '/mcm/api/articles/count?filter=' + encodeURIComponent('{"where":{"delete":false,"display":true}}')
	      	}).then(res => {
	      		if(res && res.statusText=="OK") {
	      			if(res.data && res.data.count) {
	      				that.page.count = res.data.count
	      				if(that.page.count % that.page.size == 0) {
	      					that.page.total = that.page.count / that.page.size
	      				} else {
	      					that.page.total = parseInt(that.page.count / that.page.size) + 1
	      				}
	      			} else {
	      				that.page.count = that.page.total = 0
	      			}
	      		} else {
	      			that.page.count = that.page.total = 0
	      			console.log(res)
	      		}
	      	}).catch(err => {
	      		that.page.count = that.page.total = 0
	      		console.log(err)
	      	})
		},
		getArticleList() {
			let that = this
			let page = this.page		
			let id = this.$route.params.id==0 ? '5d634d7db46fb07262045150' : this.$route.params.id
			let params = encodeURIComponent('{"fields": {"id": true, "catalogId": true, "title": true, "desc": true, "createdAt": true, "type": true, "tags": true}, "where": {"display": true, "delete": false, "catalogId": "'+id+'"}, "skip": '+(page.current-1)*page.size+', "limit": '+page.size+'}')
	      	Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});	
	      	this.$axios({
	      		method: 'GET',
	      		url: '/mcm/api/articles?filter=' + params
	      	}).then(res => {
	      		Indicator.close();
	      		if(res && res.statusText=="OK") {
	      			if(res.data && res.data.length>0) {
	      				if(that.page.current == 1) {
	      					that.list = res.data
	      				} else if(that.page.current > 1) {
	      					that.list = that.list.concat(res.data)
	      				}
	      			} else {
	      				if(that.page.current == 1) {
	      					that.list = []
	      				}
	      			}
	      		} else {
	      			if(that.page.current == 1) {
	      				that.list = []
	      			}
	      			Toast({message: '查询文章失败', duration: 1500})
	      			console.log(res)
	      		}
	      	}).catch(err => {
	      		Indicator.close();
	      		that.list = []
	      		Toast({message: '查询文章失败', duration: 1500})
	      		console.log(err)
	      	})
	    },
	    //加载下一页数据
	    getNextPageArticleName() {
	    	if(this.page.total > 1) {
		    	if(this.page.current < this.page.total) {
		    		this.page.current = this.page.current + 1
		    		this.getArticleList()
		    	} else if(this.page.current == this.page.total) {
		    		Toast({message: '没有更多了', duration: 1500})
		    	}
	    	} else {
	    		Toast({message: '没有更多了', duration: 1500})
	    	}
	    },
	    getCatalogName(id) {
	    	let la = this.lachewang
	    	let ca = la.catalogs
	    	if(ca && ca.length>0) {
	    		let n = ''
	    		ca.forEach(i => {
	    			if(i.id===id) { 
	    				n = i.name
	    			}
	    		})
	    		return n;
	    	} else {
	    		return ""
	    	}
	    }
	},
	computed: {
		...mapState(['getPages', 'getFullTime'])
	}
}
</script>

<style scoped lang="less">
ul,li{padding:0;margin:0;list-style: none;}
.articleIndex{
	.list{
		li{width:100%;padding:20px 0;
			h4{font-weight: 500;font-size: 18px;line-height: 28px;
				a{color: #212121;}
			}
			p{color: #888;line-height: 1.5;font-size: 13px;overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-line-clamp: 2;-webkit-box-orient: vertical;}
			.tags{height: 30px;overflow: hidden;font-size: 12px;display: inline-flex;align-items: center;flex-wrap: wrap;width: 100%;margin-bottom: 10px;
				a{background-color: #f1f8ff;border-radius: 3px;display: inline-block;margin: 0 .5em .5em 0;padding: .1em .9em;white-space: nowrap;}
			}
			.source{font-size: 13px;
				/*.zan{padding:0;margin:0 10px 0 0;border:none;background-color:transparent;box-shadow:none;border:none;outline:none;transition:all 0.3s;
					.icon{background-color: rgba(1,126,102,0.08);color: #009a61;width:24px;height: 24px;border-radius: 100%;display: inline-block;font-style: normal;transition:all 0.3s;}
					.num{color: #009a61;transition:all 0.3s;padding: 0 5px;}
					.text{color:#666;transition:all 0.3s;}
					&:hover{
						cursor:pointer;
						.icon{background-color:#009a61;
							&:before{color:#fff;}
						}
						.text{color: #009a61;}
					}
					&.on{
						cursor:pointer;
						.icon{background-color:#009a61;
							&:before{color:#fff;}
						}
						.text{
							&:before{content:"已";}
						}	
					}
					&:after{content:"";box-shadow: none;border:none;background-color: transparent;}
				}*/
				a{color:#586069;margin: 0 10px 0 0;}
				time{color: #9E9E9E;}
			}
		}
	}
}
@media screen and(max-width:640px){
	.articleIndex{padding:0 10px;}
} 
</style>


