<template>
<div class="row friendLink" v-if="flink">
	<div class="col-md-12">
		<div class="links">
			<a v-for="fl in flink" :key="fl.id" :href="fl.url" target="_blank">{{fl.name}}</a>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'FriendLink',
	props: ['flink'],
	data() {
		return {
			msg: ''
		}
	},
	created() {},
	methods: {}
}
</script>
<style scoped lang="less">
.friendLink{margin-bottom: 10px;}
.links{width:100%;display: flex;flex-wrap: wrap;justify-content: center;
	a{padding:0 10px;font-size:.5vw;color:#666;}
	a:hover{color:#4fc08d;}
}
</style>


