<template>
<li class="list-group-item">
	<section>
		<button class="btn btn-default title" @click="goArticle(catalog)">
			<p v-text="catalog.name"></p>
			<span class="badge badge-info" v-text="catalog.words"></span>
		</button>
		<div class="btns">
			<button class="btn btn-default" title="向下展开" 
				v-if="catalog.children && catalog.children.length>0"
				@click.stop="spreadDownCatalog($event, 'open')">
				<i class="iconfont icon-viewlist"></i>
			</button>
			<button class="btn btn-default" title="增加子类" @click.stop.prevent="addChildCatalog({id:catalog.id,name:catalog.name,level:catalog.level},$event)">
				<i class="iconfont icon-jiahao2fill"></i>
			</button>
			<button class="btn btn-default" title="修改" @click.stop.prevent="updateCatalog(catalog,$event)">
				<i class="iconfont icon-pingjia"></i>
			</button>
			<button class="btn btn-default" title="删除" @click.stop.prevent="deleteCatalog(catalog)">
				<i class="iconfont icon-guanbi2fill"></i>
			</button>
			<span class="badge badge-info" v-text="catalog.words"></span>
		</div>
	</section>
	<ul class="children" v-if="catalog.children && catalog.children.length>0">
		<admin-catalog-item 
			v-for="catalog in catalog.children" 
			:key="catalog.id"
			:class="catalog.display?'':'disable'"
			:catalog="catalog"
		>
		</admin-catalog-item>
	</ul>
</li>
</template>
<script>
import axios from 'axios'
import { mapState } from 'vuex'
import { Toast, MessageBox } from 'mint-ui'
export default {
	name: 'AdminCatalogItem',
	components: {},
	props: ['catalog'],
	data () {
		return {
			lachewang: {},
			addCatalogDialog: false
		}
	},
	activated () {
		this.lachewang = JSON.parse(localStorage.lachewang)
		this.addCatalogDialog = this.lachewang.addCatalogDialog
	},
	created () {},
	mounted () {
		if(!this.server.spreadFlag) {
			this.$store.dispatch('updateSpreadFlag', true)
			let that = this
			this.BUS.$on('spread', obj => {
				that.spreadDownCatalog(obj.dom, obj.t)
			})
		}
	},
	methods: {
		//向下展开栏目
		spreadDownCatalog(thisE, t) {
			if(!thisE.target){return false;}
			let pn = thisE.target.parentNode;
			let btnsNode, sectionNode, currentNode; 
			if(pn.className == 'btn btn-default') {
				btnsNode = pn.parentNode
			} else if(pn.className == 'btns') {
				btnsNode = pn
			}
			if(btnsNode.parentNode.nodeName == 'SECTION') {
				sectionNode = btnsNode.parentNode
				if(sectionNode.nextSibling.nodeName == 'UL') {
					currentNode = sectionNode.nextSibling
				}else if(sectionNode.nextSibling.nextSibling.nodeName == 'UL') {
					currentNode = sectionNode.nextSibling.nextSibling
				}
				
				if(t == 'open') {
					if(currentNode) {
						currentNode.style.display == 'block' ? currentNode.style.display = 'none' : currentNode.style.display = 'block'	
					} else {
						Toast({message: '暂无子栏目', duration: 1200});
					}
				} else if(t == 'add' || t == 'edit') { 
					if(currentNode) {
						currentNode.style.display = 'block'
					}
				}
			}
		},
		//增加子栏目
		addChildCatalog(obj, thisE) {
			let lachewang = JSON.parse(localStorage.lachewang)
			lachewang.addCatalogDialog = true
			localStorage.lachewang = JSON.stringify(lachewang)
			this.BUS.$emit('openAddCatalogDialog', true)
			obj.dom = thisE
			this.BUS.$emit('chooseThis', obj)
		},
		//修改栏目
		updateCatalog(obj, thisE) {
			let lachewang = JSON.parse(localStorage.lachewang)
			lachewang.addCatalogDialog = true
			localStorage.lachewang = JSON.stringify(lachewang)
			this.BUS.$emit('openAddCatalogDialog', true)
			obj.dom=thisE
			this.BUS.$emit('chooseThis', obj)
		},
		//删除指定栏目，所有子栏目，须先删除子栏目，所栏目下有文章，则须先清空文章
		deleteCatalog(obj) {
			if(obj.words>0) {
				Toast({message: '请先清空该栏目内的文章列表', duration: 2000}); 
			} else if(obj.children && obj.children.length>0) {
				Toast({message: '请先清空该栏目内子栏目', duration: 2000}); 
			} else {
				MessageBox.confirm('确定要删除吗?').then(action => {
					//若点了确定
					if(action === 'confirm') {
						if(obj && obj.id) {
							axios({
								method: 'DELETE',
								url: '/mcm/api/catalogs/'+obj.id
							}).then(res => {
								if(res && res.status == 200 && res.statusText == "OK") {
									let lachewang = JSON.parse(localStorage.lachewang)
									let catalogs = lachewang.catalogs
									let newCatalogs = []
									if(catalogs.length>0) {
										newCatalogs = catalogs.filter(i=>i.id!=obj.id)
									}
									lachewang.catalogs = newCatalogs
									localStorage.lachewang = JSON.stringify(lachewang)
									let instance = Toast({
					  					message: '删除成功', 
					  					iconClass: 'iconfont icon-success1',
					  					duration: 1200
					  				})
					  				//提示窗消失后跳转到管理后台
					  				setTimeout(() => {
									  	instance.close();
										this.BUS.$emit('updateCatalog')
									}, 1500);
								} else {
									Toast({message: '删除 失败', duration: 2000}); 
									console.log(res)
								}
							}).catch(err => {
								Toast({message: '删除失败', duration: 2000}); 
								console.log(err)
							})
						}
					} else {
						console.log(action);
					}
				}).catch(err => {
					//点了取消按钮
					if(err === 'cancel') {
						console.log('取消删除')
					}else {
						console.log(err)
					}
				})
			}
		},
		//去文章列表页
		goArticle(obj) {
			if(obj.name=="主页") {
				this.$router.push({name: 'Admin_Article_Home'})
			} else {
				this.$router.push(`/admin/article/${obj.id}`)
			}	
		}
	},
	watch: {},
	computed: {
		...mapState(['server'])
	}
}
</script>
<style scoped lang="less">
.list-group-item{width:100%;display: flex;flex-direction: column;
	&.disable{-webkit-filter: grayscale(100%);filter: grayscale(100%);}
	section{width:100%;display: flex;justify-content: space-between;align-items: center;
		.title{display: flex;align-items: center;justify-content: flex-start;
			p{margin-right: .5vw;}
			.badge{top:0;font-weight: normal;}
		}
		.title:hover p{color:#4fc08d;}
		.btns{
			.badge{display: none}
		}
	}	
	.children{padding-left: 3vw;display: none;margin-top:0.75rem;
		.title{box-sizing: border-box;}
		.list-group-item{padding-left: 0;padding-right: 0;padding-bottom: 0;border-left:none;border-bottom:none;border-right: none;}
	}
}
@media screen and (max-width: 640px) {
	.list-group-item{width:100%;
		section{width:100%;flex-direction: column;justify-content: flex-start;
			.title{width:100%;display: flex;align-items: flex-start;flex-wrap: wrap;justify-content: flex-start;text-align: left;
				span{display: none}
				p{margin:0;width: 100%;text-align: left;}
			}
			.btns{width:100%;display: flex;justify-content:space-between;align-items: center;
				.badge{display: flex;height: 6vw;background-color:transparent;color:#666;border-radius: 100%;border:1px solid #ddd;width:6vw;justify-content: center;align-items: center;}
			}
		}	
		.children{padding-left:0vw;}
	}
}
</style>


