<template>
<div class="article card">
	<header class="card-header">
		<button class="btn btn-success btn-sm" @click="writeArticle">写文章</button>
		<!--button v-if="list.length>1" class="btn btn-danger btn-sm" @click="batch('del')">批量删除</button>
		<button v-if="list.length>1" class="btn btn-primary btn-sm" @click="batch('ena')">批量显示</button>
		<button v-if="list.length>1" class="btn btn-primary btn-sm" @click="batch('dis')">批量不显示</button-->
	</header>
	<ul class="list card-body" ref="list" style="min-height: 650px;">
		<li v-for="l in list" :key="l.id">
			<div class="con">
				<button class="btn btn-default choose" @click="chooseThis($event, l)">
					<i class="iconfont icon-fangxingweixuanzhong"></i>
				</button>
				<button class="btn title" :class="l.display?'':'disabled'" @click="updateArticle(l.id)">{{l.title}}</button>
			</div>
			<div class="set">
				<button class="btn yan" @click="switchDisplay($event, l)" title="显示/隐藏">
					<i class="iconfont" :class="l.display ? 'icon-zhengyan' : 'icon-biyan2'"></i>
				</button>
				<button class="btn" title="编辑" @click="updateArticle(l.id)">
					<i class="iconfont icon-pingjia"></i>
				</button>
				<button class="btn" title="删除" @click="deleteArticle(l.id)">
					<i class="iconfont icon-shanchu1"></i>
				</button>
				<button class="btn" title="查看" @click="viewArticle(l.id)">
					<i class="iconfont icon-attachment"></i>
				</button>
			</div>
		</li>
	</ul>
	<footer v-if="pages.total>1" class="card-footer">
		<button class="btn btn-primary" 
		@click.stop.prevent='toFirst' 
		 :class="pages.current>1 ? '' : 'disabled'">首页</button>
		<button class="btn btn-primary" 
		 @click.stop.prevent='toPrev' 
		 :class="pages.current>1 ? '' : 'disabled'">上一页</button>
		<button class="btn" 
			v-for="p in pages.list" 
			:key="p" 
			:class="p===pages.current ? 'btn-default disabled' : 'btn-primary'"
			@click.stop.prevent='toThisPage(p)'
		>{{p}}</button>
		<button class="btn btn-primary" 
		 @click.stop.prevent='toNext' :class="pages.current<pages.total ? '':'disabled'">下一页</button>
		<button class="btn btn-primary"
		 @click.stop.prevent='toLast' :class="pages.current<pages.total ? '':'disabled'">末页</button>
	</footer>

</div>
</template>
<script>
import { mapState } from 'vuex'
import { Toast, Indicator, MessageBox } from 'mint-ui'
export default {
	name: 'Admin_Article',
	components: {},
	data () {
		return {
			list: [],
			lachewang: {},
			pages: {
				list: [], //页码列表
				count: 0, //总列表数
				current: 1, //当前页码
				size: 15, //每页列表数
				total: 0 //总页数
			},
			ids: []
		}
	},
	created () {},
	activated() {
		this.init()
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {
		setTimeout(()=>{
			this.setListHeight()
		}, 500)
	},
	methods: {
		//初始化
		init() {
			this.getArticleTotal()
			this.getArticleList()
		},
		//获取文章总条数
		getArticleTotal() {
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			let that = this
			let id = this.$route.params.id
			let params = encodeURIComponent('{"where":{"delete":false}}')
			//如果id为0，则表示请求所有文章列表
			if(id!=0) {
				params = encodeURIComponent('{"where":{"delete":false,"catalogId":"'+id+'"}}')
			}
			this.$axios({
				method: 'GET',
				url: '/mcm/api/articles/count?filter='+params
			}).then(res => {
				Indicator.close();
				if(res && res.status === 200 && res.statusText === "OK") {
					that.pages.count = res.data.count
					that.setPages()
				} else {
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				console.log(err)
			})
		},
		//获取文章列表
		getArticleList() {
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			let that = this
			let id = this.$route.params.id
			let limit = this.pages.size
			let skips = (this.pages.current-1) * limit
			let params = encodeURIComponent('{"where":{"delete":false},"skip":'+skips+',"limit":'+limit+'}')
			//如果id为0，则表示请求所有文章列表
			//否则只查询当前栏目下的文章
			if(id!=0) {
				params = encodeURIComponent('{"where":{"delete":false,"catalogId":"'+id+'"},"skip":'+skips+',"limit":'+limit+'}')
			}
			this.$axios({
				method: 'GET',
				url: '/mcm/api/articles?filter='+params
			}).then(res => {
				Indicator.close();
				if(res && res.status === 200 && res.statusText === "OK") {
					that.list = res.data
				} else {
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				console.log(err)
			})
		},
		//通过文章总条数，设置分页
		setPages() { 
			if(this.pages.count > 0) {
				let p = this.getPages(this.pages.count, this.pages.size)
				this.pages.total = p.pageTotal
				this.pages.list = p.pageList
			} else {
				this.pages.current = 1
				this.pages.count = 0
				this.pages.size = 15
				this.pages.total = 0
				this.pages.list = []
			}
		},
		//切换选中或取消选择
		chooseThis(thisDom, obj) {
			this.switchStyle(thisDom, {
				t: 'choose',
				y: 'icon-fangxingxuanzhongfill',
				n: 'icon-fangxingweixuanzhong',
				obj
			})
		},
		//切换，设置文章的显示或隐藏
		switchDisplay(thisDom, obj) {
			this.switchStyle(thisDom, {
				t: 'show',
				y: 'icon-biyan2',
				n: 'icon-zhengyan',
				obj
			})
			this.setArticleDisplay('show',obj.id)
		},
		switchStyle(thisDom,obj) {
			let iDom, cn;
			if(thisDom.target.nodeName === 'I'){
				iDom = thisDom.target
			}else if(thisDom.target.nodeName === 'BUTTON') {
				iDom = thisDom.target.children[0]
			}
			cn = iDom.className
			if(cn.indexOf(obj.n)!=-1){
				iDom.className = 'iconfont '+obj.y
				if(obj.t === 'choose') {
					iDom.parentNode.parentNode.parentNode.className = 'active'
					//当选中时，收集当前ID
					if(this.ids.length==0) {
						this.ids.push(obj.obj.id)
					}else if(this.ids.length>0) {
						let idss = this.ids.join()
						if(idss.indexOf(obj.obj.id)==-1){
							this.ids.push(obj.obj.id)
						}
					}
				} else if(obj.t === 'show') {
					this.setArticleDisabled(obj.obj.id, false)
				}
			} else {
				iDom.className = 'iconfont '+obj.n
				if(obj.t === 'choose') {
					iDom.parentNode.parentNode.parentNode.className = ''
					//当取消选择时删掉指定ID
					if(this.ids.length==1 && this.ids[0] === obj.obj.id) {
						this.ids = []
					}else if(this.ids.length>1) {
						let idss = this.ids.join()
						if(idss.indexOf(obj.obj.id)!=-1){
							this.ids = this.ids.filter(i => i != obj.obj.id)
						}
					}
				} else if(obj.t === 'show') {
					this.setArticleDisabled(obj.obj.id, true)
				}
			}
		},
		//设置文章显示或隐藏
		setArticleDisplay(t, id) {
			let dis = t=='show' ? true : false
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/articles/'+id,
				data: { display: dis }
			}).then(res => {
				Indicator.close();
				if(res && res.statusText=="OK") {
					Toast({message: '修改成功',iconClass: 'iconfont icon-success1', duration: 1500})
				} else {
					Toast({message: '修改失败', duration: 1500})
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				Toast({message: '修改失败', duration: 1500})
				console.log(err)
			})
		},
		setArticleDisabled(id,t){
			this.setArticleDisplay('hide',id)
			this.list.forEach(i => {
				if(i.id===id) i.display = t
			})
		},
		toFirst() {
			this.pages.current = 1
			this.getArticleList()
		},
		toPrev() {
			if(this.pages.current>1){
				this.pages.current = this.pages.current-1
				this.getArticleList()
			}
		},
		toNext() {
			if(this.pages.current<this.pages.total){
				this.pages.current = this.pages.current+1
				this.getArticleList()
			}
		},
		toLast() {
			this.pages.current = this.pages.total
			this.getArticleList()
		},
		toThisPage(p){
			if(p!==this.pages.current){
				this.pages.current = p
				this.getArticleList()
			}
		},
		//设置列表框架的高
		setListHeight() {
			let list = this.$refs.list
			if(list.children) {
				let lis = list.children
				if(lis.length>0) {
					if(lis[0]) {
						let li = lis[0]
						let h = li.offsetHeight || li.clientHeight || li.scrollHeight
						list.style.minHeight = h * this.pages.size + 'px'
					}
				}
			}
		},
		//点击查看按钮，进入文章详情页
		viewArticle(id) {
			//this.$router.push(`/article/${id}`)
			const { href } = this.$router.resolve({
			    name: "Front_Article_Detail",
			    params: {id}
			})
			window.open(href, '_blank')
		},
		//编辑文章内容
		updateArticle(id) {
			this.$router.push(`/admin/article/add/${id}`)
		},
		//删除指定文章
		deleteArticle(id) {
			if(id) {
				let that = this
				MessageBox.confirm('确定删除?').then(action => {
					if(action === 'confirm') {
						Indicator.open({text: '删除中...',spinnerType: 'fading-circle'});
						this.$axios({
							method: 'PUT',
							url: '/mcm/api/articles/'+id,
							data: {
								delete: true
							}
						}).then(res => {
							Indicator.close();
							if(res && res.status === 200 && res.statusText === "OK") {
								Toast({message: '删除成功',iconClass: 'iconfont icon-success1', duration: 1500})
								that.updatePages()
							} else {
								Toast({message: '删除失败', duration: 1500})
								console.log(res)
							}
						}).catch(err => {
							Indicator.close();
							Toast({message: '删除失败', duration: 1500})
							console.log(err)
						})
					} else {
						console.log(action)
					}
				}).catch(err => {
					if(err === 'cancel') {
						//console.log('取消删除')
					} else {
						console.log(err)
					}
				})
			} else {
				Toast({message: '未知的文章ID', duration: 2000});
			}
		},
		//写文章
		writeArticle() {
			//let id = (new Date()).getTime()
			//this.$router.push(`/admin/article/add/${id}`)
			this.$router.push('/admin/article/add/0')
		},
		//批量操作
		batch(t) {
			if(this.ids.length>0) {
				switch(t) {
					case 'del': this.batchDelete(); break;
					case 'ena': this.batchEnable(); break;
					case 'dis': this.batchDisable(); break;
				}
			} else {
				Toast({message: '请选择', duration: 1500});
			}
		},
		//批量删除
		batchDelete() {
			MessageBox.confirm('确定删除?').then(action => {
				if(action === 'confirm') {
					let arr = []
					let idss = this.ids.join()
					if(this.list.length>1){
						this.list.forEach(l => {
							if(idss.indexOf(l.id) == -1) {
								arr.push(l)
							}
						})
						this.list = arr
					} else if(this.list.length===1) {
						let l = this.list[0]
						if(l.id === idss) {
							this.list = []
						}
					}
					this.deleteSuccess()
					this.updatePages()
				} else {
					console.log(action)
				}
			}).catch(err => {
				if(err === 'cancel') {
					console.log('取消删除')
				} else {
					console.log(err)
				}
			})
		},
		//批量显示
		batchEnable() {
			this.enableOrNot(true)
		},
		//批量不显示
		batchDisable() {
			this.enableOrNot(false)
		},
		//批量设置文章的显示或隐藏
		enableOrNot(t) {
			let idss = this.ids.join()
			this.list.forEach(l => {
				if(idss.indexOf(l.id) != -1) {
					l.display = t
				}
			})
			let list = this.$refs.list
			let li = list.children
			for(let i=0; i<li.length; i++) {
				if(li[i].className === 'active') {
					li[i].className = ''
					let tagI = li[i].children[0].children[0].children[0]
					tagI.className = tagI.className.replace('icon-fangxingxuanzhongfill', 'icon-fangxingweixuanzhong')
					let yan = li[i].children[1].children[0].children[0]
					yan.className = yan.className.replace('icon-biyan2', 'icon-zhengyan')
				}
			}
			this.ids = []
		},
		//重新排列底部页码，及当前页码所在位置，以及重新显示当前页码内的文章列表
		updatePages() {
			this.getArticleList()
			//删除成功之后，根据剩余文章列表数，重新排页码
			this.setPages()
			//若删除操作时，当前页码数大于重新排列的总页码数，说明删除之后，页码数减少了，需将当前页码数往前移到最后一页；若不大于重新排列的总页码数，则保持不变
			this.pages.total>0 ? this.pages.current>this.pages.total ? this.pages.current=this.pages.total : this.pages.current=this.pages.current : this.pages.current=1
			//根据当前页码，重新请求当前页码内的文章列表
			//this.currentPages()
			//显示底部重新排好的页码
			//this.getPages()
		},
		//删除成功
		deleteSuccess() {
			this.ids = []
			Toast({
			  	message: '删除成功',
			  	iconClass: 'iconfont icon-success1',
			  	duration: 1200
			})
			localStorage.lachewang = JSON.stringify(this.lachewang)
		}
	},
	watch: {},
	computed: {
		...mapState(['server','getPages'])
	}
}
</script>
<style scoped lang="less">
.article{width:100%;display: flex;flex-direction: column;
	header{width:100%;display: flex;align-items: center;
		button{margin-right: 10px;}
	}
	.icon-fangxingweixuanzhong,.icon-fangxingxuanzhongfill{font-size: 20px;position: relative;top:3px;}
	.yan i{color:#666;}
	.icon-fangxingxuanzhongfill{color:#007bff;}
	.icon-shanchu1,.icon-fangxingweixuanzhong{color:#999;}
	.list{padding-top:10px;
		li{width:100%;display: flex;justify-content: space-between;align-items: center;border-bottom: 1px solid #ddd;transition: all 0.3s;
			&:last-child{border-bottom:0;}
			.con{flex:1;display: flex;}
			button{transition: all 0.3s;}
			button,button:active,button:hover{outline: none;box-shadow: none;}
			.title{flex-wrap: wrap;text-align: left;flex:1;color:#000;transition: all 0.3s;font-size: 14px;}
			.title.disabled{color:#999;}
			&:hover,&.active{background-color: #f5f5f5;
				.title{color:#4fc08d;}
			}
		}
	}
	footer{width:100%;display: flex;justify-content: flex-end;align-items: center;padding-top:20px;
		button{border:1px solid #ddd;margin-right:10px;font-size: 14px;}
	}
}
</style>


