<template>
<div class="editBanner">
	<input type="text" placeholder="title" v-model="title" class="form-control" />
	<input type="text" placeholder="desc" v-model="desc" class="form-control" />
	<input type="text" placeholder="url" v-model="url" class="form-control" />
	<div class="vueUpload">
		<upload tip="上传Banner"></upload>
		<div class="img" v-if="avatar">
			<img :src="avatar" alt="">
			<button class="btn btn-danger" title="删除" @click="deleteBannerFile"><i class="iconfont icon-shanchu7"></i></button>
		</div>
	</div>
	<input type="text" placeholder="existIn" v-model="existIn" class="form-control" />
	<mt-switch v-model="display"></mt-switch>
	<div class="set">
		<button class="btn btn-success" title="保存" @click="save">保&nbsp;&nbsp;存</button>
	</div>
</div>
</template>
<script>
import Vue from 'vue'
import { Switch, Toast, Indicator, MessageBox } from 'mint-ui';
Vue.component(Switch.name, Switch);
import upload from './VueUpload'
export default {
	name: 'EditBanner',
	components: {upload},
	data () {
		return {
			title: '',
			desc: '',
			url: '',
			avatar: '',
			existIn: '',
			display: true,
			avatarId: ''
		}
	},
	activated() {
		if(this.$route.params.id && this.$route.params.id != '0') {
			this.getInfo()
		} else {
			this.init()
		}
	},
	created (){},
	mounted () {
		let that = this
		this.BUS.$on("uploadSuccess", (file, res, type) => {
			if(type == "上传Banner") {
				Toast({message: '上传成功', iconClass: 'iconfont icon-success1',duration: 1500})
				setTimeout(()=>{
					that.avatar = res.url+"?"+res.id
					that.avatarId = res.id
					that.BUS.$emit('doInitForUpload')
				}, 1000)
			}
		})
	},
	methods: {
		init() {
			this.title = "";
			this.desc = "";
			this.url = "";
			this.avatar = "";
			this.existIn = "";
			this.display = true;
			this.avatarId = "";
		},
		//获取详情
		getInfo() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/banner/' + this.$route.params.id
			}).then(res => {
				if(res && res.statusText == "OK") {
					if(res.data) {
						that.title = res.data.title,
						that.desc = res.data.desc,
						that.url = res.data.url,
						that.avatar = res.data.avatar,
						that.existIn = res.data.existIn,
						that.display = res.data.display,
						that.avatarId = res.data.avatar.indexOf("?")!=-1 ? res.data.avatar.split("?")[1] : res.data.avatar
					}
				} else {
					Toast({message: '查询失败', duration: 1500})
					console.log(res)
					that.init()
				}
			}).catch(err => {
				Toast({message: '查询失败', duration: 1500})
				console.log(err)
				that.init()
			})
		},
		//保存
		save() {
			let that = this
			if(!that.title) {Toast({message: '请输入标题', duration: 1500}); return false;}
			if(!that.avatar || !that.avatarId) {Toast({message: '请上传图片', duration: 1500}); return false;}
		
			//编辑保存
			if(this.$route.params.id && this.$route.params.id != 0) {
				Indicator.open({text: '正在保存...', spinnerType: 'fading-circle'});
				this.$axios({
					method: 'PUT',
					url: '/mcm/api/banner/' + this.$route.params.id,
					data: {
						title: that.title,
						desc: that.desc,
						url: that.url,
						avatar: that.avatar,
						existIn: that.existIn,
						display: that.display
					}
				}).then(res => {
					Indicator.close();
					if(res && res.statusText == "OK") {
						Toast({message: '保存成功', iconClass: 'iconfont icon-success1', duration: 1500})
						that.$router.push('/admin/home')
						that.init()
					} else {
						Toast({message: '保存失败', duration: 1500})
						console.log(res)
					}
				}).catch(err => {
					Indicator.close();
					Toast({message: '保存失败', duration: 1500})
					console.log(err)
				})
			//新增保存	
			} else if(this.$route.params.id == 0) {
				Indicator.open({text: '正在保存...', spinnerType: 'fading-circle'});
				this.$axios({
					method: 'POST',
					url: '/mcm/api/banner',
					data: {
						title: that.title,
						desc: that.desc,
						url: that.url,
						avatar: that.avatar,
						existIn: that.existIn,
						display: that.display
					}
				}).then(res => {
					Indicator.close();
					if(res && res.statusText == "OK") {
						Toast({message: '保存成功', iconClass: 'iconfont icon-success1', duration: 1500})
						that.$router.push('/admin/home')
						that.init()
					} else {
						Toast({message: '保存失败', duration: 1500})
						console.log(res)
					}
				}).catch(err => {
					Indicator.close();
					Toast({message: '保存失败', duration: 1500})
					console.log(err)
				})
			}
		},
		//删除banner图片文件
		deleteBannerFile() {
			let that = this
			if(this.avatarId) {
				Indicator.open({text: '删除中...', spinnerType: 'fading-circle'});
				this.$axios({
					method: 'DELETE',
					url: '/mcm/api/file/' + this.avatarId
				}).then(res => {
					Indicator.close();
					if(res && res.statusText == "OK") {
						Toast({message: '删除成功', iconClass: 'iconfont icon-success1', duration: 1500})
						setTimeout(()=>{
							that.avatar = ''
							that.avatarId = ''
						},1000)
					} else {
						Toast({message: '删除失败', duration: 1500})
						console.log(res)
					}
				}).catch(err => {
					Indicator.close();
					Toast({message: '删除失败', duration: 1500})
					console.log(err)
				}) 
			} else {
				Toast({message: '获取BannerId失败', duration: 1500})
			}
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.editBanner{width:100%;
	input,.vueUpload{margin-bottom: 15px;}
	.vueUpload{display:flex;
		.img{margin-left:20px;position: relative;
			img{width:auto;height: 150px;display:block;}
			button{padding:0;position: absolute;top:0;right: 0;z-index: 2;padding: 0 0.3rem;opacity: .5;}
			&:hover{button{opacity: 1;}}
		}
	}
	.set{width:100%;display:flex;justify-content: center;}
}
</style>


