<template>
<ul class="bannerList" v-if="bannerList.length>0">
	<li v-for="bl in bannerList" :key="bl.id" :class="bl.display ? '' : 'disabled'">
		<router-link class="btn title" :to="'/admin/banner/'+bl.id">{{bl.title}}</router-link>
		<p class="desc">{{bl.desc}}</p>
		<div class="set">
			<button class="btn" :title="bl.display ? '隐藏' : '显示'" @click="switchDisplay(bl)">
				<i class="iconfont" :class="bl.display ? 'icon-zhengyan' : 'icon-biyan2'"></i>
			</button>
			<router-link :to="'/admin/banner/'+bl.id" class="btn" title="编辑">
				<i class="iconfont icon-pingjia"></i>
			</router-link>
			<button class="btn" title="删除" @click="deleteBanner(bl)">
				<i class="iconfont icon-shanchu7"></i>
			</button>
			<router-link to="/index" class="btn" title="查看" target="_blank">
				<i class="iconfont icon-attachment"></i>
			</router-link>
		</div>
	</li>
</ul>
<div v-else-if="isEmpty" class="none" style="width: 100%;height: 200px;line-height: 200px;text-align: center;color: #999;">没有设置Banner~</div>
</template>
<script>
import { Toast, Indicator, MessageBox } from 'mint-ui';
export default {
	name: 'BannerList',
	components: {},
	data () {
		return {
			isEmpty: true,
			bannerList: []
		}
	},
	activated() {
		this.getBannerList()
	},
	created (){},
	mounted () {},
	methods: {
		//获取banner列表
		getBannerList() {
			let that = this
			Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: 'GET',
				url: '/mcm/api/banner?filter=' + encodeURIComponent('{"where": {"existIn": "homeTop"}}')
			}).then(res => {
				Indicator.close();
				if(res && res.statusText == "OK") {
					if(res.data && res.data.length>0) {
						that.bannerList = res.data
						that.BUS.$emit('bannerTipToBanner', res.data.length)
					} else {
						that.bannerList = []
					}
				} else {
					that.bannerList = []
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				that.bannerList = []
				console.log(err)
			})
		},
		//切换banner隐藏显示
		switchDisplay(o) {
			let that = this
			Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/banner/' + o.id,
				data: {
					'display': !o.display
				}
			}).then(res => {
				Indicator.close();
				if(res && res.statusText == "OK") {
					Toast({message: '切换成功', iconClass: 'iconfont icon-success1',duration: 1500})
					that.getBannerList()
				} else {
					Toast({message: '切换失败', duration: 1500})
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				Toast({message: '切换失败', duration: 1500})
				console.log(err)
			})
		},
		//删除banner 这里先删banner表
		deleteBanner(o) {
			let that = this
			MessageBox.confirm('确定删除?').then(action => {
				if(action === 'confirm') {
					if(o.id) {
						let id = o.id
						Indicator.open({text: '删除中...',spinnerType: 'fading-circle'});
						this.$axios({
							method: 'DELETE',
							url: '/mcm/api/banner/' + id
						}).then(res => { 
							Indicator.close();
							if(res && res.statusText == "OK") {
								that.deleteImgForFile(o.avatar)
								Toast({message: '删除成功', iconClass: 'iconfont icon-success1', duration: 1500})
							} else {
								Toast({message: '删除失败', duration: 1500})
								console.log(res)
							}
						}).catch(err => {
							Indicator.close();
							console.log(err)
							Toast({message: '删除失败', duration: 1500})
						})
					} else {
						Toast({message: '未获取BannerID', duration: 1500})
					}
				} else {
					console.log(action)
				}
			}).catch(action => {
				if(action === 'cancel') {

				} else {
					console.log(action)
				}
			})
		},
		//再把file表里的图片删除
		deleteImgForFile(url) {
			let uId = url.split("?")[1]
			this.$axios({
				method: 'DELETE',
				url: '/mcm/api/file/' + uId
			}).then(res => {
				if(res && res.statusText == "OK") {

				} else {
					Toast({message: '删除图片失败', duration: 1500})
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '删除图片失败', duration: 1500})
				console.log(err)
			})
		}
	},
	watch: {
		bannerList() {
			if(!bannerList || bannerList.length==0) {
				this.isEmpty = true
			} else {
				this.isEmpty = false
			} 
		}
	},
	computed: {}
}
</script>
<style scoped lang="less">
.bannerList{
	li{display:flex;justify-content:space-between;align-items: center;border-bottom: 1px dashed #ddd;}
	.disabled .title{color:#999;}
	.title{width:10%;text-align: left;}
	.desc{width:50%;text-align: left;font-size: 1rem;color: #666;}
}
</style>


