<template>
<div class="system" v-if="system.name">
	<header>
		<button class="btn btn-success" @click="save">保存</button>
	</header>
	<section>
		<div class="item"><span>网站名</span><input type="text" class="form-control" v-model="system.name"></div>
		<div class="item"><span>关键词</span><input type="text" class="form-control" v-model="system.keys"></div>
		<div class="item"><span>描述</span><input type="text" class="form-control" v-model="system.desc"></div>
		<div class="item"><span>ICP备案号</span><input type="text" class="form-control" v-model="system.icpId"></div>
		<div class="item"><span>网安备案号</span><input type="text" class="form-control" v-model="system.beianId"></div>
		<div class="item"><span>网备图标</span><input type="text" class="form-control" v-model="system.beianAvatar"></div>
		<div class="item"><span>QQ号</span><input type="text" class="form-control" v-model="system.qq"></div>
		<div class="item"><span>统计代码</span><input type="text" class="form-control" v-model="system.tongji"></div>
		<div class="item"><span>版权信息</span><input type="text" class="form-control" v-model="system.copyRight"></div>
		<div class="item">
			<span>{{system.display?'显示':'关闭'}}</span>
			<mt-switch v-model="system.display" class="form-control" style="border:none;"></mt-switch>
		</div>
		<div class="item" v-if="!system.display">
			<span>关闭原因</span><input type="text" class="form-control" v-model="system.closeReason" />
		</div>
	</section>
</div>
</template>
<script>
import Vue from 'vue'
import { Switch, Toast } from 'mint-ui';
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_System',
	components: {},
	data () {
		return {
			system: {
				display:true
			},
			lachewang: {}
		}
	},
	created() {
		if(localStorage.lachewang) {
			this.lachewang = JSON.parse(localStorage.lachewang)
			if(this.lachewang.system) {
				this.system = this.lachewang.system
			} else {
				this.getConfig()
			}
		} else {
			this.getConfig()
		}
	},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted() {},
	methods: {
		getConfig() {
			let that = this
			this.$axios({
				method: 'GET',
				url: "/mcm/api/system/5d639c7aed90ca807e978c7f"
			}).then(res => {
				if(res && res.statusText=="OK") {
					that.system = res.data
					that.lachewang.system = that.system
					localStorage.lachewang = JSON.stringify(that.lachewang)
				}else {
					Toast({message: '查询配置信息失败', duration: 1500});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '查询配置信息失败', duration: 1500});
				console.log(err)
			})
		},
		save() {
			this.$axios({
				method: 'PUT',
				url: "/mcm/api/system/5d639c7aed90ca807e978c7f",
				data: {
					$set: {
						"name": this.system.name,
						"key": this.system.keys,
						"desc": this.system.desc,
						"icpId": this.system.icpid,
						"beianId": this.system.beianId,
						"beianAvatar": this.system.beianAvatar,
						"qq": this.system.qq,
						"tongji": this.system.tongji,
						"copyRight": this.system.copyRight,
						"display": this.system.display,
						"closeReason": this.system.closeReason
					}
				}
			}).then(res => {
				if(res && res.statusText=="OK") {
					this.lachewang.system = this.system
					localStorage.lachewang = JSON.stringify(this.lachewang)
					Toast({message: '保存成功', iconClass: 'iconfont icon-chenggong2', duration: 1500});
				}else {
					Toast({message: '保存失败', duration: 1500});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '保存失败', duration: 1500});
				console.log(res)
			})
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.system{
	header{width:100%;display: flex;justify-content: center;margin-bottom: 1%;}
	.item{width:100%;display: flex;margin-bottom: 20px;align-items: center;
		&>span{width:10%;text-align: right;padding-right: 2%;font-size: .8rem;}
	}
}
</style>


