<template>
	<aside class="menu col-md-2 bg-light sidebar">
		<dl v-for="m in menu" :key="m.id">
			<dt>{{m.title}}</dt>
			<dd v-if="m.items && m.items.length>0">
				<ul class="nav flex-column">
					<li class="nav-item" v-for="i in m.items" :key="i.id" :class="i.active">
						<router-link class="nav-link" :to="i.url" @click.native="lightMenu">{{i.title}}</router-link>
					</li>
				</ul>
			</dd>
		</dl>
	</aside>
</template>
<script>
export default {
	name: 'AdminMenu',
	data () {
		return {
			menu: [{
				id: 1,
				entitle: 'Common Operation',
				title: '常规操作',
				items: [{ 
					id: '1-1', 
					entitle: 'Catalogs',
					title: '栏目管理', 
					name: 'Admin_Column', 
					url: '/admin/column', 
					active: '',
					authorize: ['administrator'] 
				},{ 
					id: '1-2', 
					entitle: 'Articles',
					title: '文章管理', 
					name: 'Admin_Article', 
					url: '/admin/article/0', 
					active: '',
					authorize: ['administrator'] 
				},{ 
					id: '1-3', 
					entitle: 'Reviews',
					title: '评论管理', 
					name: 'Admin_Reviews', 
					url: '/admin/reviews', 
					active: '',
					authorize: ['administrator']  
				},{ 
					id: '1-4', 
					entitle: 'Recycle',
					title: '回收站', 
					name: 'Admin_Recycle', 
					url: '/admin/recycle', 
					active: '',
					authorize: ['administrator']  
				}]
			},{
				id: 2,
				title: '系统配置',
				entitle: 'System Configuration',
				items: [{ 
					id: '2-1', 
					entitle: 'Base Configuration',
					title: '基本配置', 
					name: 'Admin_System', 
					url: '/admin/system', 
					active: '',
					authorize: ['administrator']   
				},{ 
					id: '2-2', 
					entitle: 'System Manager',
					title: '用户管理', 
					name: 'Admin_User', 
					url: '/admin/user', 
					active: '',
					authorize: ['administrator']  
				},{ 
					id: '2-3', 
					entitle: 'Role Manager',
					title: '角色管理', 
					name: 'Admin_User_Role', 
					url: '/admin/user/role', 
					active: '',
					authorize: ['administrator']  
				}/*,{ 
					id: '2-3', 
					entitle: 'Update Cache',
					title: '更新缓存', 
					name: 'Admin_Cache', 
					url: '/admin/cache', 
					active: '',
					authorize: ['administrator']  
				}*/,{ 
					id: '2-4', 
					entitle: 'About This System',
					title: '关于本系统', 
					name: 'Admin_About', 
					url: '/admin/about', 
					active: '',
					authorize: ['administrator']  
				}]
			}]
		}
	},
	created() {},
	activated() {
		this.lightMenu()
	},
	mounted() {
		let that = this
		this.BUS.$on('setSideActived', name => {
			that.lightMenu(name)
		})
	},
	methods: {
		lightMenu (n) {
			if(!n){n=this.$route.name}
			this.menu.forEach(item => {
				if(item.items && item.items.length>0) {
					let ii = item.items
					ii.forEach(subItem => {
						if(subItem.name==='Admin_User_Role') {
							if(n==='Admin_User_Groups' || n==='Admin_User_Role') {
								subItem.active = 'active'
							} else {
								subItem.active = ''
							}
						} else {
							subItem.name === n ? subItem.active = 'active' : subItem.active = ''
						}
					})
				}
			})
		}
	}
}
</script>
<style scoped lang="less">
.menu{padding-top: 2vw;
	dl{margin-bottom: 2vw;}
	dt{padding-left: 1rem;margin-bottom: 1vw;}
	a{font-size: .8vw;}
	.nav-item{transition: all 0.3s;}
	.active,.nav-item:hover{background-color: #373277;}
	.active a,.nav-item:hover a{color:#fff;}
}
</style>	


