<template>
<div class="login">
	<h1>拉扯网后台管理系统</h1>
	<div class="container">
		<input type="text" name="username" v-model.trim="username" class="form-control" placeholder="name" />
		<input type="password" name="password" v-model.trim="password" class="form-control" placeholder="password" @keyup.enter="login" />
		<button @click="login" class="btn btn-primary">登录</button>
	</div>
</div>
</template>
<script>
import { Toast, Indicator } from 'mint-ui'
export default {
	name: 'login',
	components: {},
	data() {
		return {
			username: '',  
			password: '',
			lachewang: {}
		}
	},
	activated() {
		document.title="登录"
	},
	created() {
		if(localStorage.lachewang) {
			this.lachewang = JSON.parse(localStorage.lachewang)
		}
	},
	mounted() {
		if(this.lachewang.isLogin) {
			this.$router.push({name: 'Admin'})
		}
	},
	methods: {
		login() {
			let that = this;
			if(localStorage.lachewang) {
				that.lachewang = JSON.parse(localStorage.lachewang)
				let lt = that.lachewang.loginTimes
				if(lt) {
					if(parseInt(lt)>=3) {
						Toast({message: '今日登录错误次数已上限，明日再来吧', duration: 2000}); return false;
					}
				} else {
					that.lachewang.loginTimes==0
					localStorage.lachewang = JSON.stringify(that.lachewang)
				}
			} else {
				that.$router.push({name: 'Front_Index'});
			}

			let isLogin = that.lachewang.isLogin
			if(isLogin) {
				that.$router.push({name: 'Admin'});
				return false;
			}

			if(!that.username){ Toast({message: '请输入账号', duration: 1500}); return false; }
			if(!that.password){ Toast({message: '请输入密码', duration: 1500}); return false; }
			Indicator.open({text: '登录中...',spinnerType: 'fading-circle'});
			this.$axios({
				method: 'POST',
				url: '/mcm/api/user/login',
				data: {
					"username": that.username,
    				"password": that.password
				}
			}).then(res => { 
				Indicator.close(); 
				if(res) {
					if(res.data) {
						if(res.data.id && res.data.userId) { 
							that.lachewang.isLogin = true
							//that.lachewang.accountID = res.data.userId
							//that.lachewang.tokenID = res.data.id
							//that.lachewang.accountName = that.username
							localStorage.lachewang = JSON.stringify(that.lachewang)
			  				//弹出登录成功提示
			  				let instance = Toast({
			  					message: '登录成功', 
			  					iconClass: 'iconfont icon-success1',
			  					duration: 1500
			  				})
			  				//提示窗消失后跳转到管理后台
			  				setTimeout(() => {
							  	instance.close();
							  	that.username = ''
								that.password = ''
							  	that.$router.push({name: 'Admin'});
							}, 2000);
						} else {		
			  				console.log(1,res)
						}
					} else {		
		  				console.log(2,res)
					}
				} else {
		  			console.log(3,res)
		  		}
			}).catch(err => { 
				Indicator.close();
				that.lachewang.loginTimes = (parseInt(that.lachewang.loginTimes)||0) + 1
				localStorage.lachewang = JSON.stringify(that.lachewang)
				Toast({message: '账号或密码错误', duration: 2000})
				console.log(err)
				setTimeout(()=>{
					that.username = ''
					that.password = ''
				},2500)
			})
		}
	},
	watch: {},
	computed: {}
}
</script>

<style scoped lang="less">
.login{width:100%;height: 100%;display: flex;align-items: center;flex-direction: column;padding-top: 100px;box-sizing: border-box;
	h1{color: #373277;margin-bottom: 100px;}
	.container{display: flex;justify-content: center;align-items: center;flex-direction: column;
		input{margin-bottom: 20px;}
		.btn{background-color: #373277;color:#fff;border-color: #373277;}
		input, button{width:300px;}
	}
}
</style>


