<template>
<div class="reviews">
	<div class="card" v-if="list.length>0">
		<header class="card-header">
			<span class="id">ID</span>
			<span class="articleID">文章地址</span>
			<span class="commentator">评论者昵称</span>
			<span class="email">邮箱</span>
			<span class="comments">评论内容</span>
			<span class="createTime">评论时间</span>
			<span class="set">操作</span>
		</header>
		<ul class="card-body">
			<li v-for="(l,index) in list" :key="l.id">
				<div class="id">{{index+1}}</div>
				<div class="articleID"><router-link :to="'/article/detail/'+l.articleId" target="_blank">查看</router-link></div>
				<div class="commentator">{{l.nickname}}</div>
				<div class="email">{{l.email}}</div>
				<div class="comments">{{l.content}}</div>
				<div class="createTime">{{getFullTime(l.createdAt)}}</div>
				<div class="set">
					<button v-if="!l.display" class="btn btn-success btn-sm" @click="changeShow(l.id)">审核通过</button>
					<button v-else class="btn btn-warning btn-sm" @click="changeClose(l.id)">关闭评论</button>
					<button class="btn btn-danger btn-sm" @click="deleteReview(l.id)">删除评论</button>
				</div>
			</li>
		</ul>
		<footer class="card-footer">
			
			<button class="btn btn-primary" 
			@click.stop.prevent='toFirst' 
			 :class="pages.current>1 ? '' : 'disabled'">首页</button>
			<button class="btn btn-primary" 
			 @click.stop.prevent='toPrev' 
			 :class="pages.current>1 ? '' : 'disabled'">上一页</button>
			<button class="btn" 
				v-for="p in pages.list" 
				:key="p" 
				:class="p===pages.current ? 'btn-default disabled' : 'btn-primary'"
				@click.stop.prevent='toThisPage(p)'
			>{{p}}</button>
			<button class="btn btn-primary" 
			 @click.stop.prevent='toNext' :class="pages.current<pages.total ? '':'disabled'">下一页</button>
			<button class="btn btn-primary"
			 @click.stop.prevent='toLast' :class="pages.current<pages.total ? '':'disabled'">末页</button>
		</footer>
	</div>
	<section v-else class="empty">
		<img :src="empty" alt=""><span>Empty</span><i>空空如也~</i>
	</section>
</div>
</template>
<script>
import { Toast, Indicator, MessageBox } from 'mint-ui'
import { mapState } from 'vuex'
import empty from '../../../assets/empty.jpg'
export default {
	name: 'Admin_Reviews',
	components: {},
	data () {
		return {
			empty,
			list: [],
			pages: {
				list: [], //页码列表
				count: 0, //总列表数
				current: 1, //当前页码
				size: 15, //每页列表数
				total: 0 //总页数
			}
		}
	},
	created() {},
	activated() {
		this.init()
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted() {},
	methods: {
		init() {
			this.getCommentsTotal()
			this.getComments()
		},
		//获得评论总条数
		getCommentsTotal() {
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			let that = this
			let params = encodeURIComponent('{"where":{}}')
			this.$axios({
				method: 'GET',
				url: '/mcm/api/comments/count?filter='+params
			}).then(res => {
				Indicator.close();
				if(res && res.status === 200 && res.statusText === "OK") {
					that.pages.count = res.data.count
					that.setPages()
				} else {
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				console.log(err)
			})
		},
		getComments() {
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			let that = this
			let limit = this.pages.size
			let skips = (this.pages.current-1) * limit
			this.$axios({
				method: 'GET',
				url: '/mcm/api/comments?filter={"where":{},"skip":'+skips+',"limit":'+limit+'}'
			}).then(res => {
				Indicator.close();
				if(res && res.statusText=="OK") {
					if(res.data && res.data.length>0) {
						that.list = res.data
					} else {
						that.list = []
					}
				} else {
					that.list = []
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				that.list = []
				console.log(err)
			})
		},
		//通过文章总条数，设置分页
		setPages() { 
			if(this.pages.count > 0) {
				let p = this.getPages(this.pages.count, this.pages.size)
				this.pages.total = p.pageTotal
				this.pages.list = p.pageList
			} else {
				this.pages.current = 1
				this.pages.count = 0
				this.pages.size = 15
				this.pages.total = 0
				this.pages.list = []
			}
		},
		toFirst() {
			this.pages.current = 1
			this.init()
		},
		toPrev() {
			if(this.pages.current>1){
				this.pages.current = this.pages.current-1
				this.init()
			}
		},
		toNext() {
			if(this.pages.current<this.pages.total){
				this.pages.current = this.pages.current+1
				this.init()
			}
		},
		toLast() {
			this.pages.current = this.pages.total
			this.init()
		},
		toThisPage(p){
			if(p!==this.pages.current){
				this.pages.current = p
				this.init()
			}
		},
		//审核通过
		changeShow(id) {
			if(id) {
				let that = this
				Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
				this.$axios({
					method: 'PUT',
					url: '/mcm/api/comments/'+id,
					data: {
						'display': true
					}
				}).then(res => {
					Indicator.close();
					if(res && res.statusText=="OK") {
						Toast({message: '审核成功', duration: 1500});
						that.init()
					} else {
						Toast({message: '审核失败', duration: 1500});
						console.log(res)
					}
				}).catch(err => {
					Indicator.close();
					Toast({message: '审核失败', duration: 1500});
					console.log(err)
				})
			} else {
				Toast({message: '未获得评论ID', duration: 1500});
			}
		},
		//关闭评论
		changeClose(id) {
			if(id) {
				let that = this
				Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
				this.$axios({
					method: 'PUT',
					url: '/mcm/api/comments/'+id,
					data: {
						'display': false
					}
				}).then(res => {
					Indicator.close();
					if(res && res.statusText=="OK") {
						Toast({message: '关闭成功', duration: 1500});
						that.init()
					} else {
						Toast({message: '关闭失败', duration: 1500});
						console.log(res)
					}
				}).catch(err => {
					Indicator.close();
					Toast({message: '关闭失败', duration: 1500});
					console.log(err)
				})
			} else {
				Toast({message: '未获得评论ID', duration: 1500});
			}
		},
		//删除评论
		deleteReview(id) {
			if(id) {
				let that = this
				MessageBox.confirm('确定删除评论吗？').then(action => {
  					if(action == 'confirm') {
  						//执行删除评论
  						Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
  						that.$axios({
  							method: 'DELETE',
  							url: '/mcm/api/comments/'+id
  						}).then(res => {
  							Indicator.close();
  							if(res && res.statusText=="OK") {
  								Toast({message: '删除成功', duration: 1500});
  								that.init()
  							} else {
  								Toast({message: '删除失败', duration: 1500});
  								console.log(res)
  							}
  						}).catch(err => {
  							Indicator.close();
  							Toast({message: '删除失败', duration: 1500});
  							console.log(err)
  						})
  					} else {
  						console.log(action)
  					}
				}).catch(err => {
					if(err == 'cancel') {
						console.log('取消了删除操作')
					} else {
						console.log(err)
					}
				});
			} else {
				Toast({message: '未获得评论ID', duration: 1500});
			}
		}
	},
	watch: {},
	computed: {
		...mapState(['server', 'getPages', 'getFullTime'])
	}
}
</script>
<style scoped lang="less">
.reviews{
	.card-header{width:100%;padding:10px 0;display: flex;justify-content:space-around;font-size: 1rem;text-align: center;
		span{text-align: center;}
	}
	.card-body{width:100%;min-height: 650px; padding:0;display: flex;flex-direction: column;
		li{width:100%;display: flex;justify-content:space-around;align-items: center;padding:10px 0;transition: all 0.3s;
			&:hover{background-color:#f5f5f5;}
			div{display: flex;justify-content:center;align-items: center;font-size: .8rem;}
			button{color:#fff;font-size: .8rem;}
			.set{width:190px;padding: 0 20px;box-sizing: border-box;display:flex;justify-content:flex-end;}
			.btn-danger{margin-left: 10px;}
		}
	}
	.id{width:5%;}
	.articleID{width:5%;}
	.articleID a{color:#4fc08d;}
	.commentator{width:10%;}
	.email{width:10%;}
	.comments{width:45%;}
	.createTime{width:10%;}
	.set{width:190px;}
	.card-footer{width:100%;display: flex;justify-content:center;
		button{margin-right: 10px;}
	}
	.empty{min-height: 750px; background-color:#f6f6f6;display: flex;justify-content:center;align-items: center;flex-direction: column;
		img{height:300px;width: auto;display:block;}
		span{display:block;text-align: center;margin-top:-150px;left:-10px;position: relative;color:#ccc;}
		i{display:block;font-style: normal;font-size: 12px;color:#ccc;left:-8px;position: relative;margin-top:83px;
    	}
	}
}
</style>


