import { Toast } from 'mint-ui'
export default {
	showMenu: '',
	server: {
		spreadFlag: false
	},
	catalogs: [],
	homeBanner: [],
	homeCurtain: [],
	homeWorks: [],
	flink: [],
	systemConfig: {},
	//重组栏目，由单一列表组合成嵌套列表
	recombination: (list)=>{
		let arr = [];
		//将栏目按一级栏目、二级栏目、三级栏目这样组合，放到一个数组中，组成一个二维数组
		list.forEach(a => {
			let i = a.level-1
			if(!arr[i] || arr[i].length==0) {
				arr[i] = []
			}
			let j = arr[i].length
			a.children=[]
			arr[i][j]=a
		})
		//如果有二级栏目，则开始递归循环，否则就不用白费劲了
		if(arr[1] && arr[1].length>0) {
			//循环二维数组
			arr.forEach((item,index) => {
				let i = index+1
				if(arr[i] && arr[i].length>0) {
					//每次用下级栏目循环上级父栏目
					this.a.digui(item, arr[i])
				}
			})
		} 
		//注意，arr数组只是一个组装数据的工具，最后全部归纳到了arr二维数组的第一个子数组中，所以这里要用arr[0]表示完整数据
		return arr[0]
	},
	//用于上述方法的扩展
	digui: (aCurrent,aNext)=>{
		aNext.forEach((an,index) => {
			//通过try catch方式，在循环到正确位置时，完成相关操作后，通过throw终止循环，继续下一轮，以此来减少遍历次数
			try {
				aCurrent.forEach((ac,index) => {
					//如果下级栏目的父ID是上级栏目的ID
					if(an.parentId && ac.id && (ac.id === an.parentId)) {
						//则上级栏目把下级栏目收入children中
						ac.children.push(an)
						throw new Info('exist')
					}
				})
			} catch (e) {
				if(e.message == 'exist') throw e
			}
		})
	},
	//分页，通过总条数，每页条数，分出总页数
	getPages(count, size) {
		let pageTotal = 0
		if(count % size === 0) {
			pageTotal = parseInt(count / size)
		} else {
			pageTotal = parseInt(count / size) + 1
		}
		let pageList = []
		for( let i = 1; i <= pageTotal; i++ ) {
			pageList.push(i)
		}
		return { pageList, pageTotal }
	},
	//获得完整时间
	getFullTime(t) {
		let y = new Date(t).getFullYear()
		let m = new Date(t).getMonth()+1
		m = m < 10 ? ('0' + m) : m
		let d = new Date(t).getDate()
		d = d < 10 ? ('0' + d) : d
		let hh = new Date(t).getHours()
		hh = hh < 10 ? ('0' + hh) : hh
		let mm = new Date(t).getMinutes()
		mm = mm < 10 ? ('0' + mm) : mm
		let ss = new Date(t).getSeconds()
		ss = ss < 10 ? ('0' + ss) : ss
		return y+'-'+m+'-'+d+' '+hh+':'+mm+':'+ss
	},
	//上传图片, 并返回结果
	uploadImg(imgData) { console.log(imgData)
		this.$axios({
			method: 'POST',
			url: '/mcm/api/file',
			async: false,
			data: imgData
		}).then(res => {
			if(res && res.data && res.data.id) {
				Toast({message: '上传成功', iconClass: 'iconfont icon-success1',duration: 1500})	
			} else {
				Toast({message: '上传失败', duration: 1500})	
				console.log(res)
			}
		}).catch(err => {
			Toast({message: '上传失败', duration: 1500})	
			console.log(err)
		})
	}
}


