<template>
<div class="catalog">
	<header>
		<button class="btn btn-success btn-sm" @click="openAddCatalogDialog">添加栏目</button>
	</header>
	<ul class="list-group" v-if="catalogs && catalogs.length>0">
		<adminCatalogItem
			v-for="catalog in columns"
			:key="catalog.id"
			:class="catalog.display?'':'disable'"
			:catalog="catalog"
		>
		</adminCatalogItem>
	</ul>
	<AdminAddCatalog :catalogList="columns"></AdminAddCatalog>
</div>
</template>
<script>
import { mapState } from 'vuex'
import axios from 'axios'
import AdminCatalogItem from './AdminCatalogItem.vue'
import AdminAddCatalog from './AdminAddCatalog.vue'
export default {
	name: 'AdminCatalog',
	components: { AdminCatalogItem, AdminAddCatalog },
	data () {
		return {
			lachewang: {},
			catalogs: [],
			columns: []
		}
	},
	activated () {
		this.BUS.$emit('setSideActived', this.$route.name)
		this.init()
	},
	mounted () {
		let that = this
		this.BUS.$on('updateCatalog', () => {
			that.init()
		})
	},
	methods: {
		//初始化
		init() {
			if(localStorage.lachewang) {
				this.lachewang = JSON.parse(localStorage.lachewang)
				if(this.lachewang.catalogs && this.lachewang.catalogs.length>0) {
					this.catalogs = this.lachewang.catalogs
					this.columns = []
					this.columns = this.recombination(this.catalogs)
				} else {
					this.getCatalogs()
				}
			} else {
				this.getCatalogs()
			}
		},
		//获得栏目列表信息
		getCatalogs() {
			let that = this
			axios({
				method: 'GET',
				url: '/mcm/api/catalogs',
			}).then(res => {
				if(res && res.data && res.data.length>0) {
					that.catalogs = res.data
					that.lachewang.catalogs = that.catalogs
					localStorage.lachewang = JSON.stringify(that.lachewang)

					that.columns = []
					//循环总栏目列表
					if(that.catalogs.length>0) {
						that.columns = that.recombination(that.catalogs)
					}
				} else {
					that.lachewang.catalogs = that.columns = that.catalogs = []
					localStorage.lachewang = JSON.stringify(that.lachewang)
					console.log(res)
				}
			}).catch(err => {
				console.log(err)
			})
		},
		
		//控制栏目管理弹窗显示或隐藏
		openAddCatalogDialog () {
			let lachewang = JSON.parse(localStorage.lachewang)
			lachewang.addCatalogDialog = true
			localStorage.lachewang = JSON.stringify(lachewang)
			this.BUS.$emit('openAddCatalogDialog', true)
		}
	},
	watch: {},
	computed: {
		...mapState(['recombination'])
	}
}
</script>
<style scoped lang="less">
.catalog{
	header{margin-bottom: 1vw;}
}
</style>


