<template>
<div class="row" v-if="systemConfig">
	<p>©<span v-text="nowYear"></span> 
		<span> {{systemConfig.copyRight}}</span> 
		<a v-if="systemConfig.qq"
		 :href="'http://wpa.qq.com/msgrd?v=3&uin='+systemConfig.qq+'&site=qq&menu=yes'" target="_blank">
			<img border="0" align="absmiddle" title="您好，我是威龙，点击这里可以给我发消息哦~" alt="您好，我是威龙，点击这里可以给我发消息哦~" :src="'http://wpa.qq.com/pa?p=2:'+systemConfig.qq+':41'" />&nbsp;
		</a> 
	</p>
	<p>
		<a v-if="systemConfig.beianId" 
		href="http://www.beian.gov.cn/portal/registerSystemInfo" target="_blank">
			<img :src="systemConfig.beianAvatar" :alt="'京公网安备'+systemConfig.beianId" />京公网安备{{systemConfig.beianId}}
		</a> 
		<a href="http://www.beian.miit.gov.cn/" target="_blank"> {{systemConfig.icpId}}</a>
		<button class="btn btn-default manage" @click="manage">管理</button>
	</p>
</div>
</template>
<script>
import { Toast } from 'mint-ui'	
import { mapState } from 'vuex'
export default {
	name: 'Copyright',
	props: ['systemConfig', 'isLogin'],
	data () {
		return {
			nowYear: ''
		}
	},
	mounted() {
		this.init()
	},
	methods: {
		init() {
			let nd = new Date()
			this.nowYear = nd.getFullYear()
		},
		manage() {
			this.isLogin ? this.$router.push({name: 'Admin'}) : this.$router.push({name: 'Login'})
		}
	}
}
</script>
<style scoped lang="less">
.row{justify-content:center;font-size:0.8vw;color:#666;
	a{color:#666;}
	a:hover{color:#4fc08d;}
	button.manage{font-size:0.5vw; padding:0;margin:0;border:none;font-family: Avenir;line-height: normal;margin-top:-0.2vw;color:#fff;}
}
</style>


