<template>
<div class="worksEdit">
	<input type="text" placeholder="title" v-model="info.title" class="form-control" />
	<textarea placeholder="desc" v-model="info.desc" class="form-control" ></textarea>
	<input type="text" placeholder="url" v-model="info.url" class="form-control" />
	<mt-switch v-model="info.display"></mt-switch>
	<section class="uploadImgs">
		<div class="imgs">
			<vue-upload></vue-upload>
			<span class="noborder" v-if="info.avatar">
				<img :src="info.avatar" alt="">
				<button class="btn btn-danger" title="删除" @click="deleteImg"><i class="iconfont icon-shanchu1"></i></button>
			</span>
		</div>
		<div class="qrCode">
			<vue-upload tip="上传二维码"></vue-upload>
			<span class="noborder" v-if="info.qrCodeImgUrl">
				<img :src="info.qrCodeImgUrl" alt="">
				<button class="btn btn-danger" title="删除" @click="deleteImg('qr')"><i class="iconfont icon-shanchu1"></i></button>
			</span>
		</div>
	</section>
	<button class="btn btn-success btn-sm" @click="save">保存</button>
</div>
</template>
<script>
import Vue from 'vue'
import { Switch, Toast, Indicator, MessageBox } from 'mint-ui';
Vue.component(Switch.name, Switch);
import { mapState } from 'vuex'
import VueUpload from './VueUpload'
export default {
	name: 'worksEdit',
	components: {VueUpload},
	data () {
		return {
			msg: 'worksEdit',
			id: '',
			info: {
				id: '',
				title: '',
				desc: '',
				url: '',
				display: '',
				avatar: '',
				qrCodeImgUrl: ''
			},
			avatarId: '',
			qrCodeImgId: ''
		}
	},
	activated() {
		this.id = this.$route.params.id
		this.init()
	},
	created (){},
	mounted () {
		let that = this
		//this.BUS.$on("fileChange", (file,input) => {
			//console.log(input.nextSibling.target)
		//})
		//this.BUS.$on("progress", (file, percentage) => {
			//console.log(file, percentage)
		//})
		this.BUS.$on("uploadSuccess", (file, res, type) => {
			if(type == "上传缩略图" || type == "上传二维码") {
				Toast({message: '上传成功', iconClass: 'iconfont icon-success1',duration: 1500})
				setTimeout(()=>{
					if(type==="上传缩略图"){
						that.info.avatar = res.url+"?"+res.id
						that.avatarId = res.id
					} else if(type==="上传二维码"){
						that.info.qrCodeImgUrl = res.url+"?"+res.id
						that.qrCodeImgId = res.id
					}
					that.BUS.$emit('doInitForUpload')
				}, 1000)
			}
		})
	},
	methods: {
		//初始化
		init() {
			//新增
			if(this.id==0) {
				this.info = {
					id: '',
					title: '',
					desc: '',	
					url: '',
					display: true,
					avatar: '',
					qrCodeImgUrl: ''
				}
			//编辑
			} else {
				this.info.id = this.id
				this.getInfo()
			}	 
		},
		//获取作品详细信息
		getInfo() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/works/' + this.id
			}).then(res => {
				if(res && res.statusText == "OK") {
					if(res.data) {
						that.info = res.data
						if(that.info.avatar) {
							that.avatarId = that.info.avatar.split('?')[1]
						}
						if(that.info.qrCodeImgUrl) {
							that.qrCodeImgId = that.info.qrCodeImgUrl.split('?')[1]
						}
					}
				} else {
					console.log(res)
				}
			}).catch(err => {
				console.log(err)
			})
		},
		//删除图片
		deleteImg(t) {
			let that = this
			MessageBox.confirm('确定删除?').then(action => {
				if(action === 'confirm') {
					if(this.avatarId) {
						Indicator.open({text: '删除中...',spinnerType: 'fading-circle'});
						this.$axios({
							method: 'DELETE',
							url: '/mcm/api/file/' + this.avatarId
						}).then(res => { 
							Indicator.close();
							if(res && res.statusText == "OK") {
								that.deleteImgForWorks()
								Toast({message: '删除成功', iconClass: 'iconfont icon-success1', duration: 1500})
								setTimeout(() => {
									if(t && t=="qr") {
										that.info.qrCodeImgUrl = ''
										that.qrCodeImgId = ''
									} else {
										that.info.avatar = ''
										that.avatarId = ''
									}
									that.BUS.$emit('doInitForUpload')
								}, 1500)
							} else {
								Toast({message: '删除失败', duration: 1500})
								console.log(res)
							}
						}).catch(err => {
							Indicator.close();
							console.log(err)
							Toast({message: '删除失败', duration: 1500})
						})
					} else {
						Toast({message: '获取图片Id异常', duration: 1500})	
					}
				} else {
					console.log(action)
				}
			}).catch(err => {
				if(err === 'cancel') {
					//console.log('取消删除')
				} else {
					console.log(err)
				}
			})
		},
		//删除作品表里的图片
		deleteImgForWorks(t) {
			if(this.$route.params.id) {
				let p = {}
				if(t && t=="qr") {
					p.qrCodeImgUrl = ''
				} else {
					p.avatar = ''
				}
				this.$axios({
					method: 'PUT',
					url: '/mcm/api/works/' + this.$route.params.id,
					data: p
				}).then(res => {
					if(res && res.statusText == "OK") {
						//删除成功
					} else {
						Toast({message: '删除图片失败', duration: 1500})
						console.log(res)
					}
				}).catch(err => {
					Toast({message: '删除图片失败', duration: 1500})
					console.log(err)
				})
			} else {
				Toast({message: '获取作品Id异常', duration: 1500})	
			}
		},
		//保存
		save() {
			let that = this
			//新增保存
			if(this.id == 0) {
				let that = this
				Indicator.open({text: '保存中...',spinnerType: 'fading-circle'});
				this.$axios({
					method: 'POST',
					url: '/mcm/api/works',
					data: {
						'title': that.info.title,
						'desc': that.info.desc,	
						'url': that.info.url,
						'display': that.info.display,
						'avatar': that.info.avatar,
						'qrCodeImgUrl': that.info.qrCodeImgUrl,
					}
				}).then(res => {
					Indicator.close();
					if(res && res.statusText == "OK") {
						Toast({message: '保存成功', iconClass: 'iconfont icon-success1',duration: 1500})		
		  				setTimeout(() => {
		  					that.clearWorks()
						  	that.$router.push('/admin/works')
						}, 1500);
					} else {
						console.log(res)
					}
				}).catch(err => {
					Indicator.close();
					console.log(err)
				})
			//编辑保存	
			} else {
				let that = this
				Indicator.open({text: '保存中...',spinnerType: 'fading-circle'});
				this.$axios({
					method: 'PUT',
					url: '/mcm/api/works/' + this.id,
					data: {
						'title': that.info.title,
						'desc': that.info.desc,	
						'url': that.info.url,
						'display': that.info.display,
						'avatar': that.info.avatar,
						'qrCodeImgUrl': that.info.qrCodeImgUrl,
					}
				}).then(res => {
					Indicator.close();
					if(res && res.statusText == "OK") {
						Toast({message: '保存成功', iconClass: 'iconfont icon-success1',duration: 1500})		
		  				setTimeout(() => {
		  					that.clearWorks()
						  	that.$router.push('/admin/works')
						}, 1500);
					} else {
						console.log(res)
					}
				}).catch(err => {
					Indicator.close();
					console.log(err)
				})
			}
		},
		//清除本地缓存作品，便于重新请求新数据
		clearWorks () {
			if(localStorage.lachewang) {
				let l = JSON.parse(localStorage.lachewang)
				l.works = null
				localStorage.lachewang = JSON.stringify(l)
			}
		}
	},
	watch: {},
	computed: {
		...mapState(['uploadImg'])
	}
}
</script>
<style scoped lang="less">
.worksEdit{
	input,textarea,label{margin-bottom: 20px;font-size: .9rem;}
	textarea{resize: none;}
	.uploadImgs{width:100%;display:flex;margin-bottom: 20px;
		.imgs, .qrCode{display:flex;position: relative;width:30%;
			.noborder{width:120px;height: 120px;display:flex;justify-content:center;align-items: center;position: relative;border:1px solid #ddd;box-sizing: border-box;border-radius: 4px;overflow:hidden;
				img{width:100%;height: auto;display:block;}
				button{position: absolute;top: 0;right: 0;z-index: 2;padding:0 .4rem;opacity: 0.5;transition: all 0.3s;}
				button:hover{opacity: 1;}
			}
		}
	}
}
</style>


