<template>
<ul class="columns">
	<li v-for="c in columns" :key="c.id">
		<button class="btn btn-default btn-block" @click="chooseThisCatalog(c)">{{c.name}}</button>
		<div class="children" v-if="c.children && c.children.length>0">
			<article-columns :columns=c.children></article-columns>
		</div>
	</li>
</ul>
</template>
<script>
export default {
	name: 'ArticleColumns',
	components: {},
	props: ['columns'],
	data () {
		return {
			msg: 'columns'
		}
	},
	created() {},
	mounted() {
	},
	methods: {
		chooseThisCatalog(obj) {
			this.BUS.$emit('chooseThisCatalog',obj)
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.columns{
	button{text-align: left;}
	button:hover{background-color:#f5f5f5;color:#4fc08d;}
	.children{margin-left:4vw;}
}
</style>


