<template>
<div class="role card">
	<div class="card-header">
		<span>角色管理</span>
		<button class="btn btn-success btn-sm" title="添加角色" @click="add">
			<i class="iconfont icon-jiahao1"></i>
		</button>
	</div>
	<div class="card-body">
		<ul>
			<li class="header">
				<div class="name">角色</div>
				<div class="name">名称</div>
				<div class="desc">描述</div>
				<div class="permission">权限</div>
				<div class="status">状态</div>
				<div class="set">操作</div>
			</li>
			<li v-for="l in list" :key="l.id">
				<div class="name">{{l.name}}</div>
				<div class="title">{{l.title}}</div>
				<div class="desc">{{l.description}}</div>
				<div class="permission">{{renderPermission(l.permission)}}</div>
				<div class="status">
					<mt-switch v-model="l.display"></mt-switch>
				</div>
				<div class="set">
					<button type="button" class="btn btn-default" @click="edit(l.id)" title="编辑角色">
						<i class="iconfont icon-pingjia"></i>
					</button>
					<button type="button" class="btn btn-default" @click="del(l)" title="删除角色">
						<i v-if="l.title!='超级管理员'" class="iconfont icon-shanchu9"></i>
					</button>
				</div>
			</li>
		</ul>
	</div>
	<div class="card-footer">共 {{list.length}} 个角色</div>
</div>
</template>
<script>
import { Switch, Indicator, MessageBox, Toast } from 'mint-ui';
import Vue from 'vue'
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_User_Groups',
	components: {},
	data () {
		return {
			list: []
		}
	},
	created() {},
	activated() {
		this.getRoles()
	},
	mounted() {},
	methods: {
		//获取角色列表
		getRoles() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/role?filter={"where":{}}'
			}).then(res => {
				if(res && res.statusText=="OK") {
					if(res.data && res.data.length>0) {
						that.list = res.data
					} else {
						Toast({message: '未查到', duration: 1500});
					}
				} else {
					Toast({message: '查询失败', duration: 1500});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '查询失败', duration: 1500});
				console.log(err)
			})
		},
		//添加角色
		add() {
			this.$router.push('/admin/user/role/0')
		},
		//编辑角色
		edit(id) {
			this.$router.push('/admin/user/role/'+id)
		},
		//删除角色
		del(o) {
			let that = this
			if(o.title != '超级管理员') {
				MessageBox.confirm("确定删除该角色吗?").then(action => {
					if(action == "confirm") {
						that.$axios({
							method: 'DELETE',
							url: '/mcm/api/role/' + o.id
						}).then(res => {
							if(res && res.statusText == "OK") {
								Toast({message: '删除成功', iconClass: 'iconfont icon-success1'});
								setTimeout(() => {
									that.getRoles()
								}, 1500)
							} else {
								Toast({message: '删除失败', duration: 1500});
								console.log(res)
							}
						}).catch(err => {
							Toast({message: '删除失败', duration: 1500});
							console.log(err)
						})
					} else {
						console.log(action)
					}
				}).catch(action => {
					if(action == "cancel") {
						//点击了取消
					} else {
						console.log(action)
					}
				})
			}
		},
		//渲染权限信息
		renderPermission(list) {
			if(list.length>0) {
				let s = ""
				s = list.join("、")
				return s
			} else {
				return "——"
			}
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.role{
	.card-header{width:100%;display: flex;justify-content:space-between;}
	.card-body{width:100%;display: flex;flex-direction: column;
		li{width:100%;display: flex;font-size:0.9rem;margin-bottom: 5px;
			div{padding-left:10px;box-sizing: border-box;text-align: center;}
			.title{width:10%;}
			.name{width:10%;}
			.desc{width:30%;}
			.permission{width:30%;}
			.status{width:10%;display:flex; justify-content:center;}
			.set{width:10%;
				button{width:42px;height: 40px;}
			}
			&.header{font-weight: 700;margin-bottom: 15px;font-size:1rem;}
		}
	}
	.card-footer{text-align: right;font-size: .9rem;color: #666;}
}
</style>


