import Vue from 'vue'
import Router from 'vue-router'
import Front from '@/components/front/Index'
import Front_Index from '@/components/front/index/Index'
import Front_Article from '@/components/front/article/Index'
import Front_Article_Detail from '@/components/front/article/Detail'
import Front_Picture from '@/components/front/picture/Index'
import Front_Contact from '@/components/front/contact/Index'
import Front_Search from '@/components/front/search/Index'
import Front_Close from '@/components/front/Close'
import Admin from '@/components/admin/Index'
import Admin_Column from '@/components/admin/column/Index'
import Admin_Article from '@/components/admin/article/Index'
import Admin_Article_Home from '@/components/admin/article/Home'
import Admin_Article_EditBanner from '@/components/admin/article/EditBanner'
import Admin_Article_Add from '@/components/admin/article/Add'
import Admin_Article_Success from '@/components/admin/article/Success'
import Admin_Works from '@/components/admin/article/Works'
import Admin_Works_Edit from '@/components/admin/article/WorksEdit'
import Admin_Reviews from '@/components/admin/reviews/Index'
import Admin_Recycle from '@/components/admin/recycle/Index'
import Admin_System from '@/components/admin/system/Index'
import Admin_User from '@/components/admin/user/Index'
import Admin_User_Edit from '@/components/admin/user/Edit'
import Admin_User_AddManager from '@/components/admin/user/AddManager'
import Admin_User_Groups from '@/components/admin/user/Groups'
import Admin_User_Role from '@/components/admin/user/Role'
import Admin_Cache from '@/components/admin/cache/Index'
import Admin_About from '@/components/admin/about/Index'
import Admin_Login from '@/components/admin/login/Index'

Vue.use(Router)
export default new Router({
  mode: 'hash', //history
  routes: [
    {
      path: '/',
      name: 'Front',
      redirect: '/index',
      component: Front,
      children: [
        {
          path: 'index',
          name: 'Front_Index',
          component: Front_Index
        }, {
          path: 'article/:id',
          name: 'Front_Article',
          component: Front_Article
        }, {
          path: 'article',
          redirect: 'article/0'
        }, {
          path: 'article/detail/:id',
          name: 'Front_Article_Detail',
          component: Front_Article_Detail
        }, {
          path: 'picture',
          name: 'Front_Picture',
          component: Front_Picture
        }, {
          path: 'contact',
          name: 'Front_Contact',
          component: Front_Contact
        }, {
          path: 'search/:key',
          name: 'Front_Search',
          component: Front_Search
        }
      ]
    },
    {
      path: '/close',
      name: 'Front_Close',
      component: Front_Close
      
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin,
      children: [
        {
          path: 'column',
          name: 'Admin_Column',
          component: Admin_Column
        },{
          path: 'article/:id',
          name: 'Admin_Article',
          component: Admin_Article
        },{
          path: 'article',
          redirect: 'article/0'
        },{
          path: 'article/add/:id',
          name: 'Admin_Article_Add',
          component: Admin_Article_Add
        },{
          path: 'home',
          name: 'Admin_Article_Home',
          component: Admin_Article_Home
        },{
          path: 'banner/:id',
          name: 'Admin_Article_EditBanner',
          component: Admin_Article_EditBanner
        },{
          path: 'works',
          name: 'Admin_Works',
          component: Admin_Works
        },{
          path: 'works/edit/:id',
          name: 'Admin_Works_Edit',
          component: Admin_Works_Edit
        },{
          path: 'success/:id',
          name: 'Admin_Article_Success',
          component: Admin_Article_Success
        },{
          path: 'reviews',
          name: 'Admin_Reviews',
          component: Admin_Reviews
        },{
          path: 'recycle',
          name: 'Admin_Recycle',
          component: Admin_Recycle
        },{
          path: 'system',
          name: 'Admin_System',
          component: Admin_System  
        },{
          path: 'user',
          name: 'Admin_User',
          component: Admin_User
        },{
          path: 'user/edit/:id',
          name: 'Admin_User_Edit',
          component: Admin_User_Edit
        },{
          path: 'user/add',
          name: 'Admin_User_AddManager',
          component: Admin_User_AddManager
        },{
          path: 'user/role',
          name: 'Admin_User_Groups',
          component: Admin_User_Groups
        },{
          path: 'user/role/:id',
          name: 'Admin_User_Role',
          component: Admin_User_Role
        },{
          path: 'cache',
          name: 'Admin_Cache',
          component: Admin_Cache
        },{
          path: 'about',
          name: 'Admin_About',
          component: Admin_About
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Admin_Login
    }
  ]
})



