<template>
<ul class="catalogList">
	<li v-for="cl in adminCatalog" :key="cl.id">
		<button class="btn btn-default btn-sm" @click.stop.prevent="chooseThisParent({cl,update:true})">{{cl.name}}</button>
		<AdminCatalogList class="children" v-if="cl.children && cl.children.length>0" :adminCatalog="cl.children"></AdminCatalogList>
	</li>
</ul>
</template>
<script>
export default {
	name: 'AdminCatalogList',
	props: ['adminCatalog'],
	components: {},
	data () {
		return {
			msg: 'catalogList'
		}
	},
	created () {},
	mounted () {},
	methods: {
		chooseThisParent(obj) {
			this.BUS.$emit('chooseThisParent', obj)
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.catalogList{position: absolute;z-index: 2;background-color:#fff;border:1px solid #ddd;box-shadow: 5px 5px 5px #ddd;border-radius: 5px;display: flex;flex-direction:column;
	li{display: flex;flex-direction:column;
		button{text-align: left;border-top:1px solid #ddd;}
		button:hover{background-color:#f5f5f5;}
		.children{position: relative;border:none;box-shadow:none;border-radius:none;
			&>li{border-top:none;}
		}
	}
}
.children li{padding-left:2vw;}
</style>


