<template>
<div class="success">
	<section class="articleSuccess">
		<p class="alert alert-success" role="alert">操作成功！</p>
		<button class="btn btn-success" @click="goAdd">添加新文章</button>
		<button class="btn btn-primary edit" @click="goEdit">编辑文章</button>
		<button class="btn btn-primary viewList" @click="goViewList">查看列表</button>
		<router-link class="btn btn-primary" :to="'/article/detail/'+id" target="_blank">查看文章</router-link>
	</section>
	
</div>
</template>
<script>
export default {
	name: 'success',
	components: {},
	data () {
		return {
			id: ''
		}
	},
	created (){
		this.id = this.$route.params.id
	},
	mounted() {},
	methods: {
		goAdd() {
			this.$router.push('/admin/article/add/0')
		},
		goEdit() {
			this.$router.push('/admin/article/add/'+this.id)
		},
		goViewList() {
			this.$router.push('/admin/article/0')
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.success{
	.articleSuccess{
		.edit{margin:0 20px;}
		.viewList{margin-right:20px;}
	}
}
</style>


