<template>
<div class="cache">
	{{msg}}
</div>
</template>
<script>
export default {
	name: 'Admin_Cache',
	components: {},
	data () {
		return {
			msg: '更新缓存'
		}
	},
	created (){},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {},
	methods: {},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.cache{}
</style>


