<template>
  <div class="front" :class="this.showMenu">

    <front-top v-if="lachewang && lachewang.catalogs && lachewang.catalogs.length>0" :menu="lachewang.catalogs"></front-top>

    <front-index-banner v-if="routerName==='Front_Index'" :list="lachewang.homeBanner"></front-index-banner>

    <div class="container" :style="mainStyle">
        <div class="row">
          <keep-alive>
            <router-view/>
          </keep-alive>
        </div>
    </div>

    <div class="foot">
      <div class="container">
        <friend-link v-if="lachewang && lachewang.friendlyLink" :flink="lachewang.friendlyLink"></friend-link>

        <copy-right v-if="lachewang && lachewang.system" :systemConfig="lachewang.system" :isLogin="lachewang.isLogin"></copy-right>
      </div>  
    </div>

    <transition mode="out-in" appear
      appear-active-class="animated fadeInUp"
      enter-active-class="animated fadeInUp"
      leave-active-class="animated fadeOutDown">
      <back-top v-if="showBackTop"></back-top>
    </transition>
  </div>
</template>
<script>
import FrontTop from './common/Top'
import BackTop from './common/BackTop'
import FriendLink from './common/FriendLink'
import CopyRight from './common/CopyRight'
import FrontIndexBanner from './index/banner'
import { mapState } from 'vuex'
export default {
  name: 'Front',
  components: { FrontTop, FriendLink, CopyRight, BackTop, FrontIndexBanner },
  data() {
    return {
      routerName: '',
      showBackTop: false,
      lachewang:{
        catalogs: [],
        homeBanner: [],
        friendlyLink: [],
        system: {
          display: true
        }
      },
      mainStyle:{}
    }
  },
  activated() {
    this.routerName = this.$route.name

    let that = this
    setTimeout(() => {
      that.init()
    }, 1000)

  },
  created() {
    this.routerName = this.$route.name
  },
  mounted () {
    let that = this
    let dh = window.document.body.offsetHeight
    this.mainStyle = { minHeight: dh - 180 + 'px' }

    //滚动条下拉时显示返回顶部
    window.onscroll = function() {
      if(window.scrollY > 100) {
        if(!that.showBackTop) {
          that.showBackTop = true
        }
      } else {
        that.showBackTop = false
      }
    }
  },
  methods: {
    //初始化
    init() {
      if(localStorage.lachewang) {
          this.lachewang = JSON.parse(localStorage.lachewang)
          if(this.lachewang.system) {
            if(!this.lachewang.system.display) {
              this.$router.push({name: 'Front_Close'})
            }
          } else {
            this.getSystem()
          }
          if(!this.lachewang.homeBanner) {
            this.getBanner()
          }
          if(!this.lachewang.friendlyLink) {
            this.getFlink()
          }
          if(!this.lachewang.catalogs) {
            this.getCatalogs()
          }
      } else {
        this.lachewang.addCatalogDialog = false
        this.lachewang.isLogin = false
        this.getSystem()
        this.getFlink()
        this.getCatalogs()
        this.getBanner()
      }
      localStorage.lachewang = JSON.stringify(this.lachewang)
    },
    //获取首页banner
    getBanner() { 
        let that = this
        this.$axios({
        method: 'GET',
        url: '/mcm/api/banner?filter=' + encodeURIComponent('{ "fields": {"createdAt": false, "updatedAt": false}, "where": {"display": true}}')
      }).then(res => {
        if(res && res.statusText == 'OK') {
          if(res.data && res.data.length>0) {
            that.lachewang.homeBanner = res.data
            that.dispatch('setHomeBanners', res.data)
          } else {
            //that.lachewang.homeBanner = []
          }
        } else {
          //that.lachewang.homeBanner = []
        }
      }).catch(err => {
        //that.lachewang.homeBanner = []
        console.log(err)
      })
    },
    //获取系统配置信息
      getSystem() { 
        let that = this
        this.$axios({
        method: 'GET',
        url: '/mcm/api/system?filter=' + encodeURIComponent('{ "fields": {"createdAt": false, "updatedAt": false}}')
      }).then(res => {
        if(res && res.data && res.data.length>0) {
          this.lachewang.system = res.data[0]
        }
      }).catch(err => {
        console.log(err)
      })
      },
    //获取栏目
    getCatalogs() { 
        let that = this
        this.$axios({
        method: 'GET',
        url: '/mcm/api/catalogs?filter=' + encodeURIComponent('{ "fields": {"sort": false, "createdAt": false, "updatedAt": false}}')
      }).then(res => {
        if(res && res.data && res.data.length>0) {
          that.lachewang.catalogs = res.data
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //获取友链
    getFlink() { 
      let that = this
      this.$axios({
        method: 'GET',
        url: '/mcm/api/flink?filter=' + encodeURIComponent('{"fields": {"createdAt": false, "updatedAt": false}, "where": {"display": true}}')
      }).then(res => {
        if(res && res.data && res.data.length>0) {
          that.lachewang.friendlyLink = res.data
        } else { 
          this.flink = [] 
        }
      }).catch(err => {
        console.log(err)
      })
    }
  },
  watch: {
    $route() {
      this.routerName=this.$route.name
    },
    routerName() {
      this.routerName = this.routerName
    }
  },
  computed: {
     ...mapState(['showMenu'])
  }
}
</script>
<style scoped lang="less">
.front{-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;width:100%;height: 100%;transition: all 0.3s;
  .container{width:100%;}
  .row{height: 100%;}
  &.showMenu{margin-left:-50%;}
  .mint-popup{width:100%;height:60px;line-height:60px;background-color: rgba(0,0,0,0.5);color:#fff;text-align: center;display: flex;align-items: center;justify-content: center;
    button{position: absolute;right:20px;color:#fff;}
  }
  .foot{width:100%;padding: 30px 0;color:#777;box-sizing: border-box;}
}
</style>


