<template>
<div class="recycle card" :class="(!list || list.length==0) ? 'noborder' : ''">
	<header class="card-header" v-if="list.length>0">
		<h4>回收站</h4>
		<!--button class="btn btn-success btn-sm" @click="reCallList">批量还原</button>
		<button class="btn btn-danger btn-sm" @click="batch('del')">批量删除</button>
		<button class="btn btn-danger btn-sm" @click="batchDeleteAll">清空回收站</button-->
	</header>
	<ul class="list card-body" ref="list" v-if="list.length>0">
		<li v-for="l in list" :key="l.id">
			<div class="con">
				<button class="btn btn-default choose" @click="chooseThis($event, l)">
					<i class="iconfont icon-fangxingweixuanzhong"></i>
				</button>
				<button class="btn title" @click="toArticleDetail(l.id)" title="查看详情">{{l.title}}</button>
			</div>
			<div class="set">
				<button class="btn" title="还原" @click="reCallArticle(l.id)">
					<i class="iconfont icon-skip"></i>
				</button>
				<button class="btn" title="删除" @click="deleteArticle(l.id)">
					<i class="iconfont icon-shanchu1"></i>
				</button>
			</div>
		</li>
	</ul>
	<article class="empty" v-else><img :src="empty" alt=""><span>Empty</span><i>空空如也~</i></article>
	<footer v-if="pages.column>1" class="card-footer">
		<button class="btn btn-default" @click.stop.prevent='toFirst' :class="pages.current>1 ? '':'disabled'">首页</button>
		<button class="btn btn-default" @click.stop.prevent='toPrev' :class="pages.current>1 ? '':'disabled'">上一页</button>
		<button class="btn" 
			v-for="p in getPages()" 
			:key="p" 
			:class="p===pages.current ? 'btn-primary' : 'btn-default'"
			@click.stop.prevent='toThisPage(p)'
		>{{p}}</button>
		<button class="btn btn-default" @click.stop.prevent='toNext' :class="pages.current<pages.column ? '':'disabled'">下一页</button>
		<button class="btn btn-default" @click.stop.prevent='toLast' :class="pages.current<pages.column ? '':'disabled'">末页</button>
	</footer>
</div>
</template>
<script>
import empty from '../../../assets/empty.jpg'
import { Toast, Indicator, MessageBox } from 'mint-ui'
export default {
	name: 'Admin_Recycle',
	components: {},
	data () {
		return {
			empty,
			list: [],
			lachewang: {},
			pages: {
				//当前页码
				current: 1,
				//总列表数
				count: 0,
				//每页列表数
				size: 15,
				//总页数
				column: 0,
			},
			ids: []
		}
	},
	created () {},
	activated() {
		this.init()
		this.initPages()
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {
		setTimeout(()=>{
			this.setListHeight()
		}, 500)
	},
	methods: {
		init() {
			this.getArticleList()
		},
		initPages() {
			this.pages.count = this.list.length;
			if(this.pages.count > 0) {
				if(this.pages.count%15 == 0) {
					this.pages.column = this.pages.count/15
				} else {
					this.pages.column = Math.floor(this.pages.count/15) + 1
				}
			} else {
				this.pages.current = 1
				this.pages.count = 0
				this.pages.size = 15
				this.pages.column = 0
			}
		},
		//获取文章列表
		getArticleList() {
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			let that = this
			let limit = this.pages.size
			let skips = (this.pages.current-1) * limit
			let params = encodeURIComponent('{"where":{"delete":true},"skip":'+skips+',"limit":'+limit+'}')
			this.$axios({
				method: 'GET',
				url: '/mcm/api/articles?filter='+params
			}).then(res => {
				Indicator.close();
				if(res && res.status === 200 && res.statusText === "OK") {
					if(res.data && res.data.length>0) {
						that.list = res.data
					} else {
						that.list = []
					}
				} else {
					that.list = []
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				that.list = []
				console.log(err)
			})
		},
		//切换选中或取消选择
		chooseThis(thisDom, obj) {
			this.switchStyle(thisDom, {
				t: 'choose',
				y: 'icon-fangxingxuanzhongfill',
				n: 'icon-fangxingweixuanzhong',
				obj
			}).then(res => {
				if(res && res.status === 200 && res.statusText === "OK") {
					that.list = res.data
				} else {
					console.log(res)
				}
			}).catch(err => {
				console.log(err)
			})
		},
		switchStyle(thisDom,obj) {
			let iDom, cn;
			if(thisDom.target.nodeName === 'I'){
				iDom = thisDom.target
			}else if(thisDom.target.nodeName === 'BUTTON') {
				if(thisDom.target.children) {
					iDom = thisDom.target.children[0]
				}
			}
			cn = iDom.className
			if(cn.indexOf(obj.n)!=-1){
				iDom.className = 'iconfont '+obj.y
				if(obj.t === 'choose') {
					iDom.parentNode.parentNode.parentNode.className = 'active'
					//当选中时，收集当前ID
					if(this.ids.length==0) {
						this.ids.push(obj.obj.id)
					}else if(this.ids.length>0) {
						let idss = this.ids.join()
						if(idss.indexOf(obj.obj.id)==-1){
							this.ids.push(obj.obj.id)
						}
					}
				} else if(obj.t === 'show') {
					this.setArticleDisabled(obj.obj.id, false)
				}
			} else {
				iDom.className = 'iconfont '+obj.n
				if(obj.t === 'choose') {
					iDom.parentNode.parentNode.parentNode.className = ''
					//当取消选择时删掉指定ID
					if(this.ids.length==1 && this.ids[0] === obj.obj.id) {
						this.ids = []
					}else if(this.ids.length>1) {
						let idss = this.ids.join()
						if(idss.indexOf(obj.obj.id)!=-1){
							this.ids = this.ids.filter(i => i != obj.obj.id)
						}
					}
				} else if(obj.t === 'show') {
					this.setArticleDisabled(obj.obj.id, true)
				}
			}
		},
		//去查看详情
		toArticleDetail(id) {

		},
		//还原 单个
		reCallArticle(id) {
			Indicator.open({text: '还原中...',spinnerType: 'fading-circle'});
			let that = this
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/articles/'+id,
				data: {
					'delete': false
				}
			}).then(res=>{
				Indicator.close();
				if(res && res.statusText=="OK") {
					Toast({message: '还原成功', duration: 1500});
					that.getArticleList()
				} else {
					Toast({message: '还原失败', duration: 1500});
					console.log(res)
				}
			}).catch(err=>{
				Indicator.close();
				Toast({message: '还原失败', duration: 1500});
				console.log(err)
			})
		},
		setArticleDisabled(id,t){
			this.list.forEach(i => {
				if(i.id===id) i.enable = t
			})
		},
		toFirst() {
			this.pages.current = 1
			this.currentPages()
		},
		toPrev() {
			if(this.pages.current>1){
				this.pages.current = this.pages.current-1
				this.currentPages()
			}
		},
		toNext() {
			if(this.pages.current<this.pages.column){
				this.pages.current = this.pages.current+1
				this.currentPages()
			}
		},
		toLast() {
			this.pages.current = this.pages.column
			this.currentPages()
		},
		toThisPage(p){
			if(p!==this.pages.current){
				this.pages.current = p
				this.currentPages()
			}
		},
		//显示页码
		getPages() {
			let arr = []
			let p = this.pages
			if(p.column>0) {
				for(let i=1; i<=p.column; i++) {
					arr.push(i)
				}
			}
			return arr
		},
		//设置列表框架的高
		setListHeight() {
			let list = this.$refs.list
			if(list.children && list.children.length>0) {
				let lis = list.children
				if(lis.length>0) {
					let li = lis[0]
					let h = li.offsetHeight || li.clientHeight || li.scrollHeight
					list.style.minHeight = h * this.pages.size + 'px'
				}
			}
		},
		//删除指定文章
		deleteArticle(id) {
			if(id) {
				MessageBox.confirm('确定删除?').then(action => {
					if(action === 'confirm') {
						if(this.list.length==1) {
							let l = this.list[0]
							if(l.id === id) { 
								this.list = []
							} else {
								console.log('剩最后一条，还和当前执行的不是同一个ID，见了鬼了')
							}
						} else if(this.list.length > 1) {
							this.list = this.list.filter(i => i.id != id)
						}
						this.deleteSuccess()
						this.updatePages()
					} else {
						console.log(action)
					}
				}).catch(err => {
					if(err === 'cancel') {
						console.log('取消删除')
					} else {
						console.log(err)
					}
				})
			} else {
				console.log('未知id')
			}
		},
		//批量操作
		batch(t) {
			if(this.ids.length>0) {
				switch(t) {
					case 'del': this.batchDelete(); break;
					//case 'delAll': this.batchDeleteAll(); break;
				}
			} else {
				Toast({message: '请选择', duration: 1500});
			}
		},
		//批量删除
		batchDelete() {
			MessageBox.confirm('确定删除?').then(action => {
				if(action === 'confirm') {
					let arr = []
					let idss = this.ids.join()
					if(this.list.length>1){
						this.list.forEach(l => {
							if(idss.indexOf(l.id) == -1) {
								arr.push(l)
							}
						})
						this.list = arr
					} else if(this.list.length===1) {
						let l = this.list[0]
						if(l.id === idss) {
							this.list = []
						}
					}
					this.deleteSuccess()
					this.updatePages()
				} else {
					console.log(action)
				}
			}).catch(err => {
				if(err === 'cancel') {
					console.log('取消删除')
				} else {
					console.log(err)
				}
			})
		},
		//清空回收站
		batchDeleteAll() {
			MessageBox.confirm('确定清空吗?').then(action => {
				if(action === 'confirm') {
					this.list = []
					this.deleteSuccess()
					this.updatePages()
				} else {
					console.log(action)
				}
			}).catch(err => {
				if(err === 'cancel') {
					console.log('取消清空')
				} else {
					console.log(err)
				}
			})
		},
		//重新排列底部页码，及当前页码所在位置，以及重新显示当前页码内的文章列表
		updatePages() {
			this.init()
			//删除成功之后，根据剩余文章列表数，重新排页码
			this.initPages()
			//若删除操作时，当前页码数大于重新排列的总页码数，说明删除之后，页码数减少了，需将当前页码数往前移到最后一页；若不大于重新排列的总页码数，则保持不变
			this.pages.column>0 ? this.pages.current>this.pages.column ? this.pages.current=this.pages.column : this.pages.current=this.pages.current : this.pages.current=1
			//根据当前页码，重新请求当前页码内的文章列表
			this.currentPages()
			//显示底部重新排好的页码
			this.getPages()
		},
		//删除成功
		deleteSuccess() {
			this.ids = []
			Toast({
			  	message: '删除成功',
			  	iconClass: 'iconfont icon-success1',
			  	duration: 1200
			})
			localStorage.lachewang = JSON.stringify(this.lachewang)
		},
		//批量还原
		reCallList() {
			if(this.ids.length>0) {
				
			} else {
				Toast({message: '请选择', duration: 1500});
			}	
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.recycle{
	&.noborder{border:none;}
	header{width:100%;display: flex;align-items: center;
		button{margin-right: 10px;}
	}
	.empty{min-height: 750px; background-color:#f6f6f6;display: flex;justify-content:center;align-items: center;flex-direction: column;
		img{height:300px;width: auto;display:block;}
		span{display:block;text-align: center;margin-top:-150px;left:-10px;position: relative;color:#ccc;}
		i{display:block;font-style: normal;font-size: 12px;color:#ccc;left:-8px;position: relative;margin-top:83px;
    	}
	}
	.icon-fangxingweixuanzhong,.icon-fangxingxuanzhongfill{font-size: 20px;position: relative;top:3px;}
	.yan i{color:#666;}
	.icon-fangxingxuanzhongfill{color:#007bff;}
	.icon-shanchu1,.icon-fangxingweixuanzhong{color:#999;}
	.list{padding-top:10px;
		li{width:100%;display: flex;justify-content: space-between;align-items: center;border-bottom: 1px solid #ddd;transition: all 0.3s;
			&:last-child{border-bottom:0;}
			.con{flex:1;display: flex;}
			button{transition: all 0.3s;}
			button,button:active,button:hover{outline: none;box-shadow: none;}
			.title{flex-wrap: wrap;text-align: left;flex:1;color:#000;transition: all 0.3s;font-size: 14px;}
			.title.disabled{color:#999;}
			&:hover,&.active{background-color: #f5f5f5;
				.title{color:#4fc08d;}
			}
		}
	}
	footer{width:100%;display: flex;justify-content: flex-end;align-items: center;padding-top:20px;
		button{border:1px solid #ddd;margin-right:10px;font-size: 14px;}
	}
}
</style>


