export default {
	enableLogin (ctx, status) {
		ctx.commit('enableLogin', status)
	},
	updateSpreadFlag(ctx, status) {
		ctx.commit('updateSpreadFlag', status)
	},
	setHomeBanners(ctx, homeBanner) {
		ctx.commit('setHomeBanners', homeBanner)
	}
}


