<template>
<div class="home card">
	<mt-navbar v-model="selected" class="card-header">
		<div>
		  	<mt-tab-item id="1">Banner</mt-tab-item>
		  	<mt-tab-item id="2">Curtain</mt-tab-item>
		  	<mt-tab-item id="3">Works</mt-tab-item>
	  	</div>
	  	<button class="btn btn-success" @click="add" title="添加"><i class="iconfont icon-jiahao1"></i></button>
	</mt-navbar>
	<mt-tab-container v-model="selected" class="card-body">
	  	<mt-tab-container-item id="1"> 
	  		<banner-list></banner-list> 
	  	</mt-tab-container-item>
	  	<mt-tab-container-item id="2">
	  		<section class="curtainSection">
	  			<vue-ueditor-wrap 
				ref="ueditor" 
				v-model.trim="curtainContent" 
				:config="ueditorConfig" 
				:destroy="true" 
				:init="ueditorInit" 
				@ready="ueditorReady"
			></vue-ueditor-wrap><br/>
				<button class="btn btn-success btn-block" @click="saveCurtain">保存</button>
	  		</section>
	  	</mt-tab-container-item>
	  	<mt-tab-container-item id="3">
	  		<works></works>
	  	</mt-tab-container-item>
	</mt-tab-container>
	<footer class="card-footer">{{footerTip}}</footer>
</div>
</template>
<script>
import Vue from 'vue'
import { Navbar, TabItem, Toast, Indicator, MessageBox } from 'mint-ui';
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
import works from './Works'
import BannerList from './BannerList'
import VueUeditorWrap from 'vue-ueditor-wrap'
export default {
	name: 'AdminArticleHome',
	components: { works, BannerList, VueUeditorWrap },
	data () {
		return {
			selected: "1",
			footerTip: '',
			bannerTip: '',
			worksTip: '',
			curtainTip: '',
			curtainId: '',
			curtainContent: '',
			ueditorConfig: {
				// 如果需要上传功能,找后端小伙伴要服务器接口地址
	            // serverUrl: 'http://api.demo.com/ueditor/upload',
	            // 你的UEditor资源存放的路径,相对于打包后的index.html
	            //UEDITOR_HOME_URL: '/static/ueditor/',
	            UEDITOR_HOME_URL: 'http://www.vlon.cn/common/plugin/ueditor/',
	            // 编辑器不自动被内容撑高
	            autoHeightEnabled: false,
	            // 初始容器高度
	            initialFrameHeight: 450,
	            // 初始容器宽度
	            initialFrameWidth: '100%',
	            // 关闭自动保存
	            enableAutoSave: false
			}
		}
	},
	activated() {
		this.footerTip = this.bannerTip;
		this.changeFooterTip()
		this.getCurtain()
	},
	created () {},
	mounted () {
		let that = this
		that.BUS.$on("worksTipToWorks", len => {
			that.worksTip = `共${len}个作品`
		})
		that.BUS.$on("bannerTipToBanner", len => {
			that.bannerTip = `共${len}个Banner`
		})
		//this.changeFooterTip()
	},
	methods: {
		//获取首页大幕中内容
		getCurtain() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/homeCurtain?filter='+encodeURIComponent('{"where":{},"skip":0,"limit":20}')
			}).then(res => {
				Indicator.close();
				if(res && res.statusText == 'OK') {
					if(res.data && res.data.length>0) {
						that.curtainContent = res.data[0].content
						that.curtainId = res.data[0].id
					}
				} else {
					Toast({message: '查询失败', duration: 1500})
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '查询失败', duration: 1500})
				Indicator.close();
				console.log(err)
			})
		},
		//保存首页大幕中内容
		saveCurtain() {
			let type = 'POST';
			let params = ''
			if(this.curtainId) { //5d79e1542ce430fd39baf382
				type = 'PUT'
				params = '/'+this.curtainId
			}
			Indicator.open({text: '保存中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: type,
				url: '/mcm/api/homeCurtain'+params,
				data: {
					'content': this.curtainContent
				}
			}).then(res => {
				Indicator.close();
				if(res && res.statusText == 'OK') {
					Toast({message: '保存成功', iconClass: 'iconfont icon-success1',duration: 1500})
				} else {
					Toast({message: '保存失败', duration: 1500})
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '保存失败', duration: 1500})
				Indicator.close();
				console.log(err)
			})
		},
		//点击添加进行跳转
		add() {
			switch(this.selected) {
				case "3": this.$router.push({name: 'Admin_Works_Edit', params: {id: 0}}); break;
				case "2": this.footerTip = this.curtainTip; break;
				case "1": this.$router.push({name: 'Admin_Article_EditBanner', params: {id: 0}}); break;
			}
		},
		//改变底部提示
		changeFooterTip() {
			switch(this.selected) {
				case "3": this.footerTip = this.worksTip; break;
				case "2": this.footerTip = this.curtainTip; break;
				case "1": this.footerTip = this.bannerTip; break;
			}
		},
		//获取UEditor实例
		ueditorReady(editorInstance) {
			//console.log(`编辑器实例${editorInstance.key}: `, editorInstance)
		},
		//通过传入 init 参数来对 window.UE 进行二次开发，如添加自定义按钮等。init 函数将在 scripts 加载完毕、editor 初始化之前运行。
		ueditorInit() {
			//window.UE.registerUI(/* ... */)
			//结合 init 参数以及本组件提供的 registerButton 方法，方便地添加自定义按钮：
			//参数： name 按钮名称；icon 按钮图标；tip 按钮 hover 时显示的简介；handler 以 editor 和 name 为入参的按钮动作函数。
			/*this.$refs.ueditor.registerButton({
                name: 'test',
                icon: '/static/test-button.png',
                tip: 'this is a test tip',
                handler: (editor, name) => {
                        editor.execCommand('inserthtml', `<span>text inserted by test button</span>`)
                }
            })*/
		}
	},
	watch: {
		selected() {
			this.changeFooterTip()
		}
	},
	computed: {}
}
</script>
<style scoped lang="less">
.home{width:100%;height: 100%;
	.mint-navbar{display:flex;justify-content:space-between;
		div{display:flex;
			.mint-tab-item{flex:inherit;width:100px;transition: all 0.3s;
				&:hover{cursor: pointer;color: #26a2ff;}
				&.is-selected{background-color: #373277;color:#fff;border-bottom: 0;}
			}
		}
	}
	.card-footer{display:flex;justify-content:flex-end;font-size: .9rem;color:#666;}
}
</style>


