<template>
<div class="upload">
    <span class="tip">{{tip}}</span>
	<i class="iconfont icon-ai35"></i>
	<div :id="id" :title="tip"></div>
</div>
</template>
<script>
import { Toast, Indicator } from 'mint-ui'
let now = Date.now();	
import SHA1 from '../../../assets/SHA1.js';
export default {
	name: 'VueUpload',
	components: {},
	props: {
        tip: {
            type: String,
            default: '上传缩略图'
        },
        accept: {
            type: String,
            default: 'image'
        },
        // 上传地址
        url: {
            type: String,
            default: 'http://d.apicloud.com/mcm/api/file'
        },
        // 上传最大数量 默认为1
        fileNumLimit: {
            type: Number,
            default: 1
        },
        // 大小限制 默认5M
        fileSingleSizeLimit: {
            type: Number,
            default: 1024*1024*5
        },
        // 上传时传给后端的参数，一般为token，key等
        formData: {
            type: Object,
            default: null
        },
        // 生成formData中文件的key，下面只是个例子，具体哪种形式和后端商议
        keyGenerator: {
            type: Function,
            default(file) {
                const currentTime = new Date().getTime();
                const key = `${currentTime}.${file.name}`;
                return key;
            }
        },
        multiple: {
            type: Boolean,
            default: false
        }
    },
	data () {
		return {
            id: '',
            uploadButton: '',
			uploader: null
		}
	},
	created (){
        this.id = this.getDomId()
        this.uploadButton = '#'+this.id
    },
	mounted () {
        let that = this
		this.initWebUpload();
		this.BUS.$on("doInitForUpload", () => {
            if(document.getElementById(that.id)){
			    document.getElementById(that.id).innerHTML = ""
            }
			this.initWebUpload()
		})
	},
	methods: {
		initWebUpload() {
            let that = this
            this.uploader = WebUploader.create({
                auto: true, // 选完文件后，是否自动上传
                swf: 'http://www.vlon.cn/common/plugin/webuploader/uploader.swf',  // swf文件路径
                server: that.url,  // 文件接收服务端
                pick: {
                    id: that.uploadButton,     // 选择文件的按钮
                    multiple: this.multiple,   // 是否多文件上传 默认false
                    label: ''
                },
                accept: this.getAccept(this.accept),  // 允许选择文件格式。
                threads: 3,
                fileNumLimit: this.fileNumLimit, // 限制上传个数
                fileSingleSizeLimit: this.fileSingleSizeLimit, // 限制单个上传图片的大小
                formData: this.formData,  // 上传所需参数
                chunked: true,          //分片上传
                chunkSize: 1024*1024*5,    //分片大小
                duplicate: true  // 重复上传
            });

            // 当有文件被添加进队列的时候，添加到页面预览
            this.uploader.on('fileQueued', (file) => {
            	/*let arr = document.getElementsByTagName("input")
            	let input = null;
            	if(arr && arr.length>0) {
            		for(let i=0; i<arr.length; i++) {
            			if(arr[i].className == 'webuploader-element-invisible') {
            				input = arr[i]
            				//console.log(input.value)
            			}
            		}
            	}*/
                //this.BUS.$emit('fileChange', file, input)
            });
            //开始上传
            this.uploader.on('uploadStart', (file) => {
            	//console.log(file)
            	Indicator.open({text: '加载中...', spinnerType: 'fading-circle'})
            });

            //上传之前的配置
			this.uploader.on('uploadBeforeSend', (block, data, headers) => {
			    headers["X-APICloud-AppKey"] = SHA1("A6011233851659"+"UZ"+"94501DE2-3E09-1CDF-609F-2663F447DA9A"+"UZ"+now)+"."+now;
			    headers["X-APICloud-AppId"] = "A6011233851659";
			});

            // 文件上传过程中创建进度条实时显示。
            this.uploader.on('uploadProgress', (file, percentage) => {
                //this.BUS.$emit('progress', file, percentage)
            });
            //上传成功
            this.uploader.on('uploadSuccess', (file, response) => {
                this.BUS.$emit('uploadSuccess', file, response, that.tip)
            });
            //上传失败
            this.uploader.on('uploadError', (file, reason) => {
                //this.BUS.$emit('uploadError', file, reason)
            });
            //上传出错
            this.uploader.on('error', (type) => {
                let errorMessage = '';
                if (type === 'F_EXCEED_SIZE') {
                    errorMessage = `文件大小不能超过${this.fileSingleSizeLimit / (1024 * 1000)}M`;
                } else if (type === 'Q_EXCEED_NUM_LIMIT') {
                    errorMessage = '文件上传已达到最大上限数';
                } else {
                    errorMessage = `上传出错！请检查后重新上传！错误代码${type}`;
                }
                //this.BUS.$emit('error', errorMessage);
            });
            //上传动作执行完成，不管成功失败
            this.uploader.on('uploadComplete', (file, response) => {
            	Indicator.close();
                //this.BUS.$emit('complete', file, response);
            });
        },
        upload(file) {
            this.uploader.upload(file);
        },
        stop(file) {
            this.uploader.stop(file);
        },
        // 取消并中断文件上传
        cancelFile(file) {
            this.uploader.cancelFile(file);
        },
        // 在队列中移除文件
        removeFile(file, bool) {
            this.uploader.removeFile(file, bool);
        },
        getAccept(accept) {
            switch (accept) {
                case 'text':
                    return {
                        title: 'Texts',
                        exteensions: 'doc,docx,xls,xlsx,ppt,pptx,pdf,txt',
                        mimeTypes: '.doc,docx,.xls,.xlsx,.ppt,.pptx,.pdf,.txt'
                    };
                    break;
                case 'video':
                    return {
                        title: 'Videos',
                        exteensions: 'mp4',
                        mimeTypes: '.mp4'
                    };
                    break;
                case 'image':
                    return {
                        title: 'Images',
                        exteensions: 'gif,jpg,jpeg,bmp,png',
                        mimeTypes: '.gif,.jpg,.jpeg,.bmp,.png'
                    };
                    break;
                default: return accept
            }
        },
        getDomId() {
            return 'filePicker' + Math.floor(Math.random()*10)
        }
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.upload{width:120px;height: 120px;position: relative;display:flex;justify-content:center;align-items:center;overflow:hidden;margin-right: 50px;
    .tip{position: absolute;top:5px;left:0;z-index: 2;width:100%;font-style: normal;text-align: center;font-size: .8rem;color:#666;}
	i{font-size: 5rem;color:#999;position: relative;z-index: 1;}
	&:after{content:"仅支持jpg、jpeg、png、gif、svg格式";font-size: .8rem;color:#666;position: absolute;width:100%;bottom:0;text-align: center;}
}
</style>


