<template>
<div class="navigator nav navbar navbar-default navbar-fixed-top">
	<nav class="wide">
		<div class="list">
			<router-link v-for="n in menu" :key="n.id" :to="'/'+n.enName">
			{{n.name}}</router-link>
		</div>
		<input type="text" class="search form-control" v-model.trim="searchKey" @keyup.enter="doSearch" placeholder="Input Complete Press Enter" />
	</nav>
	<div class="mini">
		<input type="text" class="search" v-model="searchKey" placeholder="Input Complete Press Enter" />
		<button class="btn btn-default bar showNav" @click="showNav"><span></span><span></span><span></span></button>
	</div>
	<div class="menu" :class="this.showMenu">
		<button class="btn" v-for="n in menu" :key="n.id" @click="closeSideBar(n)">{{n.name}}</button>
	</div>
</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
	name: 'FrontNavigator',
	components: {},
	props: ['menu'],
	data () {
		return {
			searchKey:'',
			lachewang: {}
		}
	},
	methods: {
		//显示导航
		showNav() {
			var s = this.showMenu;
			this.showMenu = s == '' ? 'showMenu' : '';
		},
		//(移动端)关闭侧边栏
		closeSideBar (n) {
			this.$router.push('/' + n.enName)
			this.showMenu = ''
		},
		//查询
		doSearch() {
			if(this.searchKey) {
				this.$router.push('/search/'+this.searchKey)
			}
		}
	},
	computed: {
		...mapState(['showMenu'])
	}
}
</script>

<style scoped lang="less">
.navigator{width:100%;padding:10px;box-sizing: border-box;
	background:-webkit-linear-gradient(#f5f5f5,#eee);
	background:-o-linear-gradient(#f5f5f5,#eee);
	background:-moz-linear-gradient(#f5f5f5,#eee);
	background:linear-gradient(#f5f5f5,#eee);
	box-shadow:0px 0px 0px #ddd,0px 0px 0px #ddd,0px 0px 0px #ddd,0px 5px 5px #ddd;
	text-align: left;display: block;
	a{display: inline-block;padding:0 2%;}
	.wide {display: flex;justify-content: space-between;
		.list{display: flex;align-items: center;
			a{padding:0 20px;}
		}
		.search{border-radius:30px;display: inline-block;width:25%;}
	}
	.mini {display: none;justify-content: space-between;align-items: center;
		.search{flex:1;height:40px;line-height:40px;border-radius:30px;outline: none;padding-left: 20px;box-sizing: border-box;border:1px solid #ddd;margin-right: 10px;}
		.bar{height:30px;border:none;background-color:transparent;outline: none;display: flex;justify-content: space-around;align-items: center;flex-direction: column;margin-top:5px;
			span{width:30px;height: 5px;margin:0 0 5px 0;background-color: #ccc;display: block;border-radius: 5px;}
		}
	}	
}
.menu{position: fixed;right:-100%;top:0;z-index: 2;width:50%;height:100%;background-color:rgba(0,0,0,0.7);transition: all 0.3s;display: flex;flex-direction: column;}
.menu.showMenu{right:0;}
.menu button{display: block;line-height: 50px;padding-left: 20px;color:#fff;text-align: left;}
.menu .router-link-exact-active{color:#f90;}
@media only screen and (min-width: 1100px) {
	.navigator{
		a{padding-top:10px;padding-bottom: 10px;}
		.wide{display: flex;}
		.mini{display: none;}
	}
}
@media screen and (max-width: 1100px) and (min-width: 641px) {
	.navigator{
		.wide{display: flex;}
		.mini{display: none;}
	}
}
@media only screen and(max-width: 640px) {
	.navigator{
		.wide{display: none;}
		.mini{display: flex;}
		.menu{padding-top: 3vw;}
		.menu a{font-size: 5vw;}
	}
}
</style>


