<template>
	<div class="search">
		<div class="none">{{msg}}</div>
	</div>
	</template>
	<script>
	export default {
		name: 'search',
		components: {},
		data () {
			return {
				msg: 'Sorry, no found anything.'
			}
		},
		activated() {
			document.title = "搜索"
		},
		created (){},
		mounted () {},
		methods: {},
		watch: {},
		computed: {}
	}
	</script>
	<style scoped lang="less">
	.search{width:100%;
		.none{width:100%;min-height: 700px;display: flex;text-align: center;align-items: center;justify-content: center;}
	}
	</style>	


