<!--div class="works card">
	<header class="card-header">
		<span>作品管理</span>
		<button class="btn btn-success btn-sm" title="添加新作品" @click="add"><i class="iconfont icon-jiahao1"></i></button>
	</header>
	<section class="card-body" v-if="list.length>0"-->
		<ul class="list" style="min-height: 630px;" v-if="list.length>0">
			<li v-for="l in list" :key="l.id">
				<button class="btn btn-default title" :class="l.display?'':'disabled'" @click="updateArticle(l.id)">{{l.title}}</button>	
				<div class="set">
					<button class="btn yan" @click="switchDisplay(l)" title="显示/隐藏">
						<i class="iconfont" :class="l.display ? 'icon-zhengyan' : 'icon-biyan2'"></i>
					</button>
					<button class="btn" title="编辑" @click="updateArticle(l.id)">
						<i class="iconfont icon-pingjia"></i>
					</button>
					<button class="btn" title="删除" @click="deleteArticle(l.id)">
						<i class="iconfont icon-shanchu1"></i>
					</button>
					<button class="btn" title="查看" @click="viewArticle()">
						<i class="iconfont icon-attachment"></i>
					</button>
				</div>
			</li>
		</ul>
	<!--/section>
	<footer class="card-footer" v-if="list.length>0">共 {{list.length}} 个作品</footer>
</div-->
</template>
<script>
import { Toast, Indicator, MessageBox } from 'mint-ui'
export default {
	name: 'works',
	components: {},
	data () {
		return {
			msg: 'works',
			lachewang: {},
			list: []
		}
	},
	activated() {
		this.init()
		if(this.list.length>0) {
			this.BUS.$emit("worksTipToWorks", this.list.length)
		}
	},
	created () {},
	mounted () {},
	methods: {
		init() {
			if(localStorage.lachewang) {
				this.lachewang = JSON.parse(localStorage.lachewang)
				if(this.lachewang.works && this.lachewang.works.length>0) {
					this.list = this.lachewang.works
				} else {
					this.getWorks()
				}
			} else {
				this.getWorks()
			}
		},
		//获得作品列表
		getWorks() {
			let that = this
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			this.$axios({
				method: 'GET',
				url: '/mcm/api/works'
			}).then(res => {
				Indicator.close();
				if(res && res.statusText=="OK") {
					if(res.data && res.data.length>0) {
						that.lachewang.works = res.data
						that.list = that.lachewang.works
						that.BUS.$emit("worksTipToWorks", that.list.length)
						localStorage.lachewang = JSON.stringify(that.lachewang)
					} else {
						that.list = that.lachewang.works = []
						localStorage.lachewang = JSON.stringify(that.lachewang)
						console.log(res)
					}
				} else {
					that.list = that.lachewang.works = []
					localStorage.lachewang = JSON.stringify(that.lachewang)
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				that.list = that.lachewang.works = []
				console.log(err)
			})
		},
		//添加新作品
		add() {
			this.$router.push('/admin/works/edit/0')
		},
		//切换显示隐藏
		switchDisplay(obj) {
			let that = this
			let d = !obj.display
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/works/' + obj.id,
				data: {
					'display': d
				}
			}).then(res => {
				Indicator.close();
				if(res && res.statusText == "OK") {
					Toast({message: '修改成功', iconClass: 'iconfont icon-success1',duration: 1500})	
					setTimeout(() => {
	  					that.getWorks()
					}, 1500);
				} else {
					Toast({message: '修改失败', duration: 1500})
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '修改失败', duration: 1500})
				Indicator.close();
				console.log(err)
			})	
		},
		//编辑
		updateArticle(id) {
			this.$router.push('/admin/works/edit/'+id)
		},
		//删除
		deleteArticle(id) {
			if(id) {
				let that = this
				MessageBox.confirm('确定删除?').then(action => {
					if(action === 'confirm') {
						Indicator.open({text: '删除中...',spinnerType: 'fading-circle'});
						this.$axios({
							method: 'DELETE',
							url: '/mcm/api/works/'+id
						}).then(res => {
							Indicator.close();
							if(res && res.status === 200 && res.statusText === "OK") {
								Toast({message: '删除成功',iconClass: 'iconfont icon-success1', duration: 1500})
								that.getWorks()
							} else {
								Toast({message: '删除失败', duration: 1500})
								console.log(res)
							}
						}).catch(err => {
							Indicator.close();
							Toast({message: '删除失败', duration: 1500})
							console.log(err)
						})
					} else {
						console.log(action)
					}
				}).catch(err => {
					if(err === 'cancel') {
						//console.log('取消删除')
					} else {
						console.log(err)
					}
				})
			} else {
				Toast({message: '未知的作品ID', duration: 2000});
			}
		},
		//查看详情，进入首页
		viewArticle() {
			const { href } = this.$router.resolve({
			    name: "Front_Index"
			})
			window.open(href, '_blank')
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.works{
	.card-header{display:flex;justify-content:space-between;}
	.card-footer{display:flex;justify-content:flex-end;font-size: .9rem;color:#666;}
}
.list{min-height: 650px;
	li{width:100%;display: flex;justify-content: space-between;align-items: center;border-bottom: 1px solid #ddd;transition: all 0.3s;
		.title{padding-left:10px;box-sizing: border-box;}
		&:last-child{border-bottom:0;}
		.con{flex:1;display: flex;}
		button{transition: all 0.3s;}
		button,button:active,button:hover{outline: none;box-shadow: none;}
		.title{flex-wrap: wrap;text-align: left;flex:1;color:#000;transition: all 0.3s;font-size: 14px;}
		.title.disabled{color:#999;}
		&:hover,&.active{background-color: #f5f5f5;
			.title{color:#4fc08d;}
		}
	}
}
</style>


