<template>
<div class="banner" v-if="banners && banners.length>0">
	<button class="btn btn-default prev" @click="prev"><i class="iconfont icon-arrowleft"></i></button>
	<mt-swipe :auto="4000" :speed="500" @change="handleChange" :defaultIndex="index" ref="banner">
	  	<mt-swipe-item v-for="l in banners" :key="l.id">
	  		<a v-if="l.url" :href="l.url" target="_blank">
				<img :src="l.avatar" :alt="l.title" />
			</a>
			<img v-else :src="l.avatar" :alt="l.title" />
	  	</mt-swipe-item>
	</mt-swipe>
	<button class="btn btn-default next" @click="next"><i class="iconfont icon-arrowright"></i></button>
</div>
</template>
<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
import { mapState } from 'vuex'
export default {
	name: 'FrontIndexBanner',
	components: {},
	props: {
		list: {
			type: Array,
			default: []
		}
	},
	data () {
		return {
			index:0,
			banners: []
		}
	},
	activated() {},
	created () {
		if(this.list && this.list.length>0) {
			this.banners = this.list
		} else if(this.homeBanner && this.homeBanner.length>0) {
			this.banners = this.homeBanner
		} else {
			this.getBanner()
		}
	},
	mounted () {
		let that = this
		window.onresize = function(){
			that.setImgContainerHeight()
		}
		that.setImgContainerHeight()
	},
	methods: {
		handleChange(index) {
			this.index = index
		},
		prev() {
			if(this.index == 0) {
				this.index = this.list.length - 1
			}else {
				this.index = this.index - 1
			}
		},
		next() {
			if(this.index == this.list.length - 1) {
				this.index = 0
			} else {
				this.index = this.index + 1
			}
		},
		setImgContainerHeight() {
			let that = this
			if(that.$refs.banner && that.$refs.banner.$el) {
				let img = that.$refs.banner.$el.getElementsByTagName("img")[0]
				let dbw = document.body.offsetWidth
				let iw = 1920
				let ih = 400
				let dbh = 400 * parseFloat(dbw/1920).toFixed(2)
				that.$refs.banner.$el.style.height = dbh + 'px'
			}
		},
		//获取首页banner
	    getBanner() { 
	        let that = this
	        this.$axios({
	        method: 'GET',
	        url: '/mcm/api/banner?filter=' + encodeURIComponent('{ "fields": {"createdAt": false, "updatedAt": false}, "where": {"display": true}}')
	      }).then(res => {
	        if(res && res.statusText == 'OK') {
	          if(res.data && res.data.length>0) {
	          	that.banners = res.data
	          	that.dispatch('setHomeBanners', that.banners)
	            that.lachewang.homeBanner = that.banners
	            localStorage.lachewang = JSON.stringify(that.lachewang)
	          } else {
	            that.homeBanner = []
	            console.log(res)
	          }
	        } else {
	          that.homeBanner = []
	          console.log(res)
	        }
	      }).catch(err => {
	        that.homeBanner = []
	        console.log(err)
	      })
	    }
	},
	watch: {
		list(newValue,oldValue) {
			this.banners = newValue
		}
	},
	computed: {
		...mapState(['homeBanner'])
	}
}
</script>
<style scoped lang="less">
.banner{width:100%;height: auto;max-height: 600px;overflow: hidden;position: relative;
	.mint-swipe{height:400px;}
	a{display:block;}
	img{width:100%;height: auto;display:block;}
	.prev,.next{position: absolute;top:50%;z-index: 2;width:50px;height: 50px;padding:0;outline: none;display:none;justify-content:center;align-items: center;line-height:normal;margin-top:-25px;transition: all 0.3s;}
	.prev i,.next i{font-size: 3rem;color:#f6f6f6;}
	.prev{left:10px;}
	.next{right:10px;}
	/*&:hover{
		.prev,.next{display:flex;}
	}*/
}
</style>


