<template>
	<div class="add">
		<div class="articleTitle mb20">
			<input type="text" v-model.trim="articleInfo.title" class="form-control title" placeholder="title" />
			<input type="text" v-model.trim="articleInfo.author" class="form-control author" placeholder="author: vlon" />
			<input type="text" v-model.trim="articleInfo.views" class="form-control views" placeholder="views 0" />
		</div>
		<div class="arcatt mb20">
			<button class="btn btn-default" v-for="aa in arcatt" :key="aa.id" @click="switchArcatt(aa)">
				<i class="iconfont" :class="aa[aa.att]?'icon-xuanzhong':'icon-xuanze'"></i>{{aa.name}}
			</button>
		</div>
		<div class="catalogSection mb20">
			<div class="catalog">
				<input type="text" class="form-control" v-model.trim="tag.catalog"
					placeholder="on focus to choose catalog" @focus="showColumns=true" @blur="closeColumns"/>
				<i class="iconfont icon-xiangxia1"></i>
				<columns :columns="tag.columns" v-show="showColumns"></columns>
			</div>
		</div>
		<textarea type="text" v-model.trim="articleInfo.desc" class="form-control mb20 desc" placeholder="description"></textarea>
		<div class="tags mb20 pr">
			<input type="text" class="form-control" placeholder="press enter to split tags, max 5" @keyup.enter="jionTagList" v-model.trim="tag.value" @keypress="maxTags" ref="tagDom" />
			<ul v-if="articleInfo.tags && articleInfo.tags.length>0" class="tagGroups" ref="tagGroups">
				<li v-for="(t, index) in articleInfo.tags" :key="index">
					<span>{{t}}</span>
					<button class="btn" @click="delTag(t)"><i class="iconfont icon-guanbi11"></i></button>
				</li>
			</ul>
		</div>
		<div class="group mb20">
			<div class="articleEnable">
				<span>{{articleInfo.display ? 'enable' : 'disabled'}}</span>
				<mt-switch v-model="articleInfo.display"></mt-switch>
			</div>
			<div class="btn-group articleType">type&nbsp;&nbsp;&nbsp;
			  	<button type="button" class="btn btn-info dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">{{articleInfo.type||'原创'}}</button>
			  	<div class="dropdown-menu">
				    <a class="dropdown-item" href="#" @click="chooseArticleType('原创')">原创</a>
				    <div class="dropdown-divider"></div>
				    <a class="dropdown-item" href="#" @click="chooseArticleType('网络')">网络</a>
			  	</div>&nbsp;&nbsp;&nbsp;
			  	<textarea v-if="articleInfo.type==='网络'" type="text" class="form-control articleReferrer" v-model="articleInfo.referrer" rows="2" placeholder="referrer"></textarea>
			</div>
		</div>


		<!--admin-article-ueditor :content="articleInfo.content"></admin-article-ueditor-->
		<div class="ueditor">
			<vue-ueditor-wrap 
				ref="ueditor" 
				v-model.trim="articleInfo.content" 
				:config="ueditorConfig" 
				:destroy="true" 
				:init="ueditorInit" 
				@ready="ueditorReady"
			></vue-ueditor-wrap>
		</div>
		<br/>
		<button class="btn btn-success btn-block btn-lg" @click="saveHandle">Save</button>	
	</div>
</template>
<script>
import Vue from 'vue'
import { Toast, Switch, Checklist, Indicator } from 'mint-ui'
import AdminArticleUeditor from './Ueditor'
Vue.component(Switch.name, Switch)
import Columns from './columns'
import VueUeditorWrap from 'vue-ueditor-wrap'
import { mapState } from 'vuex'
export default {
	name: 'Admin_Article_Add',
	components: { 
		VueUeditorWrap, AdminArticleUeditor, Columns 
	},
	data () {
		return {
			showColumns: false,
			articleInfo: {  
				title: '',
				desc: '',
				catalogId: '',
				tags: [],
				display: true,
				content: '',
				type: '原创',
				referrer: '',
				author: 'vlon',
				views: 0,
				delete:false
			},
			tag: {
				speed: 5,
				value: '',
				catalog: '',
				columns: []
			},
			arcatt: [
				{id: 1, att: 's', name: '滚动', s: false},
				{id: 2, att: 'h', name: '头条', h: false},
				{id: 3, att: 'f', name: '幻灯', f: false},
				{id: 4, att: 'c', name: '推荐', c: false},
				{id: 5, att: 'p', name: '图片', p: false},
				{id: 6, att: 'j', name: '跳转', j: false},
				{id: 7, att: 'a', name: '特荐', a: false},
				{id: 8, att: 'b', name: '加粗', b: false}
			],
			ueditorConfig: {
				// 如果需要上传功能,找后端小伙伴要服务器接口地址
	            // serverUrl: 'http://api.demo.com/ueditor/upload',
	            // 你的UEditor资源存放的路径,相对于打包后的index.html
	            //UEDITOR_HOME_URL: '/static/ueditor/',
	            UEDITOR_HOME_URL: 'http://www.vlon.cn/common/plugin/ueditor/',
	            // 编辑器不自动被内容撑高
	            autoHeightEnabled: false,
	            // 初始容器高度
	            initialFrameHeight: 500,
	            // 初始容器宽度
	            initialFrameWidth: '100%',
	            // 关闭自动保存
	            enableAutoSave: false
			}
		}
	},
	activated() {
		let id = this.$route.params.id
		if(id=='') {
			this.$router.push('/admin/article')
		} else if(id!=0) {
			if(id.toString().length<13) {
				this.$router.push('/admin/article/add/0')
			} else {
				this.getArticleInfoById(id)
				this.getCatalogs()
			}
		} else if(id==0) {
			this.articleInfo.title = ''
			this.articleInfo.desc = ''
			this.tag.catalog = ''
			this.tag.columns = []
			this.tag.value = ''
			this.articleInfo.tags = []
			this.articleInfo.display = true
			this.articleInfo.content = ''
			this.articleInfo.author = 'vlon'
			this.articleInfo.type = '原创'
			this.articleInfo.views = 0
			this.articleInfo.referrer = ''	
			this.showColumns = false
			this.getCatalogs()
		}
	},
	created() {},
	mounted() {
		this.setTagsTextIndent()
		let that = this
		that.BUS.$on('chooseThisCatalog', obj => {
			that.articleInfo.catalogId = obj.id
			that.tag.catalog = obj.name
		})
	},
	methods: {
		//通过文章ID获取文章详细信息
		getArticleInfoById(id) {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/articles/'+id
			}).then(res => {
				if(res && res.statusText === "OK") {
					if(res.data) {
						that.articleInfo = res.data
					} else {
						that.articleInfo = {
							title: '',
							desc: '',
							catalogId: '',
							tags: [],
							display: true,
							content: '',
							type: '原创',
							referrer: '',
							author: 'vlon',
							views: 0,
							delete:false
						}
					}
				} else {
					Toast({message: '文章内容查询失败', duration: 2000});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '文章内容查询失败', duration: 2000});
				console.log(err)
			})
		},
		//根据标签数量及宽度，调整输入标签的样式
		setTagsTextIndent() {
			setTimeout(() => {
				let inputNode = this.$refs.tagDom
				let tagGroupsWidth = 0
				if(this.$refs.tagGroups) {
					tagGroupsWidth = this.$refs.tagGroups.offsetWidth
				}
				let pLeft = parseFloat(tagGroupsWidth/10).toFixed(2)
				if(!this.articleInfo.tags) {
					inputNode.style.paddingLeft = '0.75rem'
				} else {
					inputNode.style.paddingLeft = tagGroupsWidth+5+'px'
				}
			},1000)
		},
		//删除当前标签
		delTag(t) {
			if(this.articleInfo.tags.length==1) {
				this.articleInfo.tags = []
			} else if(this.articleInfo.tags.length>1) {
				this.articleInfo.tags = this.articleInfo.tags.filter(i => i!=t)
			}
			this.setTagsTextIndent()
		},
		//加入tag到列表
		jionTagList() {
			if(this.tag.value) {
				this.articleInfo.tags.push(this.tag.value)
				this.tag.value = ''
				this.setTagsTextIndent()
			}
		},
		//maxTags
		maxTags() {
			if(this.articleInfo.tags.length >= this.tag.speed) {
				this.tag.value = ''
				Toast({message: '最多只能添加5个标签', duration: 1500});
			}
		},
		//获取栏目列表
		getCatalogs() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/catalogs?filter=%7B%22where%22%3A%7B%22display%22%3Atrue%7D%7D'
			}).then(res => {
				if(res && res.data && res.data.length>0) {
					that.tag.columns = that.recombination(res.data)
				} else {
					Toast({message: '抱歉，未查到相关栏目信息', duration: 2000});
					console.log(res)
				}
			}).catch(err => {
				console.log(err)
			})
		},
		//保存
		saveHandle() {
			if(!this.articleInfo.title){Toast({message: '标题不能为空', duration: 1500});return false;}
			if(!this.articleInfo.catalogId){Toast({message: '请选择栏目', duration: 1500});return false;}
			if(!this.articleInfo.content){Toast({message: '内容不能为空', duration: 1500});return false;}
			
			//新增保存
			if(this.$route.params.id==0) {
				this.addSave()
			//编辑保存
			} else {
				this.editSave()
			}
		},
		//新增保存
		addSave() {
			let that = this
			Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: 'POST',
				url: '/mcm/api/articles',
				data: {
					title: this.articleInfo.title,
					desc: this.articleInfo.desc,
					tags: this.articleInfo.tags,
					views: this.articleInfo.views,
					type: this.articleInfo.type,
					author:this.articleInfo.author,
					content: this.articleInfo.content,
					display: this.articleInfo.display,
					catalogId: this.articleInfo.catalogId,
					delete: false,
					referrer: this.articleInfo.referrer
				}
			}).then(res => {
				Indicator.close();
				if(res && res.statusText==="OK") {
					Toast({message: '保存成功', iconClass: 'iconfont icon-success1', duration: 1500});
					setTimeout(()=>{
						that.$router.push('/admin/success/'+res.data.id)
					},1000)
				} else {
					console.log(res)
					Toast({message: '保存失败', duration: 1500});
				}
			}).catch(err => {
				Indicator.close();
				console.log(err)
				Toast({message: '保存失败', duration: 1500});
			})
		},
		//编辑保存
		editSave() {
			let that = this
			Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/articles/'+this.$route.params.id,
				data: {
					title: this.articleInfo.title,
					desc: this.articleInfo.desc,
					catalogId: this.articleInfo.catalogId,
					tags: this.articleInfo.tags,
					display: this.articleInfo.display,
					content: this.articleInfo.content,
					type: this.articleInfo.type,
					author:this.articleInfo.author,
					views: this.articleInfo.views,
					referrer: this.articleInfo.referrer
				}
			}).then(res => {
				Indicator.close();
				if(res.status==200 && res.statusText=="OK") {
					Toast({message: '保存成功', iconClass: 'iconfont icon-success1', duration: 1500});
					setTimeout(()=>{
						that.$router.push('/admin/success/'+res.data.id)
					},1000)
				} else {
					console.log(res)
					Toast({message: '1保存失败', duration: 1500});
				}
			}).catch(err => {
				Indicator.close();
				console.log(err)
				Toast({message: '2保存失败', duration: 1500});
			})
		},
		//获取UEditor实例
		ueditorReady(editorInstance) {
			//console.log(`编辑器实例${editorInstance.key}: `, editorInstance)
		},
		//通过传入 init 参数来对 window.UE 进行二次开发，如添加自定义按钮等。init 函数将在 scripts 加载完毕、editor 初始化之前运行。
		ueditorInit() {
			//window.UE.registerUI(/* ... */)
			//结合 init 参数以及本组件提供的 registerButton 方法，方便地添加自定义按钮：
			//参数： name 按钮名称；icon 按钮图标；tip 按钮 hover 时显示的简介；handler 以 editor 和 name 为入参的按钮动作函数。
			/*this.$refs.ueditor.registerButton({
                name: 'test',
                icon: '/static/test-button.png',
                tip: 'this is a test tip',
                handler: (editor, name) => {
                        editor.execCommand('inserthtml', `<span>text inserted by test button</span>`)
                }
            })*/
		},
		//关闭 栏目弹窗
		closeColumns() {
			setTimeout(()=>{
				this.showColumns=false
			},200)
		},
		//选择文章类型
		chooseArticleType(t) {
			this.articleInfo.type = t
		},
		//推荐  滚动  头条等选项
		switchArcatt(a) {
			this.arcatt.forEach(i=>{
				if(i==a){
					i[i.att] = !i[i.att]
				}
			})
		}
	},
	watch: {},
	computed: {
		...mapState(['recombination'])
	}
}
</script>
<style scoped lang="less">
.add{
	.mb20{margin-bottom: 20px;}
	.pr{position: relative;}
	.desc{resize:none;}
	.articleTitle{display:flex;align-items: center;
		.title,.author{margin-right:10px;}
		.author,.views{width:150px;}
		.views{width:80px;}
	}
	.arcatt{
		i{margin-right: 5px;}
		.icon-xuanzhong{color:#007bff;}
	}
	.group{display:flex;align-items: center;
		.articleEnable{display: flex;align-items: center;margin-right:50px;width:120px;
			.mint-switch{margin-bottom: 0;margin-left: 20px;}
			.mint-switch:hover{cursor: pointer;}
		}
		.articleType{display: flex;align-items: center;flex:1;
			.dropdown-toggle{flex: initial;}
			.articleReferrer{flex:10;resize: none;}
		}
	}
	
	.catalogSection{display: flex;position:relative;z-index:3;
		.catalog{position: relative;z-index: 2;flex:1;margin-right: 10px; 
			i{position: absolute;z-index: 2;top:10px;right: 10px;}
			.columns{position: absolute;z-index: 3;top:40px;left: 0px;background-color:#fff;width:100%;
				border:1px solid #ddd;margin-top:-1px;border-radius: 0 0 5px 5px;box-shadow: 5px 5px 5px #ddd,-5px 5px 5px #ddd;}
		}
	}
	.tags{
		input{transition: all 0.3s;}
		.tagGroups{position:absolute;left:0;top:0;z-index:2;display: flex;width:auto;height: 100%;padding:5px 0;box-sizing: border-box;
			li{display: flex;align-items: center;height:100%;margin:0 0 0 5px;background-color: #e7f2ed;
				span{color: #017e66;padding-left:5px;height:100%;display: flex;align-items: center;font-size: 12px;}
				button{padding:2px;height:100%;display: flex;align-items: center;
					i{color: #017e66;font-size: 14px;height: 17px;}
				}
				button,button:hover,button:active{outline: none;box-shadow: none;}
			}
		}
	}
	.ueditor{position: relative;z-index: 1;}
}
</style>	


