<template>
<div class="about card">
	<section class="card-header">{{msg}}</section>
	<article class="card-body">
		<dl>
			<dt>感谢您访问拉扯网</dt>
			<dd>
				<p>本系统成立于2019年，是前端开发工程师刘威龙自主研发的，个人博客类后台管理系统。</p>
				<p>使用开发框架为VUE2.X，用到了vue-cli + vueRouter + axios + vuex + Mint UI</p>
				<p>具备响应性，会根据您的设备大小进行调整。</p>
				<p>主要提供技术交流，经验分享等网络服务。</p>
			</dd>
		</dl>
		<dl>
			<dt>浏览器要求</dt>
			<dd>
				<p>针对最新版本的 Chrome、Safari 和 Internet Explorer 进行过优化。您的浏览器必须启用 Javascript。</p>
				<p>个人始终认为，国家的强盛需要依靠科技的力量，科技的进步有赖于每一个人的努力探索。</p>
				<p>虽然我们原本陌生，但通过网络认识，通过互相交流便可成为朋友。</p>
				<p>希望大家可以给那些需要帮助的人尽一份力量，同时提高自己的水平。</p>
				<p>不管是什么学历和职位，坚持积极帮助别人，在网络上人人都有机会成为英雄！</p>
			</dd>
		</dl>
	</article>
</div>
</template>
<script>
export default {
	name: 'Admin_About',
	components: {},
	data () {
		return {
			msg: '关于本系统'
		}
	},
	created (){},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {},
	methods: {},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.about{
	.card-body{min-height: 700px;
		dl{margin-bottom: 20px;
			dt{font-size:1.2rem;}
			dd{padding-left:20px;font-size: 1rem;}
		}
	}
}
</style>


