<template>
<div class="update card">
	<div class="card-header">用户管理</div>
	<ul class="card-body">
		<li>
			<div class="left">账号</div>
			<div class="right"><input type="text" class="form-control account" v-model.trim="info.username" maxlength="50"></div>
		</li>
		<li>
			<div class="left">密码</div>
			<div class="right"><input type="password" class="form-control" v-model.trim="password" maxlength="50"></div>
		</li>
		<li>
			<div class="left">确认密码</div>
			<div class="right"><input type="password" class="form-control" v-model.trim="repassword" maxlength="50"></div>
		</li>
		<li>
			<div class="left">选择角色</div>
			<div class="right">
				<select id="groups" class="form-control" @change="selectRole($event)" v-model="currentRoleId">
					<option v-for="r in roles" :key="r.id"
					 :value="r.id">{{r.title}}</option>
				</select>
			</div>
		</li>
		<li>
			<div class="left">状态</div>
			<div class="right"><mt-switch v-model="info.display"></mt-switch></div>
		</li>
	</ul>
	<footer class="card-footer">
		<button class="btn btn-success" @click="save(info.id)">&nbsp;&nbsp;&nbsp;&nbsp;保存&nbsp;&nbsp;&nbsp;&nbsp;</button>
	</footer>
</div>
</template>
<script>
import Vue from 'vue'
import { Switch, Indicator, MessageBox, Toast } from 'mint-ui';
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_User_Update',
	props: ['id'],
	components: {},
	data () {
		return {
			info: {
				id: '',
				roleId: '',
				username: '',
				display: true
			},
			password: '',
			repassword: '',
			currentRoleId: '5d634bc7b23c153e37a5ba46',
			roles: []
		}
	},
	created() {},
	activated() {
		this.init()
	},
	mounted () {},
	methods: {
		init() {
			//如果是增加
			if(this.id==0 && !this.$route.params.id) {
				this.info.id = ''
				this.info.roleId = ''
				this.info.username = ''
				this.info.display = true
				this.currentRoleId = ''
				this.repassword = ''
				this.roles = []
				this.getRole()
			//如果是编辑
			} else if(this.id==undefined && this.$route.params.id) {
				let id = this.$route.params.id
				if(id) {
					this.getUserInfoById(id)
				} else {
					Toast({message: '未获得userId', duration: 1500});
				}
			}
		},
		//通过ID获取用户信息
		getUserInfoById(id) {
			let that = this
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			this.$axios({
				method: 'GET',
				url: '/mcm/api/user/'+id
			}).then(res => {
				Indicator.close();
				if(res && res.statusText=="OK") {
					if(res.data) {
						that.info = res.data
						that.currentRoleId = that.info.roleId
						that.getRole()
					}
				} else {
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				console.log(err)
			})
		},
		//获取角色列表信息
		getRole() {
			let that = this
			Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: 'GET',
				url: '/mcm/api/role?filter={"where":{}}'
			}).then(res => {
				Indicator.close();
				if(res && res.statusText=="OK") {
					if(res.data && res.data.length>0) {
						that.roles = res.data
					} else {
						that.roles = []
					}
				} else {
					that.roles = []
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				that.roles = []
				console.log(err)
			})
		},
		//保存
		save(id) {
			let that = this
			if(!that.info.username) {Toast({message: '账号不能为空', duration: 1500}); return false}
			//若有用户ID说明是编辑保存
			if(that.info.id) {
				//若不输入密码
				if(!that.password && !that.repassword) {
					//执行保存不修改密码
					that.editSave()
				//若输入密码
				} else if(that.password && that.repassword) {
					//若密码相同
					if(that.password==that.repassword) {
						//执行保存修改密码
						that.editSave('p')
					//若密码不相同
					} else {
						Toast({message: '两次密码输入不一致', duration: 1500}); 
					}
				//其它情况
				}else {
					//比如 密码输入不完整
					if((that.password && !that.repassword)||(!that.password && that.repassword)){
						MessageBox('', '若需修改密码，则两次输入需相同；若不修改密码，可清空密码并保存');
					}
				}
			//否则是新增保存
			} else {
				if(!that.password) {Toast({message: '密码不能为空', duration: 1500}); return false}
				if(!that.repassword) {Toast({message: '请输入确认密码', duration: 1500}); return false}
				if(that.password != that.repassword) {Toast({message: '两次密码输入不一致', duration: 1500}); return false}
				that.$axios({
					method: 'POST',
					url: '/mcm/api/user',
					data: {
						username: that.info.username,
						password: that.password,
						display: that.info.display,
						roleId: that.currentRoleId
					}
				}).then(res => {
					if(res && res.statusText == "OK") {
						Toast({message: '保存成功', iconClass: 'iconfont icon-success1', duration: 1500});
						setTimeout(()=>{
							that.$router.push('/admin/user')
						},1500)
					} else {
						Toast({message: '保存失败', duration: 1500});
						console.log(res)
					}
				}).catch(err => {
					Toast({message: '保存失败', duration: 1500});
					console.log(err)
				})
			}
		},
		selectRole(oThis) {
			this.currentRoleId = oThis.target.value
		},
		//编辑保存
		editSave(t) {
			let that = this
			let data = {
				username: this.info.username,
				roleId: this.currentRoleId,
				display: this.info.display
			}
			//保存并修改密码
			if(t && t=='p') {
				data.password = this.password
			}
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/user/'+this.$route.params.id,
				data: data
			}).then(res => {
				if(res && res.statusText=="OK") {
					Toast({message: '保存成功', iconClass: 'iconfont icon-success1', duration: 1500});
					setTimeout(()=>{
						that.$router.push('/admin/user')
					},1500)
				} else {
					Toast({message: '保存失败', duration: 1500});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '保存失败', duration: 1500});
				console.log(err)
			})
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.update{
	.card-body{width:100%;min-height:650px;display: flex;flex-direction: column;padding-top:100px;box-sizing: border-box;
		li{width:100%;display: flex;align-items: center;
			.left{padding:10px 0;display: flex;text-align: right;width:40%;justify-content: flex-end;font-size:1rem;padding-right: 2%;box-sizing: border-box;}
			.right{padding:10px 0;display: flex;flex:1;}
			.btn{margin:0 auto;flex:1;max-width:700px;min-width: 300px;}
			.account{margin-right: 10px;}
			.form-control{width:30%;min-width: 300px;}
		}
	}
	.card-footer{display: flex;justify-content:center;}
}
</style>


