<template>
<div class="pictureIndex">
	
	<div class="pictures">
		<button class="btn btn-default" v-for="(l,index) in list" :key="index" @click="showLarge(index)">
			<img :src="l.icon" alt="">
		</button>
	</div>
	
	<transition name="fade">
		<div class="large" v-show="show">
			<button class="prev btn btn-default iconfont icon-arrowleft" @click="prev" @mousedown="mousedown" v-show="showPrev"></button>
			<div class="content">
				<swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="sliderEvent">
					<swiper-slide v-for="(slide, index) in list" :key="index">
						<img :src="slide.icon" alt="" ref="slideImg" />
						<transition name="animated fadeInUp delay-1s out-in">
							<p v-show="showDesc" v-if="slide.title!=''">{{slide.title}}</p>
						</transition>
					</swiper-slide>
				</swiper>
			</div>
			<button class="next btn btn-default iconfont icon-arrowright" @click="next" @mousedown="mousedown" v-show="showNext"></button>
			<button class="close btn btn-default iconfont icon-close" @click="close"></button>
			<span class="tabItems"><i>{{activeIndex+1}}</i>/{{list.length}}</span>
		</div>
	</transition>

</div>
</template>

<script>
import pictures from '../../../../static/db/picture.json'
import animate from 'animate.css'
export default {
	name: 'pictureIndex',
	components: {},
	data () {
		return {
			pictures,
			showPrev: true,
			showNext: true,
			list: [],
			swiperOption: {
	          	pagination: {
            		el: '.swiper-pagination'
          		},
          		navigation:{
          			nextEl: '.swiper-button-next',
          			prevEl: '.swiper-button-prev'
          		},
          		on:{
          			touchMove:()=>{
          				this.showDesc=false;
          			},
          			slideChangeTransitionEnd:()=>{
	          			this.activeIndex=this.$refs.mySwiper.swiper.activeIndex;
	          			//this.showDescWidth=this.$refs.slideImg[this.activeIndex].width;
	          			this.showDesc=true;
	          			this.enableShowBtn(this.activeIndex);
	          		}
          		}
	        },
	        swiperLength:0,
	        activeIndex:0,
	        show:false,
	        showDesc:false,
	        //showDescWidth:0
		}
	},
	activated() {
		document.title="图片"
	},
	created(){},
	watch: {
		activeIndex(){
			return this.$refs.mySwiper.swiper.activeIndex;
		}
	},
	computed: {
      swiper() {
        return this.$refs.mySwiper.swiper;
      }
    },
    mounted() {
    	this.getData()
    	this.swiperLength=this.$refs.mySwiper.$children.length;
    	this.activeIndex=0;
    	//this.showDescWidth=this.$refs.slideImg[this.activeIndex].width;
      	this.swiper.slideTo(0, 800, false);
    },
	methods:{
		getData () {
	      	/*this.$axios.get('/static/db/picture.json').then(res => {
	        	this.list = res.data;
	      	}).catch(err => {
	        	console.log(err);
	      	})*/
	      	this.list = this.pictures
	    },
		sliderEvent(){
			//console.log(2,this.$refs.mySwiper.$children.length)
		},
		enableShowBtn(i){
			if(i==0) {
  				this.showNext = false
  			} else if(i == (this.list.length - 1)) {
  				this.showPrev = false
  			}else{
  				this.showNext = this.showPrev = true
  			}
		},
		prev() {
			if(this.activeIndex<this.$refs.mySwiper.$children.length-1){
				this.activeIndex++;
				this.swiper.slideTo(this.activeIndex, 800, false);
				this.showDesc = true;
			}else{ 
				this.activeIndex=this.$refs.mySwiper.$children.length-1
			}
			this.enableShowBtn(this.activeIndex);	
		},
		next() {
			if(this.activeIndex>0){
				this.activeIndex--;
				this.swiper.slideTo(this.activeIndex, 800, false);
				this.showDesc = true;
			}else{
				this.activeIndex=0
			}
			this.enableShowBtn(this.activeIndex);	
		},
		close(){
			this.show=false;
		},
		showLarge(i){
			this.showDesc=true;
			this.activeIndex=i;
			this.show=true;
			this.swiper.slideTo(i, 800, false);
		},
		mousedown(){
			this.showDesc=false;
		}
	}
}
</script>

<style scoped lang="less">
/*@import url("../../../../node_modules/animate.css/animate.css");*/
.mint-header.is-fixed{display: none;}
.pictures{width:100%;position: relative;top:30px;}
.pictures button{width:25%;padding:10px;border-radius: 5px;outline: none;border:none;box-shadow: none;}
.pictures button:after{outline: none;border:none;box-shadow: none;}
.pictures button img{width:100%;height: auto;display: block;border:1px solid #ddd;padding:10px;border-radius: 5px;transition: all 0.5s;}
.pictures button:hover img{-moz-transform:scaleX(-1);-webkit-transform:scaleX(-2);-o-transform:scaleX(-1);transform:scaleX(-1);filter:flipH;}

.large{position: fixed;top:0;left: 0;z-index: 100;width: 100%;height: 100%;background-color: #000;overflow:hidden;}
.large .close{position: absolute;top: 0;right: 0;z-index: 2;width:80px;height:80px;line-height: 80px;border-radius: 0px 0px 0px 100%;font-size:2.5em;padding-left:20px; box-sizing: border-box;color:#fff;}
.large .prev,.large .next{position: absolute;top: 50%;z-index: 2;width:80px;height:80px;line-height: 80px;color:#fff;margin-top:-40px;font-size:3em;transition: all 0.3s;}
.large .prev{left: 2%;}
.large .next{right: 2%;}
.large .content{position:absolute;top:10%;left:10%;bottom:10%;right:10%;z-index:2;width:80%;height:80%;}
.large .swiper-container{width:100%;height:100%;overflow:hidden;}
.large .swiper-container .swiper-wrapper{width:100%;height: 100%;}
.large .swiper-container .swiper-wrapper .swiper-slide{width: 100%;height: 100%;}
.large .swiper-container img{display: block;height: 100%;margin:0 auto;}
.large .tabItems{position: absolute;top:10%;right:10%;z-index: 2; color: #fff;display: inline-block;font-family: "Georgia";}
.large .tabItems i{font-size: 2em;display: inline-block;}
.large .content p{font-size: 1em;color: #ccc;position: absolute;left:13%;width: 74%;bottom:0; z-index: 2;line-height: 25px;text-indent: 25px;background-color: rgba(0,0,0,0.2);padding:10px;}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
@media screen and(max-width: 640px){
	.mint-header.is-fixed{display: block;}
	.large .prev,.large .next{display:none;}
	.large .content{left:0;right:0;width:100%;top:50%;bottom:inherit;transform: translate(0,-50%);height: auto;}
	.large .content p{width:100%;left:0;}
	.large .swiper-container img{width:100%;height: auto;}
	.large .tabItems{right:inherit;left:20px;top:10px;}
	.pictures button{width:50%;}
}
</style>


