<template>
<div class="admin">
	<admin-top></admin-top> 
    <div class="main">
		<admin-menu></admin-menu>
        <section class="content col-md-9 ml-sm-auto col-lg-10 pt-3 px-4">
			<keep-alive>
				<article>欢迎使用拉扯网后台管理系统</article>
	        	<router-view></router-view>
	        </keep-alive>
        </section>
    </div>
    <admin-foot></admin-foot> 
</div>
</template>
<script>
import AdminTop from './top/Index'
import AdminFoot from './foot/Index'
import AdminMenu from './menu/Index'
export default {
	name: 'admin',
	components: { AdminTop, AdminFoot, AdminMenu },
	data(){
		return{
			lachewang: {}
		}
	},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	created() {
		if(!localStorage.lachewang){
			this.$router.push({name: 'Login'})
		} else {
			this.lachewang = JSON.parse(localStorage.lachewang)
			if(!this.lachewang.isLogin) {
				this.$router.push({name: 'Login'})
			}
		}
	},
	mounted () {
		document.title = '拉扯网后台管理系统'
	}
}
</script>
<style scoped lang="less">
.admin{width:100%;height: 100%;
	.main{width:100%;min-height:calc(100% - 150px);display: flex;}
	article{height: 100%;display: flex;justify-content: center;align-items: center;}
}
@media screen and (max-width: 640px) {
	.admin{width:100%;height: 100%;display: flex;flex-direction: column;
		.main{width:100%;height:auto;display: flex;flex-direction: column;
			aside.menu{width:100%;height:auto;display: flex;justify-content: space-around;
			}
			section.content{width:100%;height:auto;
				article{min-height: 100vw;align-items: center;}
			}
		}
	}
}
</style>


