<template>
<div class="role card">
	<header class="card-header">角色管理</header>
	<ul class="card-body">
		<li>
			<div class="left">名称</div>
			<div class="right"><input type="text" class="form-control" v-model="info.name"></div>
		</li>
		<li>
			<div class="left">标题</div>
			<div class="right"><input type="text" class="form-control" v-model="info.title"></div>
		</li>
		<li>
			<div class="left">描述</div>
			<div class="right"><input type="text" class="form-control" v-model="info.description"></div>
		</li>
		<li>
			<div class="left">状态</div>
			<div class="right"><mt-switch v-model="info.display"></mt-switch></div>
		</li>
		<li class="permission">
			<div class="left">权限</div>
			<div class="right">
				<div class="content">
					<button class="btn btn-default"
						v-for="(p,index) in permission" 
						:key="p.id"
					 	@click="chooseThis(index)"><i class="iconfont" :class="p.choose?'icon-duoxuan-yixuan':'icon-duoxuan-weixuan1'"></i><span>{{p.name}}</span></button>
				</div>
			</div>
		</li>
	</ul>
	<footer class="card-footer set">
		<button class="btn btn-success" @click="save">&nbsp;&nbsp;保&nbsp;存&nbsp;&nbsp;</button>
	</footer>
</div>
</template>
<script>
import { Switch, Indicator, MessageBox, Toast } from 'mint-ui';
import Vue from 'vue'
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_User_Role',
	components: {},
	data () {
		return {
			msg: '',
			info: {
				name: '',
				title: '',
				desc: '',
				display: true,
				permission: []
			},
			permission: [
				{id: 1, name: '所有权限', choose: false},
				{id: 2, name: '栏目管理', choose: false},	
				{id: 3, name: '文章管理', choose: false},	
				{id: 4, name: '评论管理', choose: false},	
			]
		}
	},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
		let id = this.$route.params.id
		if(id && id!=0) {
			this.msg = "编辑"
			this.info = {
				name: '',
				title: '',
				desc: '',
				display: true,
				permission: []
			}
			this.init()
		} else if(id==0) {
			this.msg = "增加"
		}
	},
	created() {},
	mounted() {},
	methods: {
		//初始化
		init() {
			this.getRoleInfoById()
		},
		//通过角色ID获取角色详细信息
		getRoleInfoById() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/role/' + this.$route.params.id
			}).then(res => {
				if(res && res.statusText == "OK") {
					if(res.data) {
						that.info = res.data
						that.renderPermission()
					}
				} else {
					console.log(res)
				}
			}).catch(err => {
				Toast('角色信息查询失败');
				console.log(err)
			})
		},
		//渲染 角色权限的选中与否
		renderPermission() {
			let that = this
			let p = this.info.permission
			if(p.length>0) {
				p.forEach(a => {
					if(a === "所有权限") {
						that.permission.forEach(i=>{
							i.choose = true
						})
					} else {
						that.permission.forEach(i => {
							if(a === i.name) {
								i.choose = true
							}
						})
					}	
				})
			} else {
				this.permission.forEach(i=>{
					i.choose = false
				})
			}
		},
		//选择/取消选择
		chooseThis(i) {
			this.permission[i].choose = !this.permission[i].choose
			//选中
			if(this.permission[i].choose && this.permission[i].name == "所有权限") {
				//所有权限
				this.info.permission = []
				this.permission.forEach(i => {
					this.info.permission.push(i.name)
				})
			//取消选中, 或选中其它权限
			} else {
				this.info.permission = []
				this.permission.forEach(i => {
					if(i.choose) {
						this.info.permission.push(i.name)
					}
				})	
			}
			if(this.info.permission.join(",").indexOf("所有权限")!=-1) {
				this.info.permission = ['所有权限']
			}
		},
		//保存
		save() {
			let that = this
			if(this.$route.params.id==0) {
				this.$axios({
					method: 'POST',
					url: '/mcm/api/role',
					data: {
						"name": this.info.name,
						"title": this.info.title,
						"description": this.info.description,
						"display": this.info.display,
						"permission": this.info.permission
					}
				}).then(res => {
					if(res && res.statusText == "OK") {
						Toast({message: '添加成功', iconClass: 'iconfont icon-success1', duration: 1500});
						setTimeout(()=>{
							that.$router.push('/admin/user/role')
							that.info = {
								name: '',
								title: '',
								desc: '',
								display: true,
								permission: []
							}
						}, 1500)
					} else {
						console.log(res)
					}
				}).catch(err => {
					console.log(err)
				})
			} else {
				this.$axios({
					method: 'PUT',
					url: '/mcm/api/role/' + this.$route.params.id,
					data: {
						"name": this.info.name,
						"title": this.info.title,
						"description": this.info.description,
						"display": this.info.display,
						"permission": this.info.permission
					}
				}).then(res => {
					if(res && res.statusText == "OK") {
						Toast({message: '修改成功', iconClass: 'iconfont icon-success1', duration: 1500});
						setTimeout(()=>{
							that.$router.push('/admin/user/role')
							that.info = {
								name: '',
								title: '',
								desc: '',
								display: true,
								permission: []
							}
						}, 1500)
					} else {
						console.log(res)
					}
				}).catch(err => {
					console.log(err)
				})
			}
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.role{
	.set{display: flex;justify-content:center;}
	.card-body{width:100%;min-height: 650px; display: flex;flex-direction: column;
		li{width:100%;display: flex;align-items: center;padding:5px 0;font-size: .9rem;
			.left{width:40%;text-align: right;padding: 0 20px 0 0;}
			.right{width:25%;min-width: 300px;}
			input{font-size: .9rem;}
		}
		.permission{display: flex;justify-content:flex-start;align-items: flex-start;
			.left{padding-top:10px;box-sizing: border-box;}
			.content{display: flex;flex-direction: column;justify-content:flex-start;align-items: flex-start;
				button,button:active{box-shadow:none;outline: none;}
				.icon-duoxuan-weixuan1{color:#999;}
				.icon-duoxuan-yixuan{color:#28a745;}
			}
		}
	}
}
</style>


