<template>
  <div class="index container">
    <div class="row jumbotron page-header" v-html="curtainContent"></div>
    <div class="row">
      <div class="col-sm-6 col-md-3" v-for="l in list" :key="l.id">
        <a class="thumbnail" href="javascript:;" @click="goPage(l)">
          <img :src="l.avatar" class="icon" :alt="l.title" />
          <div class="caption">
            <h4 v-text="l.title"></h4>
            <p :title="l.desc" v-text="l.desc"></p>
            <span>查看</span>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
import { MessageBox, Toast, Indicator } from 'mint-ui';
export default { 
  name: 'FrontIndex',
  data () {
    return {
      list: [],
      lachewang: {},
      curtainContent: ""
    }
  },
  activated () {

    this.into()
  },
  mounted () {},
  methods: {
    //初始化
    into() {
      if(localStorage.lachewang) {
        this.lachewang = JSON.parse(localStorage.lachewang)
        this.getCurtain()
        this.getWorks()
      } else {
        this.lachewang.addCatalogDialog = false
        this.lachewang.isLogin = false
        this.getCatalogs()
        this.getBanner()
        this.getCurtain()
        this.getWorks()
        this.getFlink()
        this.getSystem()
      }
    },
    //获得作品列表
    getWorks() {
      let that = this
      this.$axios({
        method: 'GET',
        url: '/mcm/api/works?filter=' + encodeURIComponent('{"where": {"display": true}}')
      }).then(res => {
        if(res && res.data && res.data.length>0) {
          that.lachewang.works = res.data
          this.list = that.lachewang.works
          localStorage.lachewang = JSON.stringify(that.lachewang)
        } else { 
          that.lachewang.works = [] 
          localStorage.lachewang = JSON.stringify(that.lachewang)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //作品链接跳转
    goPage(o) {
      if(o.url.indexOf("http")!=-1) {
        window.open(o.url, '_blank')
      } else {
        MessageBox('请用微信扫描二维码', '<img src="'+o.qrCodeImgUrl+'" />');
      }
    },
    getCurtain() {
      let that = this
      this.$axios({
        method: 'GET',
        url: '/mcm/api/homeCurtain?filter='+encodeURIComponent('{"where":{},"skip":0,"limit":20}')
      }).then(res => {
        Indicator.close();
        if(res && res.statusText == 'OK') {
          if(res.data && res.data.length>0) {
            that.curtainContent = res.data[0].content
            that.lachewang.curtainContent = that.curtainContent
            localStorage.lachewang = JSON.stringify(that.lachewang)
          }
        } else {
          Toast({message: '查询失败', duration: 1500})
          console.log(res)
        }
      }).catch(err => {
        Toast({message: '查询失败', duration: 1500})
        Indicator.close();
        console.log(err)
      })
    },
    //获取首页banner
    getBanner() { 
        let that = this
        this.$axios({
        method: 'GET',
        url: '/mcm/api/banner?filter=' + encodeURIComponent('{ "fields": {"createdAt": false, "updatedAt": false}, "where": {"display": true}}')
      }).then(res => {
        if(res && res.statusText == 'OK') {
          if(res.data && res.data.length>0) {
            that.lachewang.homeBanner = res.data
            localStorage.lachewang = JSON.stringify(that.lachewang)
          } else {
            //that.lachewang.homeBanner = []
          }
        } else {
          //that.lachewang.homeBanner = []
        }
      }).catch(err => {
        //that.lachewang.homeBanner = []
        console.log(err)
      })
    },
    //获取系统配置信息
      getSystem() { 
        let that = this
        this.$axios({
        method: 'GET',
        url: '/mcm/api/system?filter=' + encodeURIComponent('{ "fields": {"createdAt": false, "updatedAt": false}}')
      }).then(res => {
        if(res && res.data && res.data.length>0) {
          this.lachewang.system = res.data[0]
          localStorage.lachewang = JSON.stringify(that.lachewang)
        }
      }).catch(err => {
        console.log(err)
      })
      },
    //获取栏目
    getCatalogs() { 
        let that = this
        this.$axios({
        method: 'GET',
        url: '/mcm/api/catalogs?filter=' + encodeURIComponent('{ "fields": {"sort": false, "createdAt": false, "updatedAt": false}}')
      }).then(res => {
        if(res && res.data && res.data.length>0) {
          that.lachewang.catalogs = res.data
          localStorage.lachewang = JSON.stringify(that.lachewang)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //获取友链
    getFlink() { 
      let that = this
      this.$axios({
        method: 'GET',
        url: '/mcm/api/flink?filter=' + encodeURIComponent('{"fields": {"createdAt": false, "updatedAt": false}, "where": {"display": true}}')
      }).then(res => {
        if(res && res.data && res.data.length>0) {
          that.lachewang.friendlyLink = res.data
          localStorage.lachewang = JSON.stringify(that.lachewang)
        } else { 
          this.flink = [] 
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>
<style scoped>
h1{width:100%;}
.greeting{font-size: 2vw;}
.row.jumbotron.page-header{display: block;}
.page-header p{display: block;width:100%;margin-bottom: 5px;}
.thumbnail{width:100%;height:300px;position: relative;overflow: hidden;margin-bottom:20px;display: block;border-radius: 3px;}
.thumbnail .icon{width: 100%;height: auto;display: block;}
.thumbnail .caption{position: absolute;left:0;top:250px;z-index:2;width:100%;height:100%;background-color: rgba(0,0,0,0.7); padding-bottom: 0;transition: all 0.3s;}
.thumbnail:hover .caption{top:0px;}
.caption h4{padding:15px 0;text-align: center;color:#4fc08d;font-size: 1.2vw;}
.caption p{width:100%;color:#4fc08d;font-size: 1vw;padding:0 10px;box-sizing: border-box;word-wrap:break-word;word-break:break-all;}
.caption span{position:absolute;left:0;bottom:0;z-index:2;width:100%;height:40px;line-height: 40px;text-align: center;display:block;color:#fbfa2c;}
@media screen and (min-width: 1100px){
  .index{position: relative;margin-top:20px;}
}
@media screen and (max-width: 1200px){
  .caption h4{font-size: 2vw;}
  .caption p{font-size: 1.5vw;}
}
@media screen and (max-width: 768px){
  .caption h4{font-size: 3vw;}
  .caption p{font-size: 2vw;}
}
@media screen and (max-width: 640px){
  .index{position: relative;top:20px;}
  .thumbnail{height:320px;}
  .thumbnail .caption{top:auto;bottom:0;height:auto;}
  .caption h4{font-size: 4vw;padding-bottom:0;}
  .caption p{font-size: 2.5vw;}
  .caption span{display: none;}
}
</style>


