<template>
<div class="close" v-if="this.lachewang.system">
	<h2 v-if="this.lachewang.system.closeReason">{{this.lachewang.system.closeReason}}</h2>
</div>
</template>
<script>
export default {
	name: 'close',
	components: {},
	data () {
		return {
			lachewang: {}
		}
	},
	created (){
		if(localStorage.lachewang) {	
			this.lachewang = JSON.parse(localStorage.lachewang)
		}
	},
	activated () {
		document.title = "抱歉，网站暂时关闭";
	},
	methods: {},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.close{width:100%;height: 100%;background-color: #f5f5f5;display: flex;justify-content: center;padding-top:5vw;}
</style>


