<template>
<div class="user card">
	<div class="card-header">
		<span>管理人员</span>
		<div>
			<router-link to="/admin/user/add" class="btn btn-success btn-sm" title="添加管理员"><i class="iconfont icon-jiahao1"></i></router-link>&nbsp;
			<router-link to="/admin/user/role" class="btn btn-primary btn-sm" title="管理组"><i class="iconfont icon-caidan"></i></router-link>
		</div>
	</div>
	<div class="card-body" v-if="list.length>0">
		<ul>
			<li class="header">
				<span>账号</span>
				<span>角色</span>
				<span>状态</span>
				<span>创建时间</span>
				<span>操作</span>
			</li>
			<li v-for="l in list" :key="l.id">
				<span>{{l.username}}</span>
				<span>{{l.roleName}}</span>
				<span v-if="l.roleName=='超级管理员'">——</span>
				<span v-else><mt-switch v-model="l.display" @change="changeUserType(l)"></mt-switch></span>
				<span>{{getFullTime(l.createdAt)}}</span>
				<span class="set">
					<button class="btn" @click="update(l.id)"><i class="iconfont icon-pingjia"></i></button>
					<button class="btn" @click="deleteUserById(l)"><i v-if="l.roleName!='超级管理员'" class="iconfont icon-shanchu9"></i></button>
				</span>
			</li>
		</ul>
	</div>	
	<footer class="card-footer">共 {{list.length}} 位管理者</footer>
</div>
</template>
<script>
import { mapState } from 'vuex'
import Vue from 'vue'
import { Switch, Indicator, Toast, MessageBox } from 'mint-ui';
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_User',
	components: {},
	data () {
		return {
			origin: [],
			list: []
		}
	},
	created (){},
	activated() {
		//Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
		this.init()
		this.BUS.$emit('setSideActived', this.$route.name)
		Indicator.close();
	},
	mounted() {},
	methods: {
		init() {
			this.getUsers()	
			setTimeout(()=>{
				this.list = this.origin
				Indicator.close();
			},1000)
		},
		getUsers() {
			let that = this
			let params = encodeURIComponent('{"fields":{}}')
			this.$axios({
				method: 'GET',
				async: false,
				url: '/mcm/api/user?filter='+params
			}).then(res => {
				if(res && res.statusText=="OK") {
					if(res.data && res.data.length>0) {
						that.origin = res.data
						that.origin.forEach((a,index)=>{
							that.getRoleName(index, a.roleId)
						})
					} else {
						that.origin = []
					}
				} else {
					that.origin = []
				}
			}).catch(err => {
				that.origin = []
			})
		},
		//获得角色名称
		getRoleName(i,id) {
			if(id) {
				let that = this
				this.$axios({
					method: 'GET',
					async: false,
					url: '/mcm/api/role/'+id
				}).then(res => {
					if(res && res.statusText=="OK") {
						if(res.data) {
							that.origin[i].roleName = res.data.description
						} else {
							that.origin[i].roleName = '未查到'
						}
					} else {
						that.origin[i].roleName = 'Fail'
						console.log(res)
					}
				}).catch(err => {
					that.origin[i].roleName = 'Error'
					console.log(err)
				})
			} else {
				Toast({message: '未获得roleId', duration: 1500});
			}
		},
		update(id) {
			if(id) {
				this.$router.push('/admin/user/edit/'+id)
			} else {
				Toast({message: '未获得ID', duration: 1500});
			}
		},
		//改变用户状态，可以启用和关闭，但超级管理员不能关闭
		changeUserType(o) {
			Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/user/'+o.id,
				data: {
					display: o.display
				}
			}).then(res => {
				Indicator.close();
				if(res && res.statusText=="OK") {
					Toast({message: '修改成功', iconClass: 'iconfont icon-success1', duration: 1500});
				} else {
					Toast({message: '修改失败', duration: 1500});
					console.log(res)
				}
			}).catch(err => {
				Indicator.close();
				Toast({message: '修改失败', duration: 1500});
				console.log(err)
			})
		},
		//通过ID删除用户
		deleteUserById(o) {
			let that = this
			if(o.roleName!='超级管理员') {
				MessageBox.confirm('确定删除吗?').then(action => {
				  	if(action === 'confirm') {
				  		Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
				  		that.$axios({
				  			method: 'DELETE',
				  			url: '/mcm/api/user/'+o.id
				  		}).then(res => {
				  			Indicator.close();
				  			if(res && res.statusText=="OK") {
				  				Toast({message: '删除成功', iconClass: 'iconfont icon-success1', duration: 1500});
				  				that.init()
				  			} else {
				  				Toast({message: '删除失败', duration: 1500});
								console.log(res)	
				  			}
				  		}).catch(err => {
				  			Indicator.close();
				  			Toast({message: '删除失败', duration: 1500});
							console.log(err)
				  		})
				  	} else {
				  		console.log(action)
				  	}
				}).catch(err => {
					if(err === 'cancel') {
						//console.log('取消删除')
					} else {
						console.log(err)
					}
				});
			}
		}
	},
	watch: {},
	computed: {
		...mapState(['getFullTime'])
	}
}
</script>
<style scoped lang="less">
.user{
	.card-header{width:100%;display: flex;justify-content:space-between;}
	.card-body{
		li{width:100%;display: flex;justify-content: space-around;font-size: 1rem;padding:10px 0;text-align: center;color:#999;
			span{flex:1;text-align: center;}
			.set{
				button{box-sizing: border-box;width:42px;height: 40px;}
			}
			.mint-switch{justify-content:center;}
		}
		.header{font-size: 1rem;color:#000;font-weight: 700;}
	}
	.card-footer{display:flex;justify-content:flex-end;font-size: 0.9rem;color:#666;}
}
</style>


