<template>
<nav class="top">
	<section>
		<router-link to="/admin" title="vlon.cn" class="slogan">vlon.cn</router-link>
	</section>
	<section>
		<span class="sayHello">{{sayHello}}</span>
		<router-link class="link" to="/admin" title="系统主页">系统主页</router-link>
		<router-link class="link" to="/index" target="_blank" title="网站主页">网站主页</router-link>
		<button type="button" class="btn switchMenu" title="menu" @click="switchMenu">
			<i class="iconfont icon-gengduo"></i>
		</button>

		<!--div class="lang " :class="lang">
			<button type="button" class="btn cn" title="CN" @click="setLang('cn')">CN</button>
			<button type="button" class="btn en" title="EN" @click="setLang('en')">EN</button>
		</div-->
		
		<button type="button" class="btn" title="Logout" @click="logout">
			<i class="iconfont icon-tuichu8"></i>
		</button>
	</section>
</nav>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui'
import { mapState } from 'vuex'
export default {
	name: 'AdminTop',
	data () {
		return {
			//System HomePage
			//WebSite HomePage
			lang: 'cn',
			sayHello: '欢迎 admin！欢迎使用拉扯网后台管理系统',
			ensayHello: 'welcome admin！welcome to use vlon.cn administration system！',
			lachewang: {}
		}
	},
	computed: {
		...mapState(['server'])
	},
	mounted () {
		if(localStorage.lachewang) {
			this.lachewang = JSON.parse(localStorage.lachewang)
		}
	},
	methods: {
		logout () {
			MessageBox.confirm('确定退出?').then(action => {
				if(action === 'confirm') {
					this.lachewang.isLogin = status
					localStorage.lachewang = JSON.stringify(this.lachewang)
					let instance = Toast({
							message: '退出成功', 
							iconClass: 'iconfont icon-success1',
							duration: 1500
						})
						//提示窗消失后跳转到管理后台
						setTimeout(() => {
					  	instance.close();
					  	this.$router.push('/index');
					}, 2000);
				} else {
					console.log(action)
				}
			}).catch( err => {
				if(err === 'cancel') {
					console.log('取消退出')
				} else {
					console.log(err)
				}
			});
		},
		switchMenu () {},
		/*setLang(t) {
			this.lang = t;
		}*/
	}
}
</script>
<style scoped lang="less">
.top{width:100%;background-color: #373277;display: flex;justify-content: space-between;align-items: center;padding:.5vw 1vw;	
	a, button{transition: all 0.3s;}
	a{color: #cbbde2;}
	a:hover{color: #fff;}
	button{color:#fff;font-weight: 500;color: #ffe484;border: none;}
	button:hover{background-color:#ffe484;color:#563d7c}
	span{color: #cbbde2;font-size:.8vw;}
	.slogan{font-size: 1.2vw;}
	.link{font-size: .8vw;padding:0 .5vw;}
	.switchMenu{display: none;}
	.sayHello{padding-right: 0.5vw;}
	section{display: flex;align-items: center;}
	.lang{width:50px;
		button{padding:0;}
		button,button:hover,button:active{box-shadow: none;outline: none;}
	}
	.lang.cn .btn.en{font-size: 10px;color:#999;}
	.lang.en .btn.cn{font-size: 10px;color:#999;}
}
@media screen and (max-width: 640px) {
	.top{width:100%;display: flex;align-items: center;padding: 8vw 2vw;
		section {height: 100%;display: flex;align-items: center;
			span{display: none;}
			.link{padding:1vw;font-size: 3vw;}
			.slogan{font-size: 5vw;padding-left: 2vw;}
			.switchMenu{display: flex;align-items: center;
				.icon-gengduo{height: 5vw;}
			}
		}
	}
}
</style>


