<template>
<div class="detail">
	<nav aria-label="breadcrumb">
		<ol class="breadcrumb">
			<li class="breadcrumb-item"><router-link to="/index">主页</router-link></li>
			<li class="breadcrumb-item"><router-link :to="'/article/'+catalog.id">{{catalog.name}}</router-link></li>
			<li class="breadcrumb-item active" aria-current="page">文章详情</li>
		</ol>
	</nav>
	<section class="article">
		<p class="title" v-if="info.title">{{info.title}}</p>
		<div class="tags">
			<p class="type" v-if="info.type">{{info.type}}</p>
			<p class="tag" v-if="info.tags && info.tags.length>0">
				<router-link v-for="(t,index) in info.tags" :key="index" :to="'/search/'+t">{{t}}</router-link>
			</p>
			<p class="read readText" v-if="info.views>0">{{info.views}}</p>
			<p class="createTime" v-if="info.createdAt">{{getFullTime(info.createdAt)}}</p>
		</div>

		<article class="container">
			<div class="desc" v-if="info.desc">{{info.desc}}</div>  
			<div class="content" v-if="info.content" v-html="info.content"></div>  
		</article>
		<div class="end" v-if="info.content">
			<div class="copyRight" v-if="info.author">本文作者：{{info.author}}<br/>
				<p v-if="info.type=='原创'">本文为拉扯网原创内容，未经允许不得转载。</p>
			</div>
			<div class="share">
					
			</div>
		</div>
		<div class="pay" v-if="info.content">
			<button class="btn btn-danger" @click="isShang=true">打 赏</button>
			如果觉得我的文章对你有用，请随意打赏
		</div>
	</section>
	<div class="likeAnother">
		<header>大家都在看</header>
		<ul>
			<li v-for="(la,index) in likeAnother" :key="la.id">
				<router-link :to="'/article/detail/'+la.id" class="title">{{la.title}}</router-link>
				<p class="tags" v-if="la.tags && la.tags.length>0">
					<router-link v-for="(t,index) in la.tags" :key="index" :to="'/search/'+t">{{t}}</router-link>
				</p>
			</li>
		</ul>
	</div>
	<section class="comments" v-if="comments.length>0">
		<header><b>{{comments.length}}</b> 条评论</header>
		<ul class="content">
			<li v-for="c in comments" :key="c.id">
				<div class="left">
					<img :src="filterAvatar(parseInt(c.avatar))" alt="" />
				</div>
				<div class="right">
					<div class="name">
						<a v-if="c.email" :href="'mailto:'+c.email" >
							<span class="nickname">{{c.nickname}}</span>
						</a>
						<span v-else class="nickname">{{c.nickname}}</span>
						<time datetime="2019-08-21 09:56:20">{{getFullTime(c.createdAt)}}</time>
					</div>
					<div class="content">{{c.content}}</div>
				</div>
			</li>
		</ul>
	</section>
	<footer class="commentContainer">
		<div class="input">
			<div class="btn-group dropup avatar">
					<button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
					<img :src="currentAvatar" alt="" />
					</button>
			  	<ul class="dropdown-menu">
				    <li v-for="(a,index) in avatarList" :key="index">
						<button class="btn btn-default" @click="useThisAvatar(index)">
							<img :src="a" alt=""/>
						</button>
				    </li>
			  	</ul>
			</div>
			<div class="text">
				<div class="inp">
					<input type="text" placeholder="name" class="form-control" maxlength="50" v-model="commentNickName" />
					<input type="text" placeholder="email" class="form-control" v-model="commentEmail" />
				</div>
				<textarea name="commentContent" maxlength="500" v-model="commentContent" placeholder="u can say anything"></textarea>
			</div>
		</div>
		<div class="set">
			<button class="btn btn-success" @click="saveComment">biu</button>
		</div>
	</footer>
	
	<transition
		mode="out-in"
		appear
		appear-active-class="animated fadeIn"
		enter-active-class="animated fadeIn"
		leave-active-class="animated fadeOut"
		>	
		<div class="zanshangBox" v-if="isShang" @click="isShang=false">
			<img :src="zanshang" alt="" />
		</div>
	</transition>
</div>
</template>
<script>
import zanshang from '../../../assets/zanshang.jpg'
import avatar from '../../../assets/user-64.png'
import avatar01 from '../../../assets/avatar/01.jpg'
import avatar02 from '../../../assets/avatar/02.jpg'
import avatar03 from '../../../assets/avatar/03.jpg'
import avatar04 from '../../../assets/avatar/04.jpg'
import avatar05 from '../../../assets/avatar/05.jpg'
import avatar06 from '../../../assets/avatar/06.jpg'
import avatar07 from '../../../assets/avatar/07.jpg'
import avatar08 from '../../../assets/avatar/08.jpg'
import avatar09 from '../../../assets/avatar/09.jpg'
import avatar10 from '../../../assets/avatar/10.jpg'
import avatar11 from '../../../assets/avatar/11.jpg'
import avatar12 from '../../../assets/avatar/12.jpg'
import avatar13 from '../../../assets/avatar/13.jpg'
import avatar14 from '../../../assets/avatar/14.jpg'
import avatar15 from '../../../assets/avatar/15.jpg'
import avatar16 from '../../../assets/avatar/16.jpg'
import avatar17 from '../../../assets/avatar/17.jpg'
import avatar18 from '../../../assets/avatar/18.jpg'
import avatar19 from '../../../assets/avatar/19.jpg'
import avatar20 from '../../../assets/avatar/20.jpg'
import { Toast, Spinner, Indicator } from 'mint-ui'
import Vue from 'vue'
Vue.component(Spinner.name, Spinner)
import { mapState } from 'vuex'
export default {
	name: 'FrontArticleDetail',
	components: {},
	data () {
		return {
			isShang: false,
			zanshang,
			articleId: '',
			info: {},
			catalog: {},
			likeAnother: [],
			comments: [],
			avatarList: [avatar01,avatar02,avatar03,avatar04,avatar05,avatar06,avatar07,avatar08,avatar09,avatar10,avatar11,avatar12,avatar13,avatar14,avatar15,avatar16,avatar17,avatar18,avatar19,avatar20],
			currentAvatar: avatar01,
			commentAvatar: 0,
			commentNickName: '',
			commentContent: '',
			commentEmail: ''
		}
	},
	activated() {
		if(this.$route.params.id) {
			this.articleId = this.$route.params.id
			this.init()
		}
	},
	created() {},
	mounted() {
		if(localStorage.lachewang) {
			let lachewang = JSON.parse(localStorage.lachewang)
			if(lachewang.allLove && lachewang.allLove.length>0) {
				this.likeAnother = lachewang.allLove
			} else {
				this.getAllLove()
			}
		} else {
			this.getAllLove()
		}
	},
	methods: {
		//初始化
		init() {
			this.getArticleById()
		},
		//通过文章ID获取文章详细内容
		getArticleById() {
			let that = this
			Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: 'GET',
				url: '/mcm/api/articles/' + this.articleId
			}).then(res => {
				Indicator.close();
				if(res && res.statusText=="OK") {
					that.info = res.data
					document.title = that.info.title
					that.getCatalogById()
					that.getCommentByArticleId()
					that.addViews()
				} else {
					Toast({message: '查询文章内容失败', duration: 1500})
					console.log(res)
				}
				
			}).catch(err => {
				Indicator.close();
				Toast({message: '查询文章内容失败', duration: 1500})
				console.log(err)
			})	
		},
		//通过ID获得栏目信息
		getCatalogById() {
			let that = this
			if(localStorage.lachewang && JSON.parse(localStorage.lachewang).catalogs && JSON.parse(localStorage.lachewang).catalogs.length>0) {
				let ca = JSON.parse(localStorage.lachewang).catalogs
				ca.forEach(i => {
					if(i.id === that.info.catalogId) {
						that.catalog = i
					}
				})
			} else {
				this.$axios({
					method: 'GET',
					url: '/mcm/api/catalogs/'+this.info.catalogId
				}).then(res => {
					if(res && res.statusText=="OK") {
						that.catalog = res.data
					} else {
						Toast({message: '查询栏目信息失败', duration: 1500})
						console.log(res)
					}
				}).catch(err => {
					Toast({message: '查询栏目信息失败', duration: 1500})
					console.log(err)
				})
			}
		},
		//大家都在看
		getAllLove() {
			let num = 5
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/articles?filter='+encodeURIComponent('{"fields": {"id": true, "title": true, "tags": true}, "where": {"display": true, "delete": false}}')
			}).then(res => {
				if(res && res.data && res.data.length>0) {
					that.makeAllLove(res.data)
				} else {
					Toast({message: '文章列表失败', duration: 1500})
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '文章列表失败', duration: 1500})
				console.log(err)
			})
		},
		//从列表中通过浏览量从高到底排列，取出前5条
		makeAllLove(list) {
			let max = list[0].views
			let arr = []
			let a = ''
			for(let i=1; i<list.length; i++) {
				if(list[i].views < max) {
					a = JSON.stringify(list[i])
					list[i] = JSON.parse(JSON.stringify(list[i-1]))
					list[i-1] = JSON.parse(a)
				}
			}
			for(let j=list.length-1; j>list.length-6; j--) {
				arr.push(list[j])
			}
			if(localStorage.lachewang) {
				let lachewang = JSON.parse(localStorage.lachewang)
				lachewang.allLove = arr
				localStorage.lachewang=JSON.stringify(lachewang)
			}
			this.likeAnother = arr
		},
		sortNum(a,b) {
			return a - b
		},
		//保存评论
		saveComment() {
			if(localStorage.commentEffective){
				let a = localStorage.commentEffective //上次评论时间
				let b = (new Date()).getTime() //当前点击的时间
				let s = parseInt((b - a) / 1000) //上次提交评论距当前的时间差，秒
				let t = 60*5 //两次间隔5分钟  300秒
				//如果上次提交距当前不到5分钟，则无法提交，必须等5分钟后才能提交
				if(s < t) {
					Toast({message: '操作太频繁，歇会儿再来吧', duration: 2000}); return false;
				} else {
					localStorage.removeItem('commentEffective')
				}
			}
			if(!this.commentNickName) { Toast({message: '请留下尊姓大名', duration: 1500}); return false;}
			if(!this.commentContent) { Toast({message: '请输入留言内容', duration: 1500}); return false;}
			let flag = this.validComment(this.commentContent)
			if(!flag) {Toast({message: '留言包含非法内容', duration: 1500}); return false;}
			this.doSaveComment()
		},
		//验证评论内容
		validComment(t) {
			if(t.indexOf('<script')!=-1 || t.indexOf('<iframe')!=-1) {
				return false
			} else {
				return true
			}
		},
		//保存评论内容到数据库
		doSaveComment() {
			let that = this
			Indicator.open({text: '提交中...', spinnerType: 'fading-circle'});
			this.$axios({
				method: 'POST',
				url: '/mcm/api/comments',
				data: {
					'articleId': this.$route.params.id,
					'content': this.commentContent,
					'nickname': this.commentNickName,
					'email': this.commentEmail,
					'avatar': this.commentAvatar,
					'display': false
				}
			}).then(res => {
				Indicator.close()
				if(res && res.statusText=="OK") {
					localStorage.commentEffective = (new Date()).getTime()
					Toast({message: '提交成功，请等待审核', duration: 2000});
					setTimeout(()=>{
						that.commentContent = ''
						that.commentNickName = ''
						that.commentEmail = ''
					},2000)
				} else {
					Toast({message: '提交失败', duration: 1500});
					console.log(res)
				}
			}).catch(err => {
				Indicator.close()
				Toast({message: '提交评论失败', duration: 1500});
				console.log(err)
			})
		},
		//选择头像
		useThisAvatar(i) {
			this.currentAvatar = this.filterAvatar(i)
			this.commentAvatar = i
		},
		filterAvatar(i) {
			let a = i+1
			let thisAvatar
			switch(i) {
				case 0: thisAvatar = avatar01; break;
				case 1: thisAvatar = avatar02; break;
				case 2: thisAvatar = avatar03; break;
				case 3: thisAvatar = avatar04; break;
				case 4: thisAvatar = avatar05; break;
				case 5: thisAvatar = avatar06; break;
				case 6: thisAvatar = avatar07; break;
				case 7: thisAvatar = avatar08; break;
				case 8: thisAvatar = avatar09; break;
				case 9: thisAvatar = avatar10; break;
				case 10: thisAvatar = avatar11; break;
				case 11: thisAvatar = avatar12; break;
				case 12: thisAvatar = avatar13; break;
				case 13: thisAvatar = avatar14; break;
				case 14: thisAvatar = avatar15; break;
				case 15: thisAvatar = avatar16; break;
				case 16: thisAvatar = avatar17; break;
				case 17: thisAvatar = avatar18; break;
				case 18: thisAvatar = avatar19; break;
				case 19: thisAvatar = avatar20; break;
			}
			return thisAvatar
		},
		//通过文章Id查询当前文章下的评论信息
		getCommentByArticleId() {
			let that = this
			let params = encodeURIComponent('{"where": {"articleId": "'+this.$route.params.id+'", "display": true}}')
			this.$axios({
				method: 'GET',
				url: '/mcm/api/comments?filter='+params
			}).then(res => {
				if(res && res.statusText=="OK") {
					if(res.data && res.data.length>0) {
						that.comments = res.data
					}else{
						that.comments = []
					}
				} else {
					that.comments = []
					console.log(res)
				}
			}).catch(err => {
				that.comments = []
				console.log(err)
			})
		},
		//增加浏览量
		addViews() {
			this.$axios({
				method: 'PUT',
				url: '/mcm/api/articles/' + this.articleId,
				data: {
					"views": this.info.views+1
				}
			}).then(res => {
				if(res && res.statusText == "OK") {
					//浏览量+1 成功
				} else {
					console.log(res)
				}
			}).catch(err => {
				console.log(err)
			})
		}
	},
	watch: {
		$route() {
			this.articleId = this.$route.params.id
			this.init(this.articleId)
		}
	},
	computed: {
		...mapState(['getPages', 'getFullTime'])
	}
}
</script>
<style scoped lang="less">
.detail{width:100%;padding-top:10px;display: flex;flex-direction: column;
	.breadcrumb{display: flex;background-color:transparent;margin-bottom: 0;font-size: .8rem;
		a{color: #009a61;}
		.active a{color: #777;}
	}
	.article{
		.title{font-size: 2rem;margin:0 0 10px 0;}
		.tags{display: flex;font-size: .8rem;color: #999;margin-bottom: 20px;
			p{margin-right: 1rem;}
			.type{background-color: rgba(242,174,67,0.25);color: #F2AE43;padding:1px 5px;margin-right: .5rem;}
			.tag a{color: #017E66;background-color: rgba(1,126,102,0.08);padding:1px 5px;margin-right: .5rem;}
			.tag a:hover{color: #fff;background-color:#017E66; }
			.readText:after{content:' 次阅读';}
		}
		.container{min-height:700px;
			.desc{border-left:2px solid #009A61;background-color:#f5f5f5;font-size: 14px;padding:20px;margin-bottom: 20px; }
			.content{min-height: 500px;}
		}
		.centent{padding: 50px 0;}
		.end{display: flex;justify-content: space-between;align-items: center;font-size: .9rem;color:#666;
			.copyRight{}
			.share{}
		}
		.pay{display: flex;flex-direction: column;justify-content:center;align-items: center;font-size: .9rem;color:#999;
			button{margin-bottom: 1rem;}
		}
	}
	.likeAnother{display: flex;flex-direction: column;padding: 3rem 0;
		header{padding-bottom: .5rem;font-weight: 700;}
		ul{width:100%;display: flex;flex-direction: column;font-size: 0.9rem;
			li{width:100%;display: flex;padding:.2rem 0;
				.title{color: #009a61;font-weight: 700;margin-right: 2rem;transition: all 0.3s;}
				.tags{font-size: .8rem;
					a{margin-right: .5rem;color: #017E66;background-color: rgba(1,126,102,0.08);padding:2px 5px;transition: all 0.3s;}
				}
				.tags a:hover{color: #fff;background-color:#017E66;}
				.title:hover{color:#034d31;text-decoration: underline;}
			}
		}
	}
	.zanshangBox{position: fixed;left:0;top:0;z-index: 100;width:100%;height:100%;background-color:rgba(0,0,0,0.5);display: flex;justify-content:center;align-items: center;
		img{width:300px;border-radius: 5px;}
	}
	.comments{
		header{font-size: 1rem;margin-bottom: 1rem;}
		ul.content{width:100%;font-size: 0.9rem;
			li{width:100%;display: flex;border-top: 1px solid #eee;padding:.5rem;padding-left:.8rem;
				.left{
					img{width:32px;height: 32px;border-radius: 100%;display: block;}
				}
				.right{flex:1;padding-left:.5rem;box-sizing:border-box;
					.name{display: flex;align-items: center;
						.nickname{color: #009a61;font-weight: 700;margin-right: 1rem;}
						time{color: #999;font-size: 0.8rem;}
					}
					.content{padding:1rem .2rem;min-height: 4rem;}
				}
			}
		}
	}
	.commentContainer{background-color:#f5f5f5;padding:1rem;box-sizing: border-box;border-radius: 4px;
		.input{display: flex;margin-bottom: .5rem;align-items: flex-start;justify-content:flex-start;
			.avatar{display: flex;padding-left:0;outline: none;box-shadow: none;
				
			}
			.text{display: flex;flex-direction: column;flex:1;
				.inp{margin-bottom: 10px;display: flex;
					input{margin-right: 10px;width:25%;}
					input:last-child{margin-right: 0;}
				}
			}
			textarea{width:100%;height:5rem;resize: none; border-radius: .2rem;padding:0.5rem;font-size: 1rem;}
		}
		.set{display: flex;justify-content:flex-end;}
	}
	.dropdown-toggle{padding:0;margin-right: 0.5rem;border:none;box-shadow: none;
		img{width:32px;height: 32px;border-radius: 100%;display: block;}
	}
	.dropdown-toggle:after{content:'';border:none;}
	.dropdown-menu{
		&.show{width:255px;height:200px;display: flex;flex-wrap: wrap;top:-168px!important;left:-16px!important;transform: translate3d(0px, -49px, 0px)!important;background-color:#fff;box-sizing:border-box;padding-left:10px;padding-right: 5px;}
		li{margin-right: 5px;margin-bottom: 5px;
			button{padding:0;
				img{width:40px;height:40px;border-radius: 100%;display: block;background-color:#ccc;}
			}
		}
	}
}
</style>


